import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {addInputToUIState, removeInputToUIState, updateUploadProgress} from "../../actions/actionCreators";
import {
    ADDRESS_TWO,
    ADDRESS_THREE,
    ADDRESS_FOUR,
    MAILING_LEVEL,
    MAILING_UNIT_NUMBER,
    HOME_LEVEL,
    HOME_UNIT_NUMBER,
    MAILING_COUNTRY,
    HOME_BUILDING,
    MAILING_BUILDING,
    NAME_OF_EMPLOYER,
    SUBMIT_PR_1_TO_2_YEAR,
    UPLOAD_INCOME_PROOF,
    UPLOAD_NRIC_BACK
} from "../../constants/inputsConstant";

export const validatorHOC = WrappedComponent => {
    class Validator extends Component {
        componentDidMount() {
            const inputToExclude = [
                ADDRESS_TWO,
                ADDRESS_THREE,
                ADDRESS_FOUR,
                MAILING_LEVEL,
                MAILING_UNIT_NUMBER,
                HOME_LEVEL,
                HOME_UNIT_NUMBER,
                MAILING_COUNTRY,
                HOME_BUILDING,
                MAILING_BUILDING,
                NAME_OF_EMPLOYER,
                SUBMIT_PR_1_TO_2_YEAR
            ];
            if (!inputToExclude.includes(this.props.inputID)) {
                this.props.dispatch(addInputToUIState(this.props.step, this.props.inputID));
            }
        }
        componentWillUnmount() {
            this.props.dispatch(removeInputToUIState(this.props.step, this.props.inputID));
            if ([UPLOAD_INCOME_PROOF, UPLOAD_NRIC_BACK].indexOf(this.props.inputID) > -1) {
                this.props.dispatch(updateUploadProgress(this.props.step, this.props.inputID, 0));
            }
        }
        render() {
            return <WrappedComponent {...this.props} />;
        }
    }

    Validator.propTypes = {
        state: ImmutablePropTypes.map,
        inputID: PropTypes.string,
        step: PropTypes.string.isRequired,
        dispatch: PropTypes.func.isRequired
    };

    return Validator;
};



