import React, {PropTypes} from "react";

import "../resources/css/App.css";

const Main = props => {
    return (
        <div>
            {React.cloneElement(props.children, props)}
        </div>
    );
};

Main.propTypes = {
    children: PropTypes.object.isRequired
};

export default Main;



