import React, {PropTypes} from "react";

import "./SummaryBlockToggleElement.css";

const SummaryBlockToggleElement = props => {
    return (
        <div className='toggle-element-container'>
            <p className='toggle-element-label'>{props.label}</p>
            <p className='toggle-element-value'>{props.value}</p>
        </div>
    );
};

SummaryBlockToggleElement.propTypes = {
    label: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired
};

export default SummaryBlockToggleElement;



