/* global FORMS_CNFG_GLBL */
import React, {PropTypes, Component} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

// import TextInput from "../../TextInput/TextInput";
import "./Congratulation.css";
import {productsAppliedPath, myInfoFlagPath, productIdPath} from "../../../constants/path";
import {List} from "immutable";
import {passProps} from "../../HOC/passProps";
import {addPrefixUOB, sortByDefaultList} from "../../../utils";
const icons = [
    `${FORMS_CNFG_GLBL.imgPath}/signup_icon.svg`,
    `${FORMS_CNFG_GLBL.imgPath}/access_icon.svg`,
    `${FORMS_CNFG_GLBL.imgPath}/account_icon.svg`
];

class Congratulation extends Component {
    componentDidMount() {
        if (window._satellite) {
            let arr = [];
            const dataElement = window.dataElement || {};
            dataElement.event_name = "form_submit";
            arr.push(this.props.state.getIn(productIdPath()).first());
            dataElement.product_name = arr;
            window.dtmCustomEventName = dataElement.event_name;
            window._satellite.track(window.dtmCustomEventName);
        }
    }
    render() {
        const {props} = this;
        const appliedProducts = props.state.getIn(productsAppliedPath(), List());
        const sortProducts = sortByDefaultList(props.state.getIn(productIdPath()).toArray(), appliedProducts);
        const config = new props.FormConfig("congratulation");
        const isMyInfo = props.state.getIn(myInfoFlagPath());
        return (
            <div className='congratulation--main-container'>
                <div className='congratulation--left-container'>
                    <div className='congratulation--left-panel-infos-container'>
                        <div className='congratulation--left-panel-logo'>
                            <img src={`${FORMS_CNFG_GLBL.imgPath}/uobLogo.svg`} alt='uob-logo' />
                        </div>
                        <div className='congratulation--left-panel-title'>
                            {config.congratulation.leftPanel.title}
                        </div>
                        <div className='congratulation--left-panel-title-separator' />
                        {/* <div className='congratulation--left-panel-cards-panel'>
                        {appliedProducts.map(product => {
                            return (
                                <img
                                    key={product}
                                    className='congratulation--left-panel-cards-image'
                                    src={productsConfig["products"][product.get("pid")]["imgPath"]}
                                    alt={"cards"}
                                />
                            );
                        })}
                    </div> */}
                        <div className='congratulation--left-panel-text'>
                            <div>
                                {config.congratulation.leftPanel.subtitle}
                            </div>
                            <ul>
                                {sortProducts.map(product => {
                                    return (
                                        <li key={product} className='congratulation--left-panel-subtext'>
                                            <div className='congratulation--left-panel-text--bold'>
                                                {addPrefixUOB(props.productsConfig["products"][product.get("pid")]["productName"], product.get("pid"))}
                                            </div>
                                            <div>{`(Ref No. ${product.get("referenceNo")})`}</div>
                                        </li>
                                    );
                                })}
                            </ul>
                            {!isMyInfo
                                ? <div
                                      className='congratulation--left-panel-cpf'
                                      dangerouslySetInnerHTML={{
                                          __html: config.congratulation.leftPanel.submitCPFOnlineSub
                                      }}
                                  />
                                : null}
                        </div>
                    </div>
                </div>
                <div className='congratulation--right-container'>
                    <div className='congratulation--right-infos-container'>
                        <div className='congratulation--right-panel-infos-title'>
                            {config.congratulation.rightPanel.title}
                        </div>
                        <div className='congratulation--right-panel-infos-cols-container'>
                            {config.congratulation.rightPanel.body.map((item, i) => {
                                return (
                                    <div key={i} className='congratulation--right-panel-infos-col1'>
                                        <img
                                            className='congratulation--right-infos-col-image'
                                            src={item.image || icons[i]}
                                            alt={"cards"}
                                        />
                                        <div className='congratulation--right-panel-infos-col-title'>
                                            {item.header}
                                        </div>
                                        <p
                                            className='congratulation--right-panel-infos-col-text'
                                            dangerouslySetInnerHTML={{
                                                __html: item.content
                                            }}
                                        />
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

Congratulation.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    FormConfig: PropTypes.func.isRequired
};

// export default Congratulation;
export default passProps(Congratulation);



