import {
    ACTION_POST_OTP,
    ACTION_GET_ADDRESS,
    ACTION_POST_PARTIAL_APPLICATION,
    ACTION_GET_OTP_PREFIX,
    ACTION_GET_MY_INFO,
    ACTION_SUBMIT_FULL_APPLICATION,
    ACTION_CHECK_PENDING_APPLICATION,
    ACTION_GET_PENDING_APPLICATION,
    ACTION_GET_TENOR_TABLE_DATA,
    ACTION_GET_BANK_LIST,
    ACTION_VALIDATE_TIN,
    ACTION_VALIDATE_USERNAME,
    ACTION_GET_ENCRYPTION_PARAMS,
    ACTION_GET_USER_STATUS
} from "../constants/actionsConstant";

import {
    postOTP,
    getOTP,
    getAddress,
    postPartialApplication,
    getMyInfoDetails,
    postFullApplication,
    checkPendingApplication,
    getPendingApplications,
    getTenorTableData,
    getBankList,
    validateTIN,
    validateUsername,
    getEncryptionParams,
    getUserStatus
} from "./api";

const formApi = (action, successDispatch, errorDispatch, getState, dispatch, loadingDispatch) => {
    const api = {
        [ACTION_POST_OTP]: postOTP,
        [ACTION_GET_OTP_PREFIX]: getOTP,
        [ACTION_GET_ADDRESS]: getAddress,
        [ACTION_POST_PARTIAL_APPLICATION]: postPartialApplication,
        [ACTION_GET_MY_INFO]: getMyInfoDetails,
        [ACTION_SUBMIT_FULL_APPLICATION]: postFullApplication,
        [ACTION_CHECK_PENDING_APPLICATION]: checkPendingApplication,
        [ACTION_GET_PENDING_APPLICATION]: getPendingApplications,
        [ACTION_GET_TENOR_TABLE_DATA]: getTenorTableData,
        [ACTION_GET_BANK_LIST]: getBankList,
        [ACTION_VALIDATE_TIN]: validateTIN,
        [ACTION_VALIDATE_USERNAME]: validateUsername,
        [ACTION_GET_ENCRYPTION_PARAMS]: getEncryptionParams,
        [ACTION_GET_USER_STATUS]: getUserStatus
    };
    return api[action.type]({action, successDispatch, errorDispatch, getState, dispatch, loadingDispatch});
};
export default formApi;



