import React from "react";
import {render} from "react-dom";

import App from "./components/App";

import "./resources/css/index.css";

import {createFormClass} from "./utils";
import configToFetch from "./config/getConfig";
import {getMyInfo} from "./actions/actionCreators";

import store from "./store";
Promise.all(configToFetch).then(([formData, productData]) => {
    store.dispatch(getMyInfo(productData, formData));
    render(
        <App FormConfig={createFormClass(formData)} productConfig={productData} formData={formData} />,
        document.getElementById("root")
    );
});

if (module.hot) {
    module.hot.accept("./components/App", () => {
        const NextApp = require("./components/App").default;
        render(<NextApp />, document.getElementById("root"));
    });
}

(function (d, s, id) {
    let js;
    let fjs = d.getElementsByTagName("body")[0];
    if (d.getElementById(id)) { return; }
    js = d.createElement(s);
    js.id = id;
    js.onload = function () {
    // remote script has loaded
    };
    js.src = "/applyWith/pixel.js";
    fjs.insertBefore(js, fjs.childNodes[0]);
}(document, "script", "pixel-js"));



