/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import ScrollLock from "react-scroll-lock-component";

import {onFocusInput, onBlurInput, editInput, editDropDownSearchInput} from "../../actions/actionCreators";
import {uiDropDownSearchValuePath} from "../../constants/path";

import {countriesLabelValue} from "../../constants/countriesMaps";

import "./DropdownWithFlag.css";
import {getInputStates, isValidOrNotDirty} from "../../utils";
import {validatorHOC} from "../HOC/ValidatorHOC";

const getFlag = value => (value ? `${FORMS_CNFG_GLBL.imgPath}/countriesFlags/${value.toLowerCase()}.svg` : "");
const getFlagClass = flagSrc => (flagSrc ? "" : "--invisible");
const getFocus = (isFocus, isValid, isDirty, value) =>
    isFocus || value || !isValidOrNotDirty(isValid, isDirty) ? "--focused" : "";
const getErrorContainerStyle = validOrNotDirty => (validOrNotDirty ? "" : "dropdownWithFlag--content--error");
const DropdownWithFlag = props => {
    const renderDropdownWithFlagItems = myFunc => (item, i) => {
        return (
            <div
                key={i}
                onClick={
                    myFunc
                        ? () => {
                            myFunc(item);
                            props.dispatchOnBlurInput(props.step, props.inputID, false);
                        }
                        : () => {
                            props.dispatchOnEditInput(props.inputID, item.value, true, props.step);
                            props.dispatchOnBlurInput(props.step, props.inputID, false);
                        }
                }
                className='dropdownWithFlag-selection-item'
            >
                {item.label}
            </div>
        );
    };

    const [isFocus, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);

    const mapValueToLabel = (arrayOfItems, value) => {
        const item = arrayOfItems.find(x => x.value === value);
        return item ? item.label : value;
    };
    const value = props.value ? props.value : inputValue;
    const focus = getFocus(isFocus, isValid, isDirty, value);
    const showErrorLabel = isValidOrNotDirty(isValid, isDirty) ? "" : "dropdownLabel--error";
    const searchFocus = isFocus || !(!isValid || isDirty) ? "--focused" : "";
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirty(isValid, isDirty));

    // const country = countriesNamesMap[value];
    // const countryCode = country ? country : value;

    const flagSrc = getFlag(value);
    const flagVisibility = getFlagClass(flagSrc);
    const renderItems = renderDropdownWithFlagItems(props.onClick);
    const searchValue = props.state.getIn(uiDropDownSearchValuePath(props.step, props.inputID), "");
    const disabled = props.disabled ? props.disabled : false;
    let searchRef;

    return (
        <div id={props.inputID} className='dropdownWithFlag' tabIndex='1'>
            <div
                className={"dropdownWithFlag-container" + focus}
                onClick={() => {
                    if (disabled) {
                        return null;
                    }
                    if (isFocus) {
                        return props.dispatchOnBlurInput(props.step, props.inputID, false);
                    }
                    const searchInput = searchRef;
                    setTimeout(() => searchInput.focus(), 0);
                    return props.dispatchOnFocusInput(props.step, props.inputID, true);
                }}
            >
                <div className={`dropdownWithFlag--content ${errorContainerStyle}`}>
                    <div className='dropdownWithFlag--content-left'>
                        <div className={"dropdownWithFlag--inputLabel" + focus + " " + showErrorLabel}>
                            {`${props.inputLabel}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                        </div>
                        <div className='dropdownWithFlag-input-container'>
                            <div className='dropdownWithFlag-input-flag-container'>
                                <img
                                    className={"dropdownWithFlag-input-flag" + flagVisibility}
                                    src={flagSrc}
                                    alt='flag'
                                />
                            </div>
                            <div className='dropdownWithFlag--value-text'>
                                {mapValueToLabel(countriesLabelValue, value)}
                            </div>
                        </div>
                    </div>
                    <img
                        className='dropdownWithFlag--inputArrow'
                        src={`${FORMS_CNFG_GLBL.imgPath}/icon-arrow-solid-down.svg`}
                        alt='dropdownWithFlag-arrow'
                    />
                </div>
            </div>

            <div className={"dropdown--search-and-selection-flex-container" + searchFocus}>
                <div className={"dropdownWithFlag--input-container" + searchFocus}>
                    <img
                        src={`${FORMS_CNFG_GLBL.imgPath}/cross-grey.svg`}
                        onClick={() => props.dispatchOnBlurInput(props.step, props.inputID, false)}
                        className='dropdownWithFlag--input-searchInput--crossIcon'
                        alt='crossIcon'
                    />
                    <input // onKeyDown={handleKeyEvents}
                        onChange={e =>
                            props.dispatchOnEditDropDownSearchInput(props.step, props.inputID, e.target.value)}
                        onFocus={() => {
                            props.dispatchOnFocusInput(props.step, props.inputID, true);
                        }}
                        // onBlur={() => {
                        //     props.dispatchOnBlurInput(props.step, props.inputID, false);
                        // }}
                        className={"dropdownWithFlag--searchValue" + searchFocus}
                        value={searchValue}
                        placeholder='Search'
                        ref={search => (searchRef = search)}
                    />
                    <img
                        src={`${FORMS_CNFG_GLBL.imgPath}/search_icon.svg`}
                        className='dropdownWithFlag--input-searchInput--icon'
                        alt='searchIcon'
                    />
                </div>
                <ScrollLock>
                    <div className={"dropdownWithFlag-selection" + searchFocus}>
                        {props.dropdownWithFlagItems
                            .filter(item => item.label.toLowerCase().includes(searchValue.toLowerCase()))
                            .map(renderItems)}
                        <div className='iphone-fix' />
                    </div>
                </ScrollLock>
            </div>
        </div>
    );
};

DropdownWithFlag.propTypes = {
    state: ImmutablePropTypes.map,
    title: PropTypes.string,
    inputLabel: PropTypes.string,
    inputID: PropTypes.string,
    description: PropTypes.string,
    dropdownWithFlagItems: PropTypes.array,
    dispatchOnFocusInput: PropTypes.func,
    dispatchOnBlurInput: PropTypes.func,
    dispatchOnEditInput: PropTypes.func,
    step: PropTypes.string.isRequired,
    value: PropTypes.string,
    onClick: PropTypes.func,
    disabled: PropTypes.bool
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchOnEditInput: bindActionCreators(editInput, dispatch),
        dispatchOnEditDropDownSearchInput: bindActionCreators(editDropDownSearchInput, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(DropdownWithFlag));



