/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import "./PrimaryButton.css";
import {uiIsSpinnerActivePath, currentStepPath} from "../../constants/path";

const PrimaryButton = props => {
    const isLoading = props.state.getIn(uiIsSpinnerActivePath(props.step), false);
    const currentStep = props.state.getIn(currentStepPath(), "");
    const isFinalSubmit = currentStep === "summary";
    const finalSubmitText = isLoading ? "" : "Confirm";
    const continueText = isLoading ? "" : "Continue";
    const spinner = isLoading
        ? <img
              className='primary-button--spinner'
              src={`${FORMS_CNFG_GLBL.imgPath}/loading_icon.svg`}
              alt='primary-button'
          />
        : false;

    return (
        <div className='primary-button--main-container'>
            {spinner}
            <button id={props.idForButton} className='primary-button' onClick={props.onClick}>
                {isFinalSubmit ? finalSubmitText : continueText}
            </button>
        </div>
    );
};

PrimaryButton.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
    idForButton: PropTypes.string.isRequired
};

export default PrimaryButton;



