import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

import "./LocalAddressInput.css";

import TextInput from "../TextInput/TextInput";
import ReadOnlyTextInput from "../ReadOnlyTextInput/ReadOnlyTextInput";
import EditableTextInput from "../EditableTextInput/EditableTextInput";
import {editInput} from "../../actions/actionCreators";

import {
    validateFilledInput,
    checkNumbers,
    checkSize,
    noValidation,
    checkMaxSize,
    onlyAlphanumeric,
    validateSameAddress,
    checkMaxSizeComponent
} from "../../validations/inputValidation";

import {PROPERTY_TYPE} from "../../constants/inputsConstant";

import {dataPath, postalCodeServiceStatusPath, postalCodeCountExceededPath, readOnlyPath} from "../../constants/path";

import {Map} from "immutable";

const LocalAddressInput = props => {
    const getAddressData = id => props.state.getIn(dataPath([id]));
    const isPostalServiceDown = props.state.getIn(postalCodeServiceStatusPath(), false);
    const isTriesExceeded = props.state.getIn(postalCodeCountExceededPath(`${props.inputID}PostalCode`), false);
    const isReadOnlyInputs = props.state.getIn(readOnlyPath(), Map());
    const showEditableBlockAndStreet = inputId => (isPostalServiceDown || isTriesExceeded) && !isReadOnlyInputs.get(inputId);
    const showReadOnlyBlockAndStreet = inputId => isReadOnlyInputs.get(inputId);
    const addressFormat = props.state.getIn(dataPath([`${props.inputID}AddressFormat`]), "");
    const propertyType = props.state.getIn(dataPath([PROPERTY_TYPE]), "");
    const addressStreet = props.state.getIn(dataPath([`${props.inputID}Street`]), "");
    const addressBlock = props.state.getIn(dataPath([`${props.inputID}Block`]), "");
    const addressUnitNumber = props.state.getIn(dataPath([`${props.inputID}UnitNumber`]), "");
    const addressLevel = props.state.getIn(dataPath([`${props.inputID}Level`]), "");
    const addressBuilding = props.state.getIn(dataPath([`${props.inputID}Building`]), "");

    const levelandUnitValidator = field => {
        return addressFormat[0] === "H" || ["H3", "H5", "EC"].includes(propertyType)
            ? [validateFilledInput, onlyAlphanumeric, checkMaxSizeComponent(props, editInput, `${props.inputID}Building`, addressBuilding, 27 - addressUnitNumber.length - addressLevel.length) , checkMaxSize(4), validateSameAddress(field, props.state)]
            : [noValidation, onlyAlphanumeric, checkMaxSizeComponent(props, editInput, `${props.inputID}Building`, addressBuilding, 27 - addressUnitNumber.length - addressLevel.length), checkMaxSize(4), validateSameAddress(field, props.state)];
    };

    const getaddressFormatLabel = addressFormatCode => {
        const {FormConfig} = props;
        const localAddress = new FormConfig("homeMailingAddress");
        const {inputValues} = localAddress;
        const addressFormatList = inputValues[PROPERTY_TYPE] || [];

        const filteredAddressFormat = addressFormatList.filter(format => format.value === addressFormatCode) || [];
        if (filteredAddressFormat.length) {
            return filteredAddressFormat[0].label || "";
        }
        return "";
    };

    const homePropertyType = getaddressFormatLabel(addressFormat);
    const showHomePropertyType = homePropertyType && homePropertyType !== "";

    return (
        <div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    type={"Postal"}
                    inputLabel={props.inputLabels[`${props.inputID}PostalCode`]}
                    inputID={`${props.inputID}PostalCode`}
                    validators={[validateFilledInput, checkNumbers, checkSize(6), validateSameAddress(`${props.inputID}PostalCode`, props.state)]}
                />
            </div>
            {showEditableBlockAndStreet(`${props.inputID}Block`) && (
                <div className='homeMailingAddress--separator'>
                    <EditableTextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Block`]}
                        inputID={`${props.inputID}Block`}
                        validators={[validateFilledInput, onlyAlphanumeric, checkMaxSizeComponent(props, editInput, `${props.inputID}Street`, addressStreet, 29 - addressBlock.length), checkMaxSize(6)]}
                    />
                </div>
            )}
            {showEditableBlockAndStreet(`${props.inputID}Street`) && (
                <div className='homeMailingAddress--separator'>
                    <EditableTextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Street`]}
                        inputID={`${props.inputID}Street`}
                        validators={[validateFilledInput, onlyAlphanumeric, checkMaxSize(29 - addressBlock.length)]}
                    />
                </div>
            )}
            {showEditableBlockAndStreet(`${props.inputID}Building`) && (
                <div className='homeMailingAddress--separator'>
                    <TextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Building`]}
                        inputID={`${props.inputID}Building`}
                        validators={[noValidation, checkMaxSize(27 - addressUnitNumber.length - addressLevel.length)]}
                        upperCase={true}
                    />
                </div>
            )}
            {getAddressData(`${props.inputID}Block`) && showReadOnlyBlockAndStreet(`${props.inputID}Block`) ? (
                <div className='homeMailingAddress--separator'>
                    <ReadOnlyTextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Block`]}
                        inputID={`${props.inputID}Block`}
                    />
                </div>
            ) : null}
            {getAddressData(`${props.inputID}Street`) && showReadOnlyBlockAndStreet(`${props.inputID}Street`) ? (
                <div className='homeMailingAddress--separator'>
                    <ReadOnlyTextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Street`]}
                        inputID={`${props.inputID}Street`}
                    />
                </div>
            ) : null}
            {getAddressData(`${props.inputID}Building`) && showReadOnlyBlockAndStreet(`${props.inputID}Building`) ? (
                <div className='homeMailingAddress--separator'>
                    <ReadOnlyTextInput
                        {...props}
                        inputLabel={props.inputLabels[`${props.inputID}Building`]}
                        inputID={`${props.inputID}Building`}
                    />
                </div>
            ) : null}
            <div className='homeMailingAddress--separator'>
                <div className='homeMailingAddress-unitNumber-container'>
                    <div style={{flex: "1"}}>
                        {isReadOnlyInputs.get(`${props.inputID}Level`, false) ? (
                            <ReadOnlyTextInput
                                {...props}
                                inputLabel={props.inputLabels[`${props.inputID}Level`]}
                                inputID={`${props.inputID}Level`}
                                validators={levelandUnitValidator(`${props.inputID}Level`)}
                            />
                        ) : (
                            <EditableTextInput
                                {...props}
                                inputLabel={props.inputLabels[`${props.inputID}Level`]}
                                inputID={`${props.inputID}Level`}
                                validators={levelandUnitValidator(`${props.inputID}Level`)}
                            />
                        )}
                    </div>
                    <div className='homeMailingAddress-unitNumber-dash'> - </div>
                    <div style={{flex: "1"}}>
                        {isReadOnlyInputs.get(`${props.inputID}UnitNumber`, false) ? (
                            <ReadOnlyTextInput
                                {...props}
                                inputLabel={"Unit Number"}
                                inputID={`${props.inputID}UnitNumber`}
                                validators={levelandUnitValidator(`${props.inputID}UnitNumber`)}
                            />
                        ) : (
                            <EditableTextInput
                                {...props}
                                inputLabel={props.inputLabels[`${props.inputID}UnitNumber`]}
                                inputID={`${props.inputID}UnitNumber`}
                                validators={levelandUnitValidator(`${props.inputID}UnitNumber`)}
                            />
                        )}
                    </div>
                </div>
            </div>
            {showHomePropertyType && (
                <div className='homeMailingAddress--separator'>
                    <ReadOnlyTextInput
                        {...props}
                        inputLabel={props.inputLabels[PROPERTY_TYPE]}
                        value={homePropertyType}
                    />
                </div>
            )}
        </div>
    );
};

LocalAddressInput.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    inputID: PropTypes.string.isRequired,
    inputLabels: PropTypes.object.isRequired,
    FormConfig: PropTypes.func.isRequired
};

export default LocalAddressInput;



