
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

import TextInput from "../TextInput/TextInput";
import Dropdown from "../Dropdown/Dropdown";
import ToggleInputWithTitle from "../ToggleInputWithTitle/ToggleInputWithTitle";
import ToggleInput from "../ToggleInput/ToggleInput";
import CrossSelling from "../CrossSelling/CrossSelling";

import {
    validateFilledInput,
    checkMaxSize,
    onlyLettersAndSpace,
    checkKrisFlyer,
    checkSize,
    checkAsiaMiles
} from "../../validations/inputValidation";

import {
    editMultipleNameOnCard,
    editMultipleAtmAccess,
    updateDailyCardLimit,
    updateCreditLimit,
    editInputByPath,
    removeKrisOrAsia,
    onToggle
} from "../../actions/actionCreators";

import {
    NAME_ON_CARD,
    CARD_LIMIT,
    CREDIT_LIMIT,
    ACTIVATE_ATM,
    LINK_TO_FLYER_MILES,
    LINK_TO_ASIA_MILES,
    KRYSFLYER_MEMBERSHIP_NUMBER,
    ASIA_MILES_MEMBERSHIP_NUMBER,
    CREDIT_SURE,
    SAME_DAY_DELIVERY
} from "../../constants/inputsConstant";

import {dataPath, productIdPath} from "../../constants/path";

const privMilesPids = ["001333", "001515", "001853"];

const PrivMilesLinkCard = props => {
    let showFlyer = props.state.getIn(dataPath([`${props.product}-${LINK_TO_FLYER_MILES}`]));
    let showAsia = props.state.getIn(dataPath([`${props.product}-${LINK_TO_ASIA_MILES}`]));
    const kryslylerNumber = props.state.getIn(["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER], "");
    const asiaMiles = props.state.getIn(["data", "products", props.products.first(), ASIA_MILES_MEMBERSHIP_NUMBER], "");
    showFlyer = kryslylerNumber ? true : showFlyer;
    showAsia = asiaMiles ? true : showAsia;
    return (
        <div>
            <div className='account-details--separator'>
                <ToggleInput
                    description={props.inputLabels[LINK_TO_FLYER_MILES]}
                    state={props.state}
                    step={props.step}
                    inputID={`${props.product}-${LINK_TO_FLYER_MILES}`}
                    value={showFlyer}
                    onClick={() => {
                        const toggleStatus = props.state.getIn(
                            dataPath([`${props.product}-${LINK_TO_FLYER_MILES}`]),
                            false
                        );
                        return toggleStatus
                            ? props.dispatch(
                                  removeKrisOrAsia(
                                      ["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER],
                                      props.step,
                                      `${props.product}-${LINK_TO_FLYER_MILES}`,
                                      false
                                  )
                              )
                            : props.dispatch(onToggle(props.step, `${props.product}-${LINK_TO_FLYER_MILES}`, true));
                    }}
                />
                {showFlyer && (
                    <TextInput
                        inputLabel={props.inputLabels[KRYSFLYER_MEMBERSHIP_NUMBER]}
                        inputID={`${props.product}-${KRYSFLYER_MEMBERSHIP_NUMBER}`}
                        validators={[validateFilledInput, checkKrisFlyer]}
                        onChange={e => {
                            const value = e.target.value;
                            const validators = [validateFilledInput, checkKrisFlyer, checkSize(10)];
                            const status = validators
                                .map(x => x(props.formData))
                                .map(x => x(value))
                                .filter(x => !x.status);

                            return props.dispatch(
                                editInputByPath(
                                    props.step,
                                    `${props.product}-${KRYSFLYER_MEMBERSHIP_NUMBER}`,
                                    ["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER],
                                    value,
                                    status
                                )
                            );
                        }}
                        value={props.state.getIn(
                            ["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER],
                            ""
                        )}
                        {...props}
                    />
                )}
            </div>
            <div className='account-details--separator'>
                <ToggleInput
                    description={props.inputLabels[LINK_TO_ASIA_MILES]}
                    state={props.state}
                    step={props.step}
                    inputID={`${props.product}-${LINK_TO_ASIA_MILES}`}
                    value={showAsia}
                    onClick={() => {
                        const toggleStatus = props.state.getIn(
                            dataPath([`${props.product}-${LINK_TO_ASIA_MILES}`]),
                            false
                        );
                        return toggleStatus
                            ? props.dispatch(
                                  removeKrisOrAsia(
                                      ["data", "products", props.products.first(), ASIA_MILES_MEMBERSHIP_NUMBER],
                                      props.step,
                                      `${props.product}-${LINK_TO_ASIA_MILES}`,
                                      false
                                  )
                              )
                            : props.dispatch(onToggle(props.step, `${props.product}-${LINK_TO_ASIA_MILES}`, true));
                    }}
                />
                {showAsia && (
                    <TextInput
                        inputLabel={props.inputLabels[ASIA_MILES_MEMBERSHIP_NUMBER]}
                        inputID={`${props.product}-${ASIA_MILES_MEMBERSHIP_NUMBER}`}
                        validators={[validateFilledInput, checkAsiaMiles]}
                        onChange={e => {
                            const value = e.target.value;
                            const validators = [validateFilledInput, checkSize(10), checkAsiaMiles];
                            const status = validators
                                .map(x => x(props.formData))
                                .map(x => x(value))
                                .filter(x => !x.status);

                            return props.dispatch(
                                editInputByPath(
                                    props.step,
                                    `${props.product}-${ASIA_MILES_MEMBERSHIP_NUMBER}`,
                                    ["data", "products", props.products.first(), ASIA_MILES_MEMBERSHIP_NUMBER],
                                    value,
                                    status
                                )
                            );
                        }}
                        value={props.state.getIn(
                            ["data", "products", props.products.first(), ASIA_MILES_MEMBERSHIP_NUMBER],
                            ""
                        )}
                        {...props}
                    />
                )}
            </div>
        </div>
    );
};

PrivMilesLinkCard.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    inputLabels: PropTypes.object.isRequired,
    inputValues: PropTypes.object.isRequired,
    step: PropTypes.string.isRequired,
    dispatch: PropTypes.func.isRequired,
    products: ImmutablePropTypes.list.isRequired,
    product: PropTypes.string
};

const HOCDropdown = props => {
    switch (props.productType) {
        case "CASA":
            return (
                <Dropdown
                    inputID={`${CARD_LIMIT}_${props.cardType}`}
                    inputLabel={props.inputLabels[CARD_LIMIT]}
                    dropdownItems={props.inputValues[CARD_LIMIT]}
                    onClick={item => {
                        props.dispatch(updateDailyCardLimit(props.pids, props.cardType, item.value));
                    }}
                    value={props.state.getIn(["ui", "accountDetails", `${CARD_LIMIT}_${props.cardType}`, "value"])}
                    {...props}
                />
            );
        case "Card":
            return (
                <Dropdown
                    inputID={`${CREDIT_LIMIT}_${props.cardType}`}
                    inputLabel={props.inputLabels[CREDIT_LIMIT]}
                    dropdownItems={props.inputValues[CREDIT_LIMIT]}
                    onClick={item => {
                        props.dispatch(updateCreditLimit(props.pids, props.cardType, item.value));
                    }}
                    value={props.state.getIn(["ui", "accountDetails", `${CREDIT_LIMIT}_${props.cardType}`, "value"])}
                    {...props}
                />
            );
        case "CASH+":
            return (
                <Dropdown
                    inputID={`${CREDIT_LIMIT}_${props.cardType}`}
                    inputLabel={props.inputLabels[CREDIT_LIMIT]}
                    dropdownItems={props.inputValues[CREDIT_LIMIT]}
                    onClick={item => {
                        props.dispatch(updateCreditLimit(props.pids, props.cardType, item.value));
                    }}
                    value={props.state.getIn(["ui", "accountDetails", `${CREDIT_LIMIT}_${props.cardType}`, "value"])}
                    {...props}
                />
            );
        default:
            return null;
    }
};

HOCDropdown.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    productType: PropTypes.string.isRequired,
    cardType: PropTypes.string.isRequired,
    inputLabels: PropTypes.object.isRequired,
    inputValues: PropTypes.object.isRequired,
    pids: ImmutablePropTypes.list.isRequired,
    dispatch: PropTypes.func.isRequired
};

const CardDetails = props => {
    const {productsConfig, formData, FormConfig} = props;
    const accountDetails = new FormConfig(props.step);
    const {buttons} = accountDetails;
    const isCreditSureProduct = props.productType === "Card" || props.productType === "CASH+";
    const validatorsArray = [validateFilledInput, onlyLettersAndSpace, checkMaxSize(19)];
    const validator = (value, validators) =>
        validators
            .map(x => x(formData))
            .map(x => x(value))
            .filter(x => !x.status);
    const firstProduct = props.pids.first();
    const cardImgPath = productsConfig.products[firstProduct].imgPath;
    const isSameDayDeliveryOn = productsConfig.sameDayDelivery ? productsConfig.sameDayDelivery : false;
    const crossSellProductIds = productsConfig.crossSellReference[props.state.getIn(productIdPath()).first()];
    const mandatoryProductIds = crossSellProductIds ? crossSellProductIds.filter(product => product.mandatory).map(product => product.pid) : [];
    const crossSellProducts = crossSellProductIds ? crossSellProductIds.map(product => productsConfig.products[product.pid]) : [];

    const nameOnCardOnChange = e => {
        return (
            e.target.value.length < 20 &&
            props.dispatch(
                editMultipleNameOnCard(
                    props.pids,
                    props.cardType,
                    e.target.value.toUpperCase(),
                    validator(e.target.value, validatorsArray),
                    props.step
                )
            )
        );
    };

    return (
        <div>
            <div className='account-details--separator-flex-card account-details-row'>
                <div className='account-details--card-container-flex'>
                    <div className='card-type'>
                        <img src={cardImgPath} alt='card' />
                    </div>
                    <div className='account-details-name-on-card'>
                        {props.state
                            .getIn(["ui", "accountDetails", `${NAME_ON_CARD}_${props.cardType}`, "value"], "")
                            .substr(0, 19)}
                    </div>
                </div>

                <div className='account-details--separator-flex'>
                    <TextInput
                        inputLabel={props.inputLabels.nameOntheCard}
                        inputID={`${NAME_ON_CARD}_${props.cardType}`}
                        validators={validatorsArray}
                        onChange={nameOnCardOnChange}
                        value={props.state
                            .getIn(["ui", "accountDetails", `${NAME_ON_CARD}_${props.cardType}`, "value"], "")
                            .substr(0, 19)}
                        {...props}
                    />
                    {props.cardType !== "700778" ? (
                        <div className='account-details--separator'>{HOCDropdown(props)}</div>
                    ) : (
                        <p style={{paddingLeft: "14px"}}>{props.inputLabels.cashPlusPlaceholder}</p>
                    )}
                </div>
            </div>
            {privMilesPids.includes(props.cardType) && <PrivMilesLinkCard product={props.cardType} {...props} />}
            {props.productType === "CASA" && (
                <div className='account-details--separator'>
                    <ToggleInputWithTitle
                        title={props.inputLabels.activateAtms}
                        description={props.inputDescriptions.activateAtms}
                        inputID={`${ACTIVATE_ATM}_${props.cardType}`}
                        onClick={() => {
                            props.dispatch(editMultipleAtmAccess(props.pids, props.cardType, true));
                        }}
                        value={props.state.getIn(["ui", "accountDetails", `${ACTIVATE_ATM}_${props.cardType}`, "value"])}
                        {...props}
                    />
                </div>
            )}
            {isCreditSureProduct && (<div className='account-details--separator'>
                <ToggleInput description={props.inputLabels[CREDIT_SURE]} inputID={CREDIT_SURE} {...props}
                    setPath={(value) => {
                        props.dispatch(
                            editInputByPath(
                                props.step,
                                CREDIT_SURE,
                                ["data", "products", props.products.first(), CREDIT_SURE],
                                value ? "Yes" : "No",
                                []
                            )
                        );
                    }}
                />
            </div>)}
            {props.productType === "Card" && isSameDayDeliveryOn && (
              <div className='account-details--separator'>
                  <ToggleInput description={props.inputLabels[SAME_DAY_DELIVERY]} inputID={SAME_DAY_DELIVERY} {...props}
                    setPath={(value) => {
                        props.dispatch(
                            editInputByPath(
                                props.step,
                                SAME_DAY_DELIVERY,
                                ["data", "products", props.products.first(), SAME_DAY_DELIVERY],
                                value ? "Yes" : "No",
                                []
                            )
                        );
                    }}
                  />
              </div>
            )}
            {
             crossSellProducts.length > 0 &&
             <div className='account-details--separator'>
                <CrossSelling
                    {...props}
                    crossSellProducts={crossSellProducts}
                    mandatoryProductIds={mandatoryProductIds}
                    buttons={buttons}
                />
            </div>
           }
        </div>
    );
};

CardDetails.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    productType: PropTypes.string.isRequired,
    pids: ImmutablePropTypes.list.isRequired,
    cardType: PropTypes.string.isRequired,
    inputLabels: PropTypes.object.isRequired,
    inputDescriptions: PropTypes.object.isRequired,
    dispatch: PropTypes.func.isRequired,
    productsConfig: PropTypes.object.isRequired,
    formData: PropTypes.object.isRequired
};

export default CardDetails;



