import React, {PropTypes} from "react";
import Slider from "react-slick";
import "./../../../resources/css/Slick.css";
import "./CrossSellingMobile.css";
import {
    addCrossProductCasa,
    addCrossProductCards,
    addCrossProductUnsecuredLoan,
    removeCrossProduct,
    getTenorTableData,
    addSAVJSProduct
} from "../../../actions/actionCreators";
import {productIdPath} from "../../../constants/path";
import {List, Map, fromJS} from "immutable";
import ImmutablePropTypes from "react-immutable-proptypes";
import {isExistCreditOrDebit} from "../../../utils";
import {CREDIT_CARDS, DEDIT_CARDS} from "../../../constants/common";
const responsive = [
  {breakpoint: 800,
    settings: {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 1.05
    }
  }
];

const addCrossProduct = (app, productCurrencies, familyType, productType, productId, typeOfProduct) => {
    const name = app.state.getIn(
        ["ui", "accountDetails", `nameOntheCard_${familyType}`, "value"],
        app.state.getIn(["data", "fullName"])
    );
    const preferedDailyLimit = app.state.getIn(["ui", "accountDetails", `cardLimit_${familyType}`, "value"], "2000");
    const activateAtms = app.state.getIn(["ui", "accountDetails", `activateAtms_${familyType}`, "value"], true);
    const toggleSameValue = app.state.getIn(["data", "samePurposeToggle"], false);
    const casaProducts = app.state.getIn(["data", "products"]).filter((value, index) => {
        return typeOfProduct.getIn(["products", index, "type"], false) === "CASA";
    });

    const defaultValues = toggleSameValue
        ? casaProducts
              .first()
              .get("currencies")
              .first()
        : Map({purposeOfFunds: "", sourceOfFunds: "", countryOfFunds: "SG"});

    switch (productType) {
        case "CASA":
            if (productId === "SAVJS") {
                return app.dispatch(addSAVJSProduct(productId, productCurrencies, familyType, defaultValues));
            }
            app.dispatch(
                addCrossProductCasa(
                    productId,
                    productCurrencies,
                    familyType,
                    {name: name.toUpperCase(), preferedDailyLimit, activateAtms},
                    defaultValues
                )
            );
            break;
        case "Card":
            app.dispatch(
                addCrossProductCards(productId, familyType, name.toUpperCase(), preferedDailyLimit, activateAtms)
            );
            break;
        default:
            app.dispatch(addCrossProductUnsecuredLoan(productId));
    }
    return null;
};

const CrossSellingItem = props => {
    const {productsConfig} = props;
    const typeOfProduct = fromJS(productsConfig);
    const productIds = props.state.getIn(productIdPath(), List());
    const productIsSelected = productIds.includes(props.product.pid);
    const selected = productIsSelected ? "--selected" : "";
    const buttonDisableClass = props.isMandatory ? "crossSelling-item-button--disable" : "";
    let buttonText = productIsSelected ? props.buttons.remove : props.buttons.add;
    buttonText = props.isMandatory ? props.buttons.mandatory : buttonText;
    const imagePath = productsConfig.products[props.product.pid].imgPathCrossell;
    const disable = props.disable ? " crossSelling-item-info-container--disable" : "";
    return (
      <div className={`crossSelling-item${selected}`}>
        <div className={`crossSelling-item-info-box--mobile${selected}`}>
          <div className={`crossSelling-item-info${selected}`}>
            <div className={`crossSelling-item-info-container${selected}${disable}`}>
              <img
                  className='crossSelling-item-image'
                  src={imagePath}
                  alt='productImg'
              />
              <div className='crossSelling-titleDescButton'>
                <div className='crossSelling-titleAndDesc--mobile'>
                  <div className='crossSelling-item-title-container'>
                      <div className={`crossSelling-item-title${selected}`}>{props.product.productName}</div>
                  </div>
                  <div className='crossSelling-item-desc-container'>
                      <ul>
                          {props.product.productDesc.map((desc, i) =>
                              <li key={i} className={`crossSelling-item-desc${selected}`} dangerouslySetInnerHTML={{__html: desc}} />
                          )}
                      </ul>
                  </div>
                </div>
                <button
                    className={`crossSelling-item-button ${buttonDisableClass}`}
                    onClick={() => {
                        const productCurrencies = Map({
                            [props.product.pid]: typeOfProduct.getIn([
                                "products",
                                props.product.pid,
                                "currencies",
                                "values"
                            ])
                        });
                        const familyType = typeOfProduct.getIn(["products", props.product.pid, "pid"]);
                        const productType = typeOfProduct.getIn(["products", props.product.pid, "type"]);
                        if (productType === "personalLoan" || productType === "balanceTransfer") {
                            props.dispatch(getTenorTableData(props.product.pid, productType === "personalLoan" ? "PL" : "BT"));
                        }
                        return productIsSelected
                            ? props.dispatch(removeCrossProduct(props.product.pid))
                            : addCrossProduct(
                                  {state: props.state, dispatch: props.dispatch},
                                  productCurrencies,
                                  familyType,
                                  productType,
                                  props.product.pid,
                                  typeOfProduct
                              );
                    }}
                >
                    {buttonText}
                </button>
                </div>
              </div>
          </div>
        </div>
      </div>
    );
};

const CrossSellingMobile = props => {
    const {formData} = props;
    const productIds = props.state.getIn(productIdPath(), List());
    return (
        <div className='crossSelling-container--mobile'>
            <div className='crossSelling-title'>{formData.formSteps.accountDetails["crossSellSubtitle-Default"]}</div>
            <div className='crossSelling-items-container--mobile'>
              <Slider responsive={responsive}>
                {props.crossSellProducts.map((product, i) =>
                      <CrossSellingItem
                          {...props}
                          key={i}
                          product={product}
                          isMandatory={props.mandatoryProductIds.includes(product.pid)}
                          dispatch={props.dispatch}
                          state={props.state}
                          disable={isExistCreditOrDebit(product.pid, productIds, CREDIT_CARDS, DEDIT_CARDS)}
                          buttons={props.buttons}
                      />
                )}
              </Slider>
            </div>
        </div>
    );
};

CrossSellingItem.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    productsConfig: PropTypes.object.isRequired,
    product: PropTypes.object.isRequired,
    isMandatory: PropTypes.bool.isRequired,
    dispatch: PropTypes.func.isRequired,
    buttons: PropTypes.object.isRequired
};

CrossSellingMobile.propTypes = {
    buttons: PropTypes.object.isRequired,
    crossSellProducts: PropTypes.array.isRequired,
    formData: PropTypes.object.isRequired,
    state: ImmutablePropTypes.map.isRequired
};

export default CrossSellingMobile;



