/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";
import SummaryBlockToggleElement from "../components/SummaryBlockToggleElement/SummaryBlockToggleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {dataPath, ui, isFinalSubmitPath} from "../../../../constants/path";
import {
    HOME_POSTAL_CODE,
    HOME_HOME_BLOCK,
    HOME_STREET,
    HOME_LEVEL,
    HOME_UNIT_NUMBER,
    MAILING_COUNTRY,
    MAILING_POSTAL_CODE,
    MAILING_HOME_BLOCK,
    MAILING_STREET,
    MAILING_LEVEL,
    MAILING_UNIT_NUMBER,
    ADDRESS_ONE,
    ADDRESS_TWO,
    ADDRESS_THREE,
    ADDRESS_FOUR,
    ADDRESS_CITY,
    SAME_HOME_AND_MAILING_ADD,
    MAILING_BUILDING,
    HOME_BUILDING,
    PROPERTY_TYPE,
    RESIDENCE_STATUS
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";

import {countriesNamesMap} from "../../../../constants/countriesMaps";

const SummaryBlockHomeAddress = props => {
    const homePostalCode = props.state.getIn(dataPath([HOME_POSTAL_CODE]));
    const homeBlock = props.state.getIn(dataPath([HOME_HOME_BLOCK]));
    const homeStreet = props.state.getIn(dataPath([HOME_STREET]));
    const homeLevel = props.state.getIn(dataPath([HOME_LEVEL]));
    const homeUnitNumber = props.state.getIn(dataPath([HOME_UNIT_NUMBER]));
    const homeBuilding = props.state.getIn(dataPath([HOME_BUILDING]));

    const homePropertyType = props.state.getIn(dataPath([PROPERTY_TYPE]));
    const homeHomeOwnership = props.state.getIn(dataPath([RESIDENCE_STATUS]));
    const homeLengthOfResidencyMonths = props.state.getIn(dataPath(["lengthOfResidencyMonths"]));
    const homeLengthOfResidencyYears = props.state.getIn(dataPath(["lengthOfResidencyYears"]));

    const differentAddress = props.state.getIn(dataPath([SAME_HOME_AND_MAILING_ADD]));
    const mailingCountry = props.state.getIn(dataPath([MAILING_COUNTRY]));

    const mailingPostalCode = props.state.getIn(dataPath([MAILING_POSTAL_CODE]));
    const mailingBlock = props.state.getIn(dataPath([MAILING_HOME_BLOCK]));
    const mailingStreet = props.state.getIn(dataPath([MAILING_STREET]));
    const mailingLevel = props.state.getIn(dataPath([MAILING_LEVEL]));
    const mailingUnitNumber = props.state.getIn(dataPath([MAILING_UNIT_NUMBER]));
    const mailingBuilding = props.state.getIn(dataPath([MAILING_BUILDING]));

    const addressOne = props.state.getIn(dataPath([ADDRESS_ONE]));
    const addressTwo = props.state.getIn(dataPath([ADDRESS_TWO]));
    const addressThree = props.state.getIn(dataPath([ADDRESS_THREE]));
    const addressFour = props.state.getIn(dataPath([ADDRESS_FOUR]));
    const addressCity = props.state.getIn(dataPath([ADDRESS_CITY]));
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);

    const {FormConfig} = props;

    const {title, inputLabels, dropDownValues} = new FormConfig("homeMailingAddress");
    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "homeMailingAddress"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                <div className='summary--block-content-container-flex'>
                    <SummaryBlockSimpleElement label={inputLabels[HOME_POSTAL_CODE]} value={homePostalCode} />
                    <SummaryBlockSimpleElement label={inputLabels[HOME_HOME_BLOCK]} value={homeBlock} />
                    <SummaryBlockSimpleElement label={inputLabels[HOME_STREET]} value={homeStreet} />
                    {homeBuilding && (
                        <SummaryBlockSimpleElement label={inputLabels[HOME_BUILDING]} value={homeBuilding} />
                    )}
                    {homeLevel && <SummaryBlockSimpleElement label={inputLabels[HOME_LEVEL]} value={homeLevel} />}
                    {homeUnitNumber && (
                        <SummaryBlockSimpleElement label={inputLabels[HOME_UNIT_NUMBER]} value={homeUnitNumber} />
                    )}
                    {homePropertyType && (
                        <SummaryBlockSimpleElement
                            label={inputLabels[PROPERTY_TYPE]}
                            value={dropDownValues[PROPERTY_TYPE][homePropertyType]}
                        />
                    )}
                    {homeHomeOwnership && (
                        <SummaryBlockSimpleElement
                            label={inputLabels[RESIDENCE_STATUS]}
                            value={dropDownValues[RESIDENCE_STATUS][homeHomeOwnership]}
                        />
                    )}
                    {homeLengthOfResidencyYears && (
                        <SummaryBlockSimpleElement
                            label={inputLabels["lengthOfResidencyYears"]}
                            value={homeLengthOfResidencyYears}
                        />
                    )}
                    {homeLengthOfResidencyMonths && (
                        <SummaryBlockSimpleElement
                            label={inputLabels["lengthOfResidencyMonths"]}
                            value={homeLengthOfResidencyMonths}
                        />
                    )}
                </div>
                <div className='summary--block-separator'>
                    <SummaryBlockToggleElement
                        label={inputLabels["sameHomeMailingAddress"]}
                        value={differentAddress ? "Yes" : "No"}
                    />
                </div>

                {differentAddress && mailingCountry === "SG" ? (
                    <div className='summary--block-content-container-flex'>
                        <SummaryBlockSimpleElement
                            label={inputLabels[MAILING_COUNTRY]}
                            value={countriesNamesMap[mailingCountry]}
                        />
                        <SummaryBlockSimpleElement label={inputLabels[MAILING_POSTAL_CODE]} value={mailingPostalCode} />
                        <SummaryBlockSimpleElement label={inputLabels[MAILING_HOME_BLOCK]} value={mailingBlock} />
                        <SummaryBlockSimpleElement label={inputLabels[MAILING_STREET]} value={mailingStreet} />
                        {mailingBuilding && (
                            <SummaryBlockSimpleElement label={inputLabels[MAILING_BUILDING]} value={mailingBuilding} />
                        )}
                        {mailingLevel && (
                            <SummaryBlockSimpleElement label={inputLabels[MAILING_LEVEL]} value={mailingLevel} />
                        )}
                        {mailingUnitNumber && (
                            <SummaryBlockSimpleElement
                                label={inputLabels[MAILING_UNIT_NUMBER]}
                                value={mailingUnitNumber}
                            />
                        )}
                    </div>
                ) : null}

                {differentAddress && mailingCountry !== "SG" ? (
                    <div className='summary--block-content-container-flex'>
                        <SummaryBlockSimpleElement
                            label={inputLabels[MAILING_COUNTRY]}
                            value={countriesNamesMap[mailingCountry]}
                        />
                        <SummaryBlockSimpleElement label={inputLabels[ADDRESS_ONE]} value={addressOne} />
                        {addressTwo && <SummaryBlockSimpleElement label={inputLabels[ADDRESS_TWO]} value={addressTwo} />}
                        {addressThree && (
                            <SummaryBlockSimpleElement label={inputLabels[ADDRESS_THREE]} value={addressThree} />
                        )}
                        {addressFour && (
                            <SummaryBlockSimpleElement label={inputLabels[ADDRESS_FOUR]} value={addressFour} />
                        )}
                         {addressCity && (
                            <SummaryBlockSimpleElement label={inputLabels[ADDRESS_CITY]} value={addressCity} />
                        )}
                    </div>
                ) : null}
            </div>
        </div>
    );
};

SummaryBlockHomeAddress.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    FormConfig: PropTypes.func.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockHomeAddress);



