import {push} from "react-router-redux";
import {detectPathName} from "../utils";

import {ACTION_SUBMIT_FULL_APPLICATION_SUCCESS} from "../constants/actionsConstant";

const customRouterMiddleware = ({dispatch}) => next => action => {
    if (action.type === ACTION_SUBMIT_FULL_APPLICATION_SUCCESS) {
        try {
            document.body.style.position = "relative";
        } catch (err) {
          //
        }
        dispatch(push(`${detectPathName()}congratulation`));
    }
    return next(action);
};
export default customRouterMiddleware;



