import {addNewInputToState, removeInputFromState, variancePipe} from "../utils";
import {
    NATURE_OF_EMPLOYMENT,
    NAME_OF_EMPLOYER,
    INDUSTRY,
    SAME_HOME_AND_MAILING_ADD,
    MAILING_POSTAL_CODE,
    MAILING_COUNTRY,
    ADDRESS_ONE,
    PURPOSE_OF_FUNDS,
    SOURCE_OF_FUNDS,
    COUNTRY_OF_FUNDS,
    CURRENCIES,
    OCCUPATION
} from "../constants/inputsConstant";

import {ACTION_EDIT_INPUT, ACTION_UPDATE_CURRENCIES} from "../constants/actionsConstant";
import {Map} from "immutable";

export const inputConfig = ({inputId, step, value, type, currency}) => state => {
    const generateNewKey = () => {
        if (type === ACTION_EDIT_INPUT && inputId === MAILING_COUNTRY && value !== "SG") {
            return `${inputId}:Others`;
        }
        if (type === ACTION_UPDATE_CURRENCIES) {
            return inputId;
        }
        return inputId && `${value}` ? `${inputId}:${value}` : "";
    };
    const reducer = {
        [`${NATURE_OF_EMPLOYMENT}:salaried`]: () => [addNewInputToState([OCCUPATION, NAME_OF_EMPLOYER, INDUSTRY])],
        [`${NATURE_OF_EMPLOYMENT}:SELFE`]: () => [
            removeInputFromState([OCCUPATION]),
            addNewInputToState([INDUSTRY, NAME_OF_EMPLOYER])
        ],
        [`${NATURE_OF_EMPLOYMENT}:STUDT`]: () => [removeInputFromState([OCCUPATION, NAME_OF_EMPLOYER, INDUSTRY])],
        [`${NATURE_OF_EMPLOYMENT}:NOINC`]: () => [removeInputFromState([OCCUPATION, NAME_OF_EMPLOYER, INDUSTRY])],
        [`${SAME_HOME_AND_MAILING_ADD}:true`]: () => [
            addNewInputToState([MAILING_POSTAL_CODE])
        ],
        [`${SAME_HOME_AND_MAILING_ADD}:false`]: () => [
            removeInputFromState([MAILING_POSTAL_CODE, ADDRESS_ONE, MAILING_COUNTRY])
        ],
        [`${MAILING_COUNTRY}:Singapore`]: () => [
            addNewInputToState([MAILING_POSTAL_CODE]),
            removeInputFromState([ADDRESS_ONE])
        ],
        [`${MAILING_COUNTRY}:Others`]: () => [
            removeInputFromState([MAILING_POSTAL_CODE]),
            addNewInputToState([ADDRESS_ONE])
        ],
        [CURRENCIES]: () => {
            const currencies = state.getIn(["data", "currencies"], Map());

            return currencies.has(currency)
                ? [
                    addNewInputToState([
                        `${PURPOSE_OF_FUNDS}-${currency}`,
                        `${SOURCE_OF_FUNDS}-${currency}`,
                        `${COUNTRY_OF_FUNDS}-${currency}`
                    ])
                ]
                : [
                    removeInputFromState([
                        `${PURPOSE_OF_FUNDS}-${currency}`,
                        `${SOURCE_OF_FUNDS}-${currency}`,
                        `${COUNTRY_OF_FUNDS}-${currency}`
                    ])
                ];
        },
        // [`${IS_EXISTING_KRISFLYER_MEMBER}:true`]: () => [addNewInputToState([KRYSFLYER_MEMBERSHIP_NUMBER])],
        // [`${IS_EXISTING_KRISFLYER_MEMBER}:false`]: () => [removeInputFromState([KRYSFLYER_MEMBERSHIP_NUMBER])],
        DEFAULT: () => state
    };
    const newKey = generateNewKey();
    return reducer[newKey] ? variancePipe(reducer[newKey](), state, step) : reducer.DEFAULT();
};



