/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {dataPath, ui, isFinalSubmitPath} from "../../../../constants/path";
import {
    DATE_OF_BIRTH,
    MARITAL_STATUS,
    NATIONALITY,
    GENDER,
    COUNTRY_OF_BIRTH,
    EDUCATION_LEVEL,
    JUNIOR_SAVER_NAME,
    JUNIOR_SAVER_GENDER,
    JUNIOR_SAVER_DOB,
    JUNIOR_SAVER_BIRTH_CERT,
    JUNIOR_SAVER_NATIONALITY,
    JUNIOR_SAVER_COUNTRY_OF_BIRTH,
    COUNTRY_OF_RESIDENCE
} from "../../../../constants/inputsConstant";
import {countriesNamesMap} from "../../../../constants/countriesMaps";
import "./SummaryBlock.css";

const SummaryBlockPersonalInfo = props => {
    const dateOfBirth = props.state.getIn(dataPath([DATE_OF_BIRTH]));
    const maritalStatus = props.state.getIn(dataPath([MARITAL_STATUS]));
    const nationality = props.state.getIn(dataPath([NATIONALITY]));
    const gender = props.state.getIn(dataPath([GENDER]));
    const educationLevel = props.state.getIn(dataPath([EDUCATION_LEVEL]));
    const countryOfBirth = props.state.getIn(dataPath([COUNTRY_OF_BIRTH]));
    const countryOfResidence = props.state.getIn(dataPath([COUNTRY_OF_RESIDENCE]));
    const jsName = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_NAME]));
    const jsGender = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_GENDER]));
    const jsDOB = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_DOB]));
    const jsBirthCert = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_BIRTH_CERT]));
    const jsNationality = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_NATIONALITY]));
    const jsCountryOfBirth = props.state.getIn(dataPath(["products", "SAVJS", JUNIOR_SAVER_COUNTRY_OF_BIRTH]));
    const {FormConfig} = props;
    const {dropDownValues, title, inputLabels} = new FormConfig("personalInfo");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);

    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "personalInfo"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                <div className='summary--block-content-container-flex'>
                    <SummaryBlockSimpleElement label={inputLabels[DATE_OF_BIRTH]} value={dateOfBirth} />
                    <SummaryBlockSimpleElement label={inputLabels[GENDER]} value={dropDownValues[GENDER][gender]} />
                    <SummaryBlockSimpleElement
                        label={inputLabels[MARITAL_STATUS]}
                        value={dropDownValues[MARITAL_STATUS][maritalStatus]}
                    />
                    <SummaryBlockSimpleElement
                        label={inputLabels[EDUCATION_LEVEL]}
                        value={dropDownValues[EDUCATION_LEVEL][educationLevel]}
                    />
                    <SummaryBlockSimpleElement label={inputLabels[NATIONALITY]} value={countriesNamesMap[nationality]} />
                    <SummaryBlockSimpleElement
                        label={inputLabels[COUNTRY_OF_BIRTH]}
                        value={countriesNamesMap[countryOfBirth]}
                    />
                    <SummaryBlockSimpleElement
                        label={inputLabels[COUNTRY_OF_RESIDENCE]}
                        value={countriesNamesMap[countryOfResidence]}
                    />
                </div>
                {jsName && (
                    <div>
                        <div style={{color: "#012667", marginBottom: "5px", fontWeight: "bold"}}>Child Details</div>
                        <div className='summary--block-content-container-flex'>
                            <SummaryBlockSimpleElement label={inputLabels[JUNIOR_SAVER_NAME]} value={jsName} />
                            <SummaryBlockSimpleElement
                                label={inputLabels[JUNIOR_SAVER_GENDER]}
                                value={dropDownValues[GENDER][jsGender]}
                            />
                            <SummaryBlockSimpleElement label={inputLabels[JUNIOR_SAVER_DOB]} value={jsDOB} />
                            <SummaryBlockSimpleElement
                                label={inputLabels[JUNIOR_SAVER_NATIONALITY]}
                                value={jsBirthCert}
                            />
                            <SummaryBlockSimpleElement
                                label={inputLabels[JUNIOR_SAVER_COUNTRY_OF_BIRTH]}
                                value={countriesNamesMap[jsNationality]}
                            />
                            <SummaryBlockSimpleElement
                                label='Country of Birth'
                                value={countriesNamesMap[jsCountryOfBirth]}
                            />
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

SummaryBlockPersonalInfo.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired,
    FormConfig: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockPersonalInfo);



