import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {Map} from "immutable";

import {updateCurrencies} from "../../actions/actionCreators";

import "./CurrencySelection.css";

const Currency = props => {
    const isSelected = props.selectedCurrencies.has(props.currency)
        ? "input-items input-items--selected"
        : "input-items";
    return (
        <div className={isSelected} onClick={props.onClick}>
            {props.currency}
        </div>
    );
};

const CurrencySelection = props => {
    const selectedCurrencies = props.state.getIn(["data", "products", props.pid, "currencies"], Map());
    return (
        <div className='selection-input-container'>
            <div className='input-label'>Currencies</div>
            <div className='input-selection'>
                {props.currencies.map(currency => (
                    <Currency
                        currency={currency}
                        selectedCurrencies={selectedCurrencies}
                        onClick={() => {
                            if (props.clickable) {
                                props.dispatchUpdateCurrencies(currency, props.step, props.inputID, props.pid);
                            }
                        }}
                        key={"currency-" + currency}
                    />
                ))}
            </div>
        </div>
    );
};

CurrencySelection.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    pid: PropTypes.string.isRequired,
    currency: PropTypes.string,
    currencies: ImmutablePropTypes.list.isRequired
};

Currency.propTypes = {
    selectedCurrencies: ImmutablePropTypes.map.isRequired,
    onClick: PropTypes.func.isRequired,
    currency: PropTypes.string.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchUpdateCurrencies: bindActionCreators(updateCurrencies, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(CurrencySelection);



