import {ACTION_POST_PARTIAL_APPLICATION_SUCCESS} from "../constants/actionsConstant";
import {getEncryptionParams} from "../actions/actionCreators";

const getEncryptionParamsMiddleware = ({dispatch}) => next => action => {
    if (action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && action.currentStep === "digitalServices") {
        next(action);
        return dispatch(getEncryptionParams(action.nextStep));
    }
    return next(action);
};

export default getEncryptionParamsMiddleware;



