/* global FORMS_CNFG_GLBL, UOBApplet */
import {productIdPath, dataPath} from "../constants/path";
import {Map} from "immutable";
import {formData} from "../config/formDataConfig";
import {handleSessionTimeout, handleValidationMismatch, handleServerDown} from "../utils";
import {
    MOBILE_NUMBER
} from "../constants/inputsConstant";

const baseURL = FORMS_CNFG_GLBL.WS_PATH;
const serverURL = FORMS_CNFG_GLBL.SERVER_PATH;

const validateOtpURL = () => `${baseURL}/sg/v1/access/verifyotp`;
const generateOtpURL = () => `${baseURL}/sg/v1/access/sendotp`;
const getAddressURL = (postalcode) => `${serverURL}/sg/v1/referencedata/address?postalcode=${postalcode}`;
const postPartialApplicationURL = () => `${baseURL}/dosubmitpartialform`;
const postInitialApplicationURL = () => `${baseURL}/initiate`;
const submitApplicationURL = () => `${baseURL}/dosubmitform`;
const getMyinfoDetailURL = () => `${baseURL}/getmyinfodetail/index.json`;
// const checkPendingApplicationURL = () => `${baseURL}/checkpendingapplications`;
const getPendingApplicationURL = () => `${baseURL}/getpendingapplications`;
const getTenorDataTableURL = (product, type) => `${serverURL}/sg/v1/referencedata/promotions?product=${product}&type=${type}`;
const getBankListURL = () => `${serverURL}/sg/v1/referencedata/banks`;
const tinValidateURL = (country, tinId) => `${serverURL}/sg/v1/validate/tin/${country}/${tinId}`;
const validateUsernameURL = (username) => `${baseURL}/sg/v1/validate/pib/username/${username}`;
const getUserStatusURL = () => `${baseURL}/sg/v1/products/start`;
const getEncryptionParamsURL = () => `${serverURL}/sg/v1/access/publickeyandrandom`;
export const fileUploadUrl = () => `${baseURL}/fileUpload`;

const headers = {
    Accept: "application/json",
    "Content-Type": "application/json"
    // Cache: "no-cache"
};

const SUCCESS = "00000";
const TIME_OUT = 90000; // time out 90 seconds

const postInit = data => ({headers, method: "GET", credentials: "same-origin", body: JSON.stringify(data)});

export const postOTP = async ({action, successDispatch, errorDispatch}) => {
    const query = window
        .fetch(validateOtpURL(), postInit(action.payload))
        .then(res => {
            if (res.status === 200) {
                return res;
            } else if (res.status === 400) {
                throw Error("400");
            }
            throw Error("500");
        })
        .then(() => [null, true])
        .catch(err => {
            return err.message === "400" ? ["400", null] : ["500", null];
        });

    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getOTP = async ({successDispatch, errorDispatch, getState}) => {
    const mobile_number = getState().state.getIn(dataPath([MOBILE_NUMBER]),"").replace(/\s+/g, "");
    const query = window
        .fetch(generateOtpURL(), postInit({mobile_number}))
        .then(res => {
            if (res.status === 200) {
                return res.json();
            } else if (res.status === 400) {
                throw Error("400");
            }
            throw Error("500");
        })
        .then(data => [null, {smsPrefix: data && data.prefix ? data.prefix.replace(/-+$/, "") : ""}])
        .catch(err => {
            return err.message === "400" ? ["400", null] : ["500", null];
        });

    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getAddress = async ({action, successDispatch, errorDispatch}) => {
    const query = window
        .fetch(getAddressURL(action.payload.postal))
        .then(res => {
            if (res.status === 200) {
                return res;
            } else if (res.status === 400) {
                throw Error("400");
            }
            throw Error("500");
        })
        .then(res => res.json())
        .then(data => [null, data])
        .catch(err => {
            return err.message === "400" ? ["400", null] : ["500", null];
        });

    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const postPartialApplication = ({getState, action, successDispatch, errorDispatch, loadingDispatch}) => {
    const state = getState().state;
    const productId = state.getIn(productIdPath()).get(0);
    const stateData = state.getIn(dataPath([]), Map()).toJS();
    const fullData = {...formData, ...stateData};
    fullData.mobileNumber = fullData.mobileNumber && fullData.mobileNumber.replace(/ /g,"");
    loadingDispatch({loadding: true});
    const postApplicationURL = action.currentStep === "basicInfo" ? postInitialApplicationURL(productId) : postPartialApplicationURL(productId);
    return new Promise((resolve, reject) => {
        let interval = setTimeout(() => reject(errorDispatch(new TypeError("Client timed out"))), TIME_OUT);
        fetch(postApplicationURL, postInit(fullData))
            .then(res => {if (res.status === 200) { return res; } throw res.status;})
            .then(res => res.json())
            .then(({status, error, data, readonly}) => {
                clearTimeout(interval);
                resolve({status, error, data, readonly});
                return status === SUCCESS ? successDispatch(["basicInfo", "accountDetails"].indexOf(action.currentStep) > -1 ? {data, readonly} : {data: fullData}) : errorDispatch(error || status);
            }).catch(err => errorDispatch(err));
    }).catch(e => errorDispatch(e));
};

export const getMyInfoDetails = async ({successDispatch, errorDispatch, dispatch}) => {
    const query = window
        .fetch(getMyinfoDetailURL(), postInit())
        .then(res => handleSessionTimeout(res, dispatch))
        .then(res => handleServerDown(res, dispatch))
        .then(res => res.json())
        .then(({status, errorDesc, data}) => (status ? [null, {...data, status}] : [errorDesc || status, null]))
        .catch(err => [err]);

    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

const dostoreRSApublickey = (uobApplet, Mod_siz, Pub_String, Mod_String) => {
    const errorNum = uobApplet.OBM_SpecifyRSAPublicKey(parseInt(Mod_siz) / 8 , Pub_String, Mod_String);
	// Logger.log(uobApplet + Mod_siz);
    if (errorNum !== 0) {
        return false;
    }
    return true;
};

const encryptUserLogin = (pwd, RN_String, Mod_siz, Pub_String, Mod_String) => {
    const uobApplet = new UOBApplet();

    dostoreRSApublickey(uobApplet, Mod_siz, Pub_String, Mod_String);
    let C_String = "";
    let P_String = "";
    let PINString = pwd;

    const errorNum = uobApplet.OBM_EncryptPassword(PINString, RN_String);
    // Logger.log(errorNum);
    if (errorNum !== 0) {
        return null;
    }
    C_String = uobApplet.OBM_GetEncryptedPassword();
    P_String = uobApplet.OBM_GetEncodingParameter();
    // Logger.log('C_String '+C_String+' P_String '+P_String);
    if (P_String && C_String && C_String !== "") {
        return {
            encryptedPassword: C_String.toUpperCase(),
            encodingParameter: P_String.toUpperCase()
        };
    }
    return null;
};

export const postFullApplication = async ({getState, successDispatch, errorDispatch, dispatch}) => {
    const state = getState().state;
    const stateData = state.getIn(dataPath([]), Map()).toJS();

    try {
        const isNTB = state.getIn(["app","isNTB"], false);
        if (!isNTB) {
            throw new Error();
        }
        const encryptData = state.getIn(["app", "encryptionParams"], Map()).toJS();
        const password = state.getIn(["app", "password"], "");
        const encPassObj = encryptUserLogin(password, encryptData.onetimeSaltValue,
                    encryptData.modSize, encryptData.publicComponent, encryptData.publicKey);

        stateData.userIdentity = stateData.username;
        stateData.legalIdentity = stateData.isSG ? stateData.nric : stateData.passport;
        stateData.legalIdentityType = stateData.isSG ? "IC" : "PP";
        stateData.legalIdentityCountry = stateData.isSG ? "SG" : stateData.passportIdentityCountry;
        stateData.encryptedPassword = encPassObj.encryptedPassword;
        stateData.encryptedNewPassword = encPassObj.encryptedPassword;
        stateData.keyIndex = encryptData.publicKeyIndex;
        stateData.randomAccessNumber = encryptData.onetimeSaltValue;
        stateData.encodingParameter = encPassObj.encodingParameter;

    } catch (e) {
        stateData.userIdentity = null;
        stateData.legalIdentity = null;
        stateData.legalIdentityType = null;
        stateData.legalIdentityCountry = null;
        stateData.encryptedPassword = null;
        stateData.encryptedNewPassword = null;
        stateData.keyIndex = null;
        stateData.randomAccessNumber = null;
        stateData.encodingParameter = null;
    }
    try {
        stateData.mobileNumber = stateData.mobileNumber.replace(/ /g,"");
    } catch (e) {
      //
    }

    try {
        const isMyInfoFlow = state.getIn(["app", "myInfo"], false);
        if (!isMyInfoFlow) {
            const isSG = stateData.isSG;
            const isPR = isSG && stateData.nationality !== "SG";
            let residentialStatus = "A";
            if (isSG) {
                residentialStatus = isPR ? "P" : "C";
            }
            stateData.residentialStatus = residentialStatus;
        }
    } catch (e) {
      //
    }
    // stateData
    const query = window
        .fetch(submitApplicationURL(), postInit(stateData))
        .then(res => handleSessionTimeout(res, dispatch))
        .then(res => handleValidationMismatch(res, dispatch))
        .then(res => handleServerDown(res, dispatch))
        .then(res => res.json())
        .then(({status, errorDesc, data}) => (status === SUCCESS ? [null, data] : [errorDesc || status, null]))
        .catch(err => [err]);

    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

