export const getPendingApplications = async ({getState, successDispatch, errorDispatch, dispatch}) => {
    const pendingApplications = getState()
        .state.getIn(dataPath(["pendingApplications"]))
        .toJS();
    const formatData = {
        productId: pendingApplications
    };
    const query = window
        .fetch(getPendingApplicationURL(), postInit(formatData))
        .then(res => handleSessionTimeout(res, dispatch))
        .then(res => handleValidationMismatch(res, dispatch))
        .then(res => handleServerDown(res, dispatch))
        .then(res => res.json())
        .then(({status, errorDesc, data}) => (status === SUCCESS ? [null, data] : [errorDesc || status, null]))
        .catch(err => [err]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getTenorTableData = async ({successDispatch, errorDispatch, action}) => {
    const isCCregex = /CC.*/gi;
    const type = action.promotionType;
    const product = isCCregex.test(action.pid) ? "CC" : "CP";

    const query = window
        .fetch(getTenorDataTableURL(product, type))
        .then(res => {
            if (res.status === 200) {
                return res;
            }
            throw Error("500");
        })
        .then(res => res.json())
        .then((data) => [null, data])
        .catch(err => [err]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getBankList = async ({successDispatch, errorDispatch, dispatch}) => {
    const query = window
        .fetch(getBankListURL())
        .then(res => handleSessionTimeout(res, dispatch))
        .then(res => handleValidationMismatch(res, dispatch))
        .then(res => handleServerDown(res, dispatch))
        .then(res => res.json())
        .then(({banks}) => [null, banks])
        .catch(err => [err]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const validateTIN = async ({successDispatch, errorDispatch, action}) => {
    const query = window
        .fetch(tinValidateURL(action.country, action.id), postInit())
        .then(res => {
            if (res.status === 200) {
                return [null, {isValidate: true, showValid: true}];
            } else if (res.status === 400) {
                return [null, {isValidate: false, showValid: false}];
            }
            return [null, {isValidate: false, showValid: true}];
        })
        .catch(() => [null, {isValidate: false, showValid: true}]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const validateUsername = async ({getState, successDispatch, errorDispatch, action}) => {
    const state = getState().state;
    const stateData = state.getIn(dataPath([]), Map()).toJS();

    const checkUserData = {
        userIdentity: stateData.username,
        legalIdentity: stateData.isSG ? stateData.nric : stateData.passport,
        legalIdentityType: stateData.isSG ? "IC" : "PP",
        legalIdentityCountry: stateData.isSG ? "SG" : stateData.passportIdentityCountry
    };

    const query = window
        .fetch(validateUsernameURL(action.username), postInit(checkUserData))
        .then(res => {
            if (res.status === 200) {
                return [null, {isValid: true}];
            } else if (res.status === 400) {
                return [{errorMsg: "usernameIsNotAvailable", global: false}, null];
            }
            return [{errorMsg: "serviceNotAvailable", global: true}, null];
        })
        .catch(() => [{errorMsg: "serviceNotAvailable", global: true}, null]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getEncryptionParams = async ({successDispatch, errorDispatch}) => {
    const query = window
        .fetch(getEncryptionParamsURL())
        .then(res => {
            if (res.status === 200) {
                return res.json();
            }
            throw new Error("serviceNotAvailable");
        })
        .then(result => [null, result])
        .catch(err => [err]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

export const getUserStatus = async ({getState, successDispatch, errorDispatch}) => {
    const state = getState().state;
    const stateData = state.getIn(dataPath([]), Map()).toJS();

    const userStatusData = {
        legalIdentity: stateData.isSG ? stateData.nric : stateData.passport,
        legalIdentityType: stateData.isSG ? "IC" : "PP",
        legalIdentityCountry: stateData.isSG ? "SG" : stateData.passportIdentityCountry
    };

    const query = window
        .fetch(getUserStatusURL(), postInit(userStatusData))
        .then(res => {
            if (res.status === 200) {
                return res.json();
            }
            throw new Error();
        })
        .then(body => {
            if (!body.existingRelationship) {
                return [null, {isNTB: true}];
            }
            throw new Error();
        })
        .catch(() => [{errorMsg: "serviceNotAvailable", global: false}, null]);
    const [error, result] = await query;
    return error ? errorDispatch(error) : successDispatch(result);
};

