import React, {Component} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import TextInput from "../../TextInput/TextInput";
import PhoneInput from "../../PhoneInput/PhoneInput";
import Accordian from "../../Accordian/Accordian";
import ToggleInput from "../../ToggleInput/ToggleInput";
import DropdownWithFlag from "../../DropdownWithFlag/DropdownWithFlag";
import {countriesLabelValue} from "../../../constants/countriesMaps";

import {addValidFlag, getMyInfo, onToggle, removeBillingProof} from "../../../actions/actionCreators";
import {
    validatePhoneNumber,
    validateNRICAndPassport,
    validateFilledInput,
    checkFullName,
    checkEmail,
    checkMaxSize,
    onlyAlphanumeric,
    checkDateOfBirthFormat,
    checkDateIsOverTodayDate,
    checkDateFormat,
    expiredMonth
} from "../../../validations/inputValidation";

import "./BasicInfo.css";
import {List} from "immutable";

import {
    FULL_NAME,
    NRIC,
    EMAIL,
    MOBILE_NUMBER,
    IS_SG,
    PASSPORT_EXPIRY,
    PASSPORT,
    PASSPORT_IDENTITY_COUNTRY
} from "../../../constants/inputsConstant";

import {dataPath, myInfoFlagPath, currentStepPath} from "../../../constants/path";

class BasicInfo extends Component {
    // DEMO
    componentDidMount() {
        // this.props.dispatchInit();
        // const props = this.props;
        // const isMyInfoFlow = props.state.getIn(myInfoFlagPath());
        // return !isMyInfoFlow && props.dispatch(removeNRIC()) && props.dispatch(onToggle(props.step, IS_SG, false));
    }
    render() {
        const props = this.props;
        const {FormConfig} = props;
        const basicInfo = new FormConfig(props.step);
        const {inputLabels, title, subtitle, inputValues} = basicInfo;
        const [postMessage, postMessageContent] = basicInfo.getPostMessage();
        const hanYuPinYinName = props.state.getIn(dataPath(["hanYuPinYinName"]));
        const aliasName = props.state.getIn(dataPath(["aliasName"]));
        const hanyuPinYinAliasName = props.state.getIn(dataPath(["hanyuPinYinAliasName"]));
        const marriedName = props.state.getIn(dataPath(["marriedName"]));
        const otherNames = List([hanYuPinYinName, aliasName, hanyuPinYinAliasName, marriedName]);
        const isMyInfoFlow = props.state.getIn(myInfoFlagPath());
        const isSG = props.state.getIn(dataPath([IS_SG]));
        const countriesWithNoSG = countriesLabelValue.filter(country => country.value !== "SG");
        const residentialStatus = props.state.getIn(dataPath(["residentialStatus"]));
        const getResidentialStatusLabel = () => {
            const rs = inputValues.residentialStatus.find(x => x.value === residentialStatus);
            return rs ? rs.label : "";
        };
        const currentStep = props.state.getIn(currentStepPath(), "");
        const disabledBasicStep = (currentStep !== "basicInfo" && currentStep !== "otp") ? true : false;

        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <div>
                    <TextInput
                        inputLabel={inputLabels.fullName}
                        inputID={FULL_NAME}
                        disabled={disabledBasicStep}
                        validators={[validateFilledInput, checkFullName, checkMaxSize(70)]}
                        {...props}
                    />
                </div>
                {otherNames.some(v => v) ? (
                    <div className='basicInfoOtherNames'>
                        {otherNames.map((v, i) => (
                            <div className='basicInfoOtherNames-items' key={i}>
                                {v}
                            </div>
                        ))}
                    </div>
                ) : null}
                {!isMyInfoFlow && (
                    <div className='basicInfoSeparator'>
                        <ToggleInput
                            description={inputLabels.isSG}
                            state={props.state}
                            step={props.step}
                            inputID={IS_SG}
                            onClick={() => {
                                const toggleStatus = props.state.getIn(dataPath([IS_SG]), false);
                                if (!disabledBasicStep) {
                                    return toggleStatus
                                        ? props.dispatch(onToggle(props.step, IS_SG, false))
                                        : props.dispatch(removeBillingProof(props.step, IS_SG, true));
                                } else {
                                    return null;
                                }
                            }}
                        />
                    </div>
                )}
                {isSG || isMyInfoFlow ? (
                    <div className='basicInfoSeparator'>
                        <TextInput
                            inputLabel={inputLabels.nric}
                            inputID={NRIC}
                            upperCase={true}
                            disabled={disabledBasicStep}
                            validators={[validateFilledInput, validateNRICAndPassport]}
                            {...props}
                        />
                        {
                          residentialStatus && residentialStatus !== "" && (
                            <div className='basicInfoOtherNames'>
                                <div className='basicInfoOtherNames-items' style={{paddingBottom: 10}}>
                                    {
                                      getResidentialStatusLabel()
                                    }
                                </div>
                            </div>
                          )
                        }
                    </div>
                ) : (
                    <div>
                        <div className='basicInfoSeparator'>
                            <TextInput
                                inputLabel={inputLabels.passport}
                                inputID={PASSPORT}
                                upperCase={true}
                                disabled={disabledBasicStep}
                                validators={[validateFilledInput, onlyAlphanumeric, checkMaxSize(15)]}
                                {...props}
                            />
                        </div>
                        <div className='basicInfoSeparator'>
                            <TextInput
                                inputLabel={inputLabels.passportExpiry}
                                inputID={PASSPORT_EXPIRY}
                                disabled={disabledBasicStep}
                                validators={[validateFilledInput, checkDateOfBirthFormat, checkDateFormat, checkDateIsOverTodayDate, expiredMonth(6)]}
                                {...props}
                                dateFormat
                            />
                        </div>
                        <div className='personalInfoSeparator'>
                            <DropdownWithFlag
                                inputLabel={inputLabels[PASSPORT_IDENTITY_COUNTRY]}
                                inputID={PASSPORT_IDENTITY_COUNTRY}
                                disabled={disabledBasicStep}
                                dropdownWithFlagItems={countriesWithNoSG}
                                {...props}
                            />
                        </div>
                    </div>
                )}
                <div className='basicInfoSeparator'>
                    <TextInput
                        inputLabel={inputLabels.email}
                        inputID={EMAIL}
                        disabled={disabledBasicStep}
                        validators={[validateFilledInput, checkMaxSize(30), checkEmail]}
                        {...props}
                    />
                </div>
                <div className='basicInfoSeparator'>
                    <PhoneInput
                        type={"Phone"}
                        inputLabel={inputLabels.mobileNumber}
                        inputID={MOBILE_NUMBER}
                        disabled={disabledBasicStep}
                        validators={[validatePhoneNumber]}
                        {...props}
                    />
                </div>
                {postMessage && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>
                )}
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch),
        dispatchInit: bindActionCreators(getMyInfo, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(BasicInfo);



