import React, {PropTypes} from "react";
import scrollIntoView from "scroll-into-view";

import SummaryBlockBasicInfo from "./blocks/SummaryBlockBasicInfo";
import SummaryBlockPersonalInfo from "./blocks/SummaryBlockPersonalInfo";
import SummaryBlockAccountDetails from "./blocks/SummaryBlockAccountDetails";
import SummaryBlockHomeAddress from "./blocks/SummaryBlockHomeAddress";
import SummaryBlockWorkInfo from "./blocks/SummaryBlockWorkInfo";
import SummaryBlockDigitalServices from "./blocks/SummaryBlockDigitalServices";
import SummaryBlockUploadDocuments from "./blocks/SummaryBlockUploadDocuments";
import SummaryBlockDeclaration from "./blocks/SummaryBlockDeclaration";
import Accordian from "../../Accordian/Accordian";
import AccordianToggleButton from "../../AccordianToggleButton/AccordianToggleButton";

import "./Summary.css";

import stepsEnum from "../../../constants/stepsEnum";

import ImmutablePropTypes from "react-immutable-proptypes";

const Summary = props => {
    const basicInfo = props.state.getIn(["ui", "basicInfo"]) && !props.state.getIn(["ui", "basicInfo"]).isEmpty();
    const accountDetails =
        props.state.getIn(["ui", "accountDetails"]) && !props.state.getIn(["ui", "accountDetails"]).isEmpty();
    const personalInfo =
        props.state.getIn(["ui", "personalInfo"]) && !props.state.getIn(["ui", "personalInfo"]).isEmpty();
    const homeMailingAddress =
        props.state.getIn(["ui", "homeMailingAddress"]) && !props.state.getIn(["ui", "homeMailingAddress"]).isEmpty();
    const workInfo = props.state.getIn(["ui", "workInfo"]) && !props.state.getIn(["ui", "workInfo"]).isEmpty();
    const digitalServices = props.state.getIn(["ui", "digitalServices"]) && !props.state.getIn(["ui", "digitalServices"]).isEmpty();
    const uploadDocuments =
        props.state.getIn(["ui", "uploadDocuments"]) && !props.state.getIn(["ui", "uploadDocuments"]).isEmpty();
    const declaration = props.state.getIn(["ui", "declaration"]) && !props.state.getIn(["ui", "declaration"]).isEmpty();
    const {FormConfig} = props;
    const summary = new FormConfig(props.step);
    const {title, subtitle, termsAndConditions} = summary;
    const [postMessage, postMessageContent] = summary.getPostMessage();
    const {mainPoint, bulletedPoint, PDPATitle, PDPAContent} = termsAndConditions;

    const onclickScrollToContinueButton = (inputID, step, value) => {
        if (!value) {
            return;
        }
        Promise.resolve().then(() => {
            const currentStep = "summary";
            const element = document.getElementById(`${stepsEnum[currentStep]}_${currentStep}`);
            scrollIntoView(element, {time: 500, align: {top: 0}}, () => {});
        });
    };
    let products = props.state.getIn(["data", "products"]);
    return (
        <div>
            <h1>{title}</h1>
            <div className='uob-form-separator' />
            <p>{subtitle}</p>

            {basicInfo ? <SummaryBlockBasicInfo {...props} /> : null}

            {accountDetails
                ? products
                      .sort((a, b) => a.get("rank") < b.get("rank"))
                      .map((product, productKey) => (
                          <div key={`accountDetails-${productKey}`} className='summary--separator'>
                              <SummaryBlockAccountDetails product={product} productKey={productKey} {...props} />
                          </div>
                      ))
                      .toList()
                : null}

            {personalInfo ? (
                <div className='summary--separator'>
                    <SummaryBlockPersonalInfo {...props} />
                </div>
            ) : null}
            {homeMailingAddress ? (
                <div className='summary--separator'>
                    <SummaryBlockHomeAddress {...props} />
                </div>
            ) : null}
            {workInfo ? (
                <div className='summary--separator'>
                    <SummaryBlockWorkInfo {...props} />
                </div>
            ) : null}
            {digitalServices ? (
                <div className='summary--separator'>
                    <SummaryBlockDigitalServices {...props} />
                </div>
            ) : null}
            {uploadDocuments ? (
                <div className='summary--separator'>
                    <SummaryBlockUploadDocuments {...props} />
                </div>
            ) : null}
            {declaration ? (
                <div className='summary--separator'>
                    <SummaryBlockDeclaration {...props} />
                </div>
            ) : null}
            {postMessage && (
                <div className='basicInfoSeparator'>
                    <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                </div>
            )}
            {bulletedPoint && (
                    <div className='basicInfoSeparator'>
                        <AccordianToggleButton
                            {...props}
                            title={mainPoint}
                            content={bulletedPoint}
                            inputID={"summarytnc"}
                            forcedOpen={true}
                            toggleTrue={false}
                            additionalOnClick={onclickScrollToContinueButton}
                        />
                    </div>
                )}
            {PDPATitle && (
                    <div className='basicInfoSeparator'>
                        <AccordianToggleButton
                            {...props}
                            title={PDPATitle}
                            content={PDPAContent}
                            inputID={"pdpa"}
                            forcedOpen={true}
                            toggleTrue={true}
                        />
                    </div>
                )}
        </div>
    );
};

Summary.propTypes = {
    FormConfig: PropTypes.func.isRequired,
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired
};

export default Summary;



