/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import "./PhoneInput.css";
import {getInputStates, isValidOrNotDirty} from "../../utils";
import {asYouType} from "libphonenumber-js";
import {readOnlyPath} from "../../constants/path";
import {onFocusInput, onBlurInput, editInput} from "../../actions/actionCreators";
import {Map} from "immutable";

const getVisibility = isValid => (isValid ? "" : "--invisible");
const getReadOnlyClass = isReadOnly => (isReadOnly ? "PhoneInputContainer--readOnly" : "");
const getFocus = (isFocus, inputValue, isValid, isDirty) =>
    isFocus || inputValue.length > 0 || !isValidOrNotDirty(isValid, isDirty) ? "--focused" : "";

const PhoneInput = props => {
    const [isFocus, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const {formData} = props;
    const focus = getFocus(isFocus, inputValue, isValid, isDirty);
    const visibility = getVisibility(isValid);
    const showErrorLabel = isValidOrNotDirty(isValid, isDirty) ? "" : "phoneInputLabel--error";
    const isReadOnly = props.state.getIn(readOnlyPath(), Map()).get(props.inputID);
    const disabled = props.disabled ? props.disabled : false;
    const readOnlyClass = getReadOnlyClass(isReadOnly);
    const errorContainerStyle = isValidOrNotDirty(isValid, isDirty) ? "" : "PhoneInputContainer--error";
    const tick = (
        <img className={`phoneInputTick${visibility}`} src={`${FORMS_CNFG_GLBL.imgPath}/Tick.svg`} alt='tick' />
    );
    const cross = <img className={"phoneInputTick"} src={`${FORMS_CNFG_GLBL.imgPath}/cross.svg`} alt='tick' />;
    const validPic = isValidOrNotDirty(isValid, isDirty) ? tick : cross;

    const formatPhoneNo = no => new asYouType().input(no);

    const country = number => {
        const formatter = new asYouType();
        formatter.input(number);
        return formatter.country;
    };
    const formatNumber = number => {
        const formatter = new asYouType();
        return formatter.input(number);
    };
    const countryFlagPath = country(inputValue)
        ? `${FORMS_CNFG_GLBL.imgPath}/countriesFlags/${country(inputValue).toLowerCase()}.svg`
        : "";
    const validator = (value, validators) => validators.map(x => x(formData)).map(x => x(value)).filter(x => !x.status);
    const handleOnChange = e => {
        const value = e.target.value === "" ? "+" : formatPhoneNo(e.target.value);
        props.dispatchEditInput(props.inputID, value, validator(value, props.validators), props.step);
    };

    return (
        <div id={props.inputID}>
            <div className={`PhoneInputContainer ${readOnlyClass} ${errorContainerStyle}`}>
                {countryFlagPath && <img className={"phoneInputFlag"} src={countryFlagPath} alt='flag' />}
                <label className={`phoneInputLabel${focus} ${showErrorLabel}`}>
                    {`${props.inputLabel}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                </label>
                <input
                    onFocus={() => {
                        if (inputValue === "") {
                            props.dispatchEditInput(
                                props.inputID,
                                "+65",
                                validator("+65", props.validators),
                                props.step
                            );
                        }
                        props.dispatchOnFocusInput(props.step, props.inputID, true);
                    }}
                    onBlur={() => {
                        props.dispatchOnBlurInput(props.step, props.inputID, false);
                    }}
                    disabled={isReadOnly || disabled}
                    value={formatNumber(inputValue)}
                    onChange={handleOnChange}
                    type='text'
                    className={`phoneInput${focus}`}
                />
                {isReadOnly
                    ? <img
                          className='phoneInputTick--verified'
                          src={`${FORMS_CNFG_GLBL.imgPath}/non-editable-icon4.svg`}
                          alt='dropdown-arrow'
                      />
                    : validPic}
            </div>
            {formData.formSteps.basicInfo.postInputsMessage.MobileNumberMessage.default.title &&
                <div className='PhoneInputMessageContent'>
                    {formData.formSteps.basicInfo.postInputsMessage.MobileNumberMessage.default.title}
                </div>}
        </div>
    );
};

PhoneInput.propTypes = {
    state: ImmutablePropTypes.map,
    inputLabel: PropTypes.string,
    inputID: PropTypes.string,
    step: PropTypes.string.isRequired,
    value: PropTypes.string,
    dispatchOnBlurInput: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    disabled: PropTypes.bool
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchEditInput: bindActionCreators(editInput, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(PhoneInput);



