import React from "react";

import "./CrossSelling.css";

import CrossSellingDesktop from "./CrossSellingDesktop/CrossSellingDesktop";
import CrossSellingMobile from "./CrossSellingMobile/CrossSellingMobile";

const CrossSelling = props => {
    return (
        <div>
            <div className='crossSelling-desktop'><CrossSellingDesktop {...props} /></div>
            <div className='crossSelling-mobile'><CrossSellingMobile {...props} /></div>
        </div>
    );
};

export default CrossSelling;



