import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {List} from "immutable";
import {addValidFlag, onCheck} from "../../../actions/actionCreators";

import {uiStepPath, productIdPath} from "../../../constants/path";
import FileUpload from "../../FileUpload/FileUpload";
import FileUploadDefault from "../../FileUploadDefault/FileUploadDefault";
import Accordian from "../../Accordian/Accordian";
import ToggleInput from "../../ToggleInput/ToggleInput";
import TextInput from "../../TextInput/TextInput";

import "./UploadDocuments.css";
import {
    UPLOAD_BILLING_PROOF,
    UPLOAD_NRIC_FRONT,
    UPLOAD_NRIC_BACK,
    UPLOAD_PASSPORT,
    UPLOAD_PASSPORT_BACK,
    UPLOAD_INCOME_PROOF,
    UPLOAD_WORK_PERMIT,
    SUBMIT_CPF_ONLINE,
    NRIC,
    NATURE_OF_EMPLOYMENT,
    IS_SG,
    NRIC_ISSUE_DATE,
    NATIONALITY,
    SUBMIT_PR_1_TO_2_YEAR,
    ANNUAL_INCOME,
    SAME_HOME_AND_MAILING_ADD,
    DATE_OF_BIRTH,
    UPLOAD_EMPLOYMENT_PROOF
} from "../../../constants/inputsConstant";

import {
    sellingCards,
    sellinPersonalLoan,
    sellingCashPlus,
    sellinBalanceTransfer
} from "../../../utils";
import {dataPath, myInfoFlagPath, cpfDataPath} from "../../../constants/path";

import {
    validateFilledInput,
    checkDateOfBirthFormat,
    checkDateNotOverTodayDate,
    checkDateFormat,
    compareWithDate
} from "../../../validations/inputValidation";

const UploadIdDocs = props => {
    const isSG = props.state.getIn(dataPath([IS_SG]), true);
    return isSG ? (
        <div className='upload-document--info-separator'>
            <div className='upload-document--info-separator'>
                <FileUpload
                    title={props.inputLabels[UPLOAD_NRIC_FRONT]}
                    description={props.inputDescriptions[UPLOAD_NRIC_FRONT]}
                    inputLabel='Upload a file'
                    inputID={UPLOAD_NRIC_FRONT}
                    {...props}
                />
                <div />
                <FileUpload
                    title={props.inputLabels[UPLOAD_NRIC_BACK]}
                    description={props.inputDescriptions[UPLOAD_NRIC_BACK]}
                    inputLabel='Upload a file'
                    inputID={UPLOAD_NRIC_BACK}
                    {...props}
                />
            </div>
        </div>
    ) : (
        <div className='upload-document--info-separator'>
            <FileUpload
                title={props.inputLabels[UPLOAD_PASSPORT]}
                description={props.inputDescriptions[UPLOAD_PASSPORT]}
                inputLabel='Upload a file'
                inputID={UPLOAD_PASSPORT}
                {...props}
            />
            <FileUpload
                title={props.inputLabels[UPLOAD_WORK_PERMIT]}
                description={props.inputDescriptions[UPLOAD_WORK_PERMIT]}
                inputLabel='Upload a file'
                inputID={UPLOAD_WORK_PERMIT}
                {...props}
            />
        </div>
    );
};
class UploadDocuments extends Component {
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const productIds = props.state.getIn(productIdPath());
        const structure = props.state.getIn(uiStepPath(props.step));
        const uploadDocuments = new FormConfig(props.step);
        const {inputLabels, title, subtitle, inputDescriptions} = uploadDocuments;
        const [postMessage, postMessageContent] = uploadDocuments.getPostMessage();
        const allowCPFOnline = props.state.getIn(dataPath([SUBMIT_CPF_ONLINE]), false);
        const IC = props.state.getIn(dataPath([NRIC]));
        const isMyInfo = props.state.getIn(myInfoFlagPath(), false);
        const haveCPF = props.state.getIn(cpfDataPath(), false);
        const employmentStatus = props.state.getIn(dataPath([NATURE_OF_EMPLOYMENT]));
        const isOtherMail = props.state.getIn(["data", SAME_HOME_AND_MAILING_ADD], false);
        const showUploadCpf =
            (haveCPF && employmentStatus === "SELFE") ||
              (!haveCPF && (sellingCards(productIds, productsConfig) ||
                              sellingCashPlus(productIds, productsConfig) ||
                              sellinBalanceTransfer(productIds, productsConfig) ||
                              sellinPersonalLoan(productIds, productsConfig)));
        const isSG = props.state.getIn(dataPath([IS_SG]), true);
        const isPR = isSG && props.state.getIn(dataPath([NATIONALITY])) !== "SG";
        const residentialStatus = props.state.getIn(dataPath(["residentialStatus"]));
        const isShowUploadNRICBack = props.state.getIn(["data", SUBMIT_PR_1_TO_2_YEAR], false);
        const cpfData = props.state.getIn(cpfDataPath(), List());
        const anualIncome = Number(props.state.getIn(dataPath([ANNUAL_INCOME]), ""));
        const isShowUploadIncome = anualIncome === 0 && (cpfData.count() === 0 || Number(cpfData.first().get("amount")) === 0) ? true : false;
        const dateOfBirth = props.state.getIn(dataPath([DATE_OF_BIRTH]));
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>

                {!isMyInfo && isSG && (
                  <div className='upload-document--info-separator'>
                      <FileUpload
                          title={inputLabels[UPLOAD_NRIC_FRONT]}
                          description={inputDescriptions[UPLOAD_NRIC_FRONT]}
                          inputLabel='Upload a file'
                          inputID={UPLOAD_NRIC_FRONT}
                          IC={IC}
                          {...props}
                      />
                  </div>
                )}

                {!isMyInfo && isSG && (
                  <div className='upload-document--info-separator'>
                      <FileUpload
                          title={inputLabels[UPLOAD_NRIC_BACK]}
                          description={inputDescriptions[UPLOAD_NRIC_BACK]}
                          inputLabel='Upload a file'
                          inputID={UPLOAD_NRIC_BACK}
                          IC={IC}
                          {...props}
                      />
                  </div>
                )}
                {!isMyInfo && isPR && (
                  <div className='upload-document--info-separator'>
                    <TextInput
                        inputLabel={inputLabels[NRIC_ISSUE_DATE]}
                        inputID={NRIC_ISSUE_DATE}
                        validators={[validateFilledInput, checkDateOfBirthFormat, checkDateFormat, compareWithDate(dateOfBirth, "birthday")]}
                        {...props}
                        dateFormat
                    />
                  </div>
                )}
                {!isMyInfo && !isSG && (
                  <div>
                    <div className='upload-document--info-separator'>
                        <FileUpload
                            title={inputLabels[UPLOAD_PASSPORT]}
                            description={inputDescriptions[UPLOAD_PASSPORT]}
                            inputLabel='Upload a file'
                            inputID={UPLOAD_PASSPORT}
                            IC={IC}
                            {...props}
                        />
                    </div>
                    <div className='upload-document--info-separator'>
                        <FileUpload
                            title={inputLabels[UPLOAD_PASSPORT_BACK]}
                            description={inputDescriptions[UPLOAD_PASSPORT_BACK]}
                            inputLabel='Upload a file'
                            inputID={UPLOAD_PASSPORT_BACK}
                            IC={IC}
                            {...props}
                        />
                    </div>
                  </div>
                )}

                {(showUploadCpf || isOtherMail) && (
                    <div className='upload-document--info-separator upload-document--components-merger'>
                        <div>
                            {isOtherMail || structure.has(UPLOAD_BILLING_PROOF) || !isSG ? (
                                <FileUpload
                                    title={inputLabels[UPLOAD_BILLING_PROOF]}
                                    description={inputDescriptions[UPLOAD_BILLING_PROOF]}
                                    inputLabel='Upload a file'
                                    inputID={UPLOAD_BILLING_PROOF}
                                    {...props}
                                />
                            ) : null}
                        </div>
                    </div>
                )}
                {(showUploadCpf || isShowUploadIncome) && (
                    <div className='upload-document--info-separator upload-document--components-merger'>
                        <div>
                            {!isMyInfo &&
                                isSG && (
                                    <ToggleInput
                                        description={inputLabels[SUBMIT_CPF_ONLINE]}
                                        inputID={SUBMIT_CPF_ONLINE}
                                        {...props}
                                    />
                                )}
                            {!allowCPFOnline && (
                                <FileUpload
                                    title={inputLabels[UPLOAD_INCOME_PROOF]}
                                    description={
                                        isSG
                                            ? inputDescriptions[UPLOAD_INCOME_PROOF]
                                            : inputDescriptions["uploadIncomeProofForeigner"]
                                    }
                                    inputLabel='Upload a file'
                                    inputID={UPLOAD_INCOME_PROOF}
                                    {...props}
                                />
                            )}
                        </div>
                    </div>
                )}
                {
                    isMyInfo && isSG && (isShowUploadIncome) &&
                    <div className='upload-document--info-separator'>
                        <FileUpload
                            title={inputLabels[UPLOAD_EMPLOYMENT_PROOF]}
                            description={inputDescriptions[UPLOAD_EMPLOYMENT_PROOF]}
                            inputLabel='Upload a file'
                            inputID={UPLOAD_EMPLOYMENT_PROOF}
                            {...props}
                        />
                    </div>
                }
                {!isMyInfo && !isSG && (
                  <div className='upload-document--info-separator'>
                    <FileUpload
                        title={inputLabels[UPLOAD_WORK_PERMIT]}
                        description={inputDescriptions[UPLOAD_WORK_PERMIT]}
                        inputLabel='Upload a file'
                        inputID={UPLOAD_WORK_PERMIT}
                        IC={IC}
                        {...props}
                    />
                  </div>
                )}

                 {
                    residentialStatus === "P" &&  // P is Singapore Permanant
                    <div>
                        {
                            <div className='upload-document--info-separator'>
                              <ToggleInput
                                description={inputLabels[SUBMIT_PR_1_TO_2_YEAR]}
                                inputID={SUBMIT_PR_1_TO_2_YEAR}
                                {...props}
                             />
                           </div>
                        }
                        {
                            isShowUploadNRICBack &&
                                <div>
                                    <div className='upload-document--info-separator'>
                                        <FileUploadDefault
                                            title={inputLabels[UPLOAD_NRIC_BACK]}
                                            description={inputDescriptions[UPLOAD_NRIC_BACK]}
                                            inputLabel='Upload a file'
                                            inputID={UPLOAD_NRIC_BACK}
                                            IC={IC}
                                            {...props}
                                        />
                                    </div>
                                    <div className='upload-document--info-separator'>
                                        <TextInput
                                            inputLabel={inputLabels[NRIC_ISSUE_DATE]}
                                            inputID={NRIC_ISSUE_DATE}
                                            validators={[validateFilledInput, checkDateOfBirthFormat, checkDateFormat, checkDateNotOverTodayDate, compareWithDate(dateOfBirth, "birthday")]}
                                            {...props}
                                            dateFormat
                                        />
                                    </div>
                                </div>
                        }
                    </div>
                }

                {postMessage && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>
                )}
            </div>
        );
    }
}

UploadIdDocs.propTypes = {
    IC: PropTypes.string.isRequired,
    inputLabels: PropTypes.object.isRequired,
    inputDescriptions: PropTypes.object.isRequired,
    state: ImmutablePropTypes.map.isRequired
};

UploadDocuments.propTypes = {};

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch),
        dispatchOnCheck: bindActionCreators(onCheck, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(UploadDocuments);



