export const editInput = ({inputId, value}) => state => {
    if (inputId === "propertyType" && state.getIn(currentStepPath()) === "homeMailingAddress") {
        if (["H3", "H5", "EC"].includes(value)) {
            const uiDefaultState = Map({
                isFocus: false,
                isDirty: false,
                isValid: false,
                errorMsg: ""
            });
            const currentState = state.getIn(["ui", "homeMailingAddress", HOME_LEVEL], Map());
            state.setIn(["ui", "homeMailingAddress", HOME_LEVEL], uiDefaultState.merge(currentState));
            const currentState2 = state.getIn(["ui", "homeMailingAddress", HOME_UNIT_NUMBER], Map());
            state.setIn(["ui", "homeMailingAddress", HOME_UNIT_NUMBER], uiDefaultState.merge(currentState2));
        } else {
            state.setIn(["ui", "homeMailingAddress"], state.getIn(["ui", "homeMailingAddress"]).remove(HOME_LEVEL));
            state.setIn(["ui", "homeMailingAddress"], state.getIn(["ui", "homeMailingAddress"]).remove(HOME_UNIT_NUMBER));
        }
    }
    return state.setIn(dataPath([inputId]), value);
};

