export const updateCurrencies = (currency, pid) => state => {
    const currencies = state.getIn(["data", "products", pid, "currencies"], Map());
    const isSameToggle = state.getIn(["data", "samePurposeToggle"], true);
    const initValue = isSameToggle
        ? currencies.first()
        : Map({
            purposeOfFunds: "",
            sourceOfFunds: "",
            countryOfFunds: "SG"
        });

    if (currencies.has(currency)) {
        if (currencies.size >= 2) {
            const uiState = state.getIn(["ui", "accountDetails"]);
            const newUIState = uiState.filter((acc, value) => {
                if (!value) {
                    return true;
                }
                return !(value.includes(currency) && value.includes("CURFX"));
            });
            state.setIn(["data", "products", pid, "currencies"], currencies.remove(currency));
            return state.setIn(["ui", "accountDetails"], newUIState);
        } else {
            return state;
        }
    } else {
        return (
            state.setIn(["data", "products", pid, "currencies", currency], initValue) &&
            state.setIn(["ui", "accountDetails", `${pid}-${PURPOSE_OF_FUNDS}-${currency}`, "isDirty"], isSameToggle) &&
            state.setIn(["ui", "accountDetails", `${pid}-${PURPOSE_OF_FUNDS}-${currency}`, "isValid"], isSameToggle) &&
            state.setIn(["ui", "accountDetails", `${pid}-${SOURCE_OF_FUNDS}-${currency}`, "isDirty"], isSameToggle) &&
            state.setIn(["ui", "accountDetails", `${pid}-${SOURCE_OF_FUNDS}-${currency}`, "isValid"], isSameToggle) &&
            state.setIn(["ui", "accountDetails", `${pid}-${COUNTRY_OF_FUNDS}-${currency}`, "isDirty"], true) &&
            state.setIn(["ui", "accountDetails", `${pid}-${COUNTRY_OF_FUNDS}-${currency}`, "isValid"], true)
        );
    }
};

