export const pushProductID = action => state => {
    const currentPIDSelected = state.getIn(productIdPath(), List());
    const newPIDSelected = action.data.productId.reduce(
        (pids, pid) => (!pids.includes(pid) ? currentPIDSelected.push(pid) : pids),
        currentPIDSelected
    );
    return state.setIn(productIdPath(), newPIDSelected);
};

export const updateUIStateTree = () => state => {
    //TO-DO write logic to handle >1 product
    const productsData = state.getIn(productsDataPath(), Map());
    const currentPIDSelected = state.getIn(productIdPath(), List());
    const appFlow = state.getIn(flowPath());
    const stepToShowForETP = ["basicInfo", "pendingApplication", "accountDetails", "summary"];
    const mapProductConfig = flow => pid => {
        const productType = productsData.getIn(["products", pid, "type"]);
        return flow === "ETP"
            ? productConfig[productType].filter((_, step) => stepToShowForETP.includes(step))
            : productConfig[productType];
    };

    const appStructure = currentPIDSelected.map(mapProductConfig(appFlow));
    const uiDefaultState = Map({
        isFocus: false,
        isDirty: false,
        isValid: false,
        errorMsg: ""
    });
    const newUIStateTree = appStructure.first().mapEntries(([key, value]) => {
        const currentStepState = state.getIn(uiStepPath(key));
        const newValue = value.reduce((r, f) => {
            if (currentStepState) {
                return r.set(f, currentStepState.get(f));
            }
            return f ? r.set(f, uiDefaultState) : r.set(f, Map({isValid: true}));
        }, OrderedMap());
        return [key, newValue];
    });
    return state.setIn(ui, newUIStateTree);
};

