export const updateUIStateForLevelAndUnit = ({data, inputId}) => state => {
    if (data.addressFormat && data.addressFormat[0] === "H") {
        const currentAddressStepState = state.getIn(uiStepPath("homeMailingAddress"), Map());
        const prependText = inputId.includes(HOME) ? HOME : MAILING;
        const levelFormat = `${prependText}Level`;
        const unitFormat = `${prependText}UnitNumber`;
        const defaultState = {
            isValid: false,
            isDirty: false
        };
        const levelAndUnitState = {
            [levelFormat]: defaultState,
            [unitFormat]: defaultState
        };
        return state.setIn(uiStepPath("homeMailingAddress"), currentAddressStepState.merge(fromJS(levelAndUnitState)));
    }
    return state;
};

