export const addressError = action => state => {
    const postalErrors = {
        "400": () => {
            const newState1 = state.setIn(uiErrorMsgPath("homeMailingAddress", action.inputId), state.getIn(["formLabels", "formInputErrorMessages", "invalidAddress"]));
            const newState2 = newState1.setIn(uiIsValidPath("homeMailingAddress", action.inputId), false);
            return newState2;
        },
        "500": () => state.setIn(postalCodeServiceStatusPath(), true).setIn(genericErrorPath(), state.getIn(["formLabels", "formInputErrorMessages", "adressServiceNotAvailable"])),
        DEFAULT: () => state.setIn(postalCodeServiceStatusPath(), true)
    };
    return postalErrors[action.error] ? postalErrors[action.error]() : postalErrors.DEFAULT();
};

