export const updatePartialApplicationSubmitStatus = ({data}) => state => {
    const objectKeys = Object.keys(data.data);
    const removedNullKeys = objectKeys.reduce((newObject, k) => {
        if (data.data[k] !== null) {
            newObject[k] = data.data[k];
        }
        return newObject;
    }, {});
    const mergeData = Object.assign({}, formData, removedNullKeys);
    return state.setIn(dataPath([]), fromJS(mergeData));
};

