export const mapDataToStore = ({data}) => state => {
    if (data.form) {
        const objectKeys = Object.keys(data.form);
        const removedNullKeys = objectKeys.reduce((newObject, k) => {
            if (data.form[k]) {
                newObject[k] = data.form[k];
            }
            return newObject;
        }, {});

        if (removedNullKeys.mobileNumber && removedNullKeys.mobileNumber[0] === "+") {
            removedNullKeys.mobileNumber = removedNullKeys.mobileNumber;
        } else if (removedNullKeys.mobileNumber && removedNullKeys.mobileNumber.length > 1) {
            removedNullKeys.mobileNumber = "+" + removedNullKeys.mobileNumber;
        } else {
            removedNullKeys.mobileNumber = "+65";
        }

        const mergeData = Object.assign({}, formData, removedNullKeys);
        return state.setIn(dataPath([]), fromJS(mergeData));
    }
    return state.setIn(dataPath([]), fromJS(formData));
};

