export const updatePurposeOfFundsForAll = value => state => {
    const products = state.getIn(["data", "products"]);

    products.map((product, productKey) => {
        const currencies = product.get("currencies");

        if (currencies) {
            currencies.map((currency, currencyKey) => {
                state.setIn(["data", "products", productKey, "currencies", currencyKey, "purposeOfFunds"], value);
                state.setIn(
                    ["ui", "accountDetails", `${productKey}-${PURPOSE_OF_FUNDS}-${currencyKey}`, "isDirty"],
                    true
                );
                state.setIn(
                    ["ui", "accountDetails", `${productKey}-${PURPOSE_OF_FUNDS}-${currencyKey}`, "isValid"],
                    true
                );
                return state;
            });
        }
        return products;
    });
    return state;
};

