export const matchErrorsMsgToInputs = action => state => {
    const error = fromJS(action.error);
    const stepUi = state.getIn(uiStepPath(action.currentStep));
    const filterError = error.map(input =>
        input
            .filter((v, k) => k === "errorMsg")
            .set("isValid", false)
            .set("isDirty", true)
    );
    const newUi = stepUi.mergeDeep(filterError);
    return state.setIn(uiStepPath(action.currentStep), newUi);
};

export const setDefaultFieldsToValid = () => state => {
    const newState = state.setIn(uiIsDirtyPath("personalInfo", COUNTRY_OF_RESIDENCE), true);
    return !newState.getIn(dataPath([EDUCATION_LEVEL]),"") ? newState : state.setIn(uiIsValidPath("personalInfo", EDUCATION_LEVEL),true);
};

export const setFieldsToValid = () => state => {
    const inputsToSetValid = state
        .getIn(dataPath([]))
        .filter(v => v)
        .keySeq();
    const validState = Map({isValid: true});
    const uiCurrentState = state.getIn(ui, Map());
    const filteredKeys = uiCurrentState.filter(
        step => step.filter((inputKeys, input) => inputsToSetValid.includes(input)).count() > 0
    );
    const updateFilteredKey = filteredKeys.map(filteredStep =>
        filteredStep.map(
            (inputKeys, input) => {
                if (!inputKeys) {
                    return inputKeys;
                }
                return (inputsToSetValid.includes(input) ? inputKeys.merge(validState) : inputKeys);
            }
        )
    );
    const updatedState = uiCurrentState.merge(updateFilteredKey);
    return state.setIn(ui, updatedState);
};

export const setFieldsToDirty = () => state => {
    const inputsToSetValid = state
        .getIn(dataPath([]))
        .filter(v => v)
        .keySeq();
    const validState = Map({isDirty: true});
    const uiCurrentState = state.getIn(ui, Map());
    const filteredKeys = uiCurrentState.filter(
        step => step.filter((inputKeys, input) => inputsToSetValid.includes(input)).count() > 0
    );
    const updateFilteredKey = filteredKeys.map(filteredStep =>
        filteredStep.map(
            (inputKeys, input) => (inputsToSetValid.includes(input) ? inputKeys.merge(validState) : inputKeys)
        )
    );
    const updatedState = uiCurrentState.merge(updateFilteredKey);
    return state.setIn(ui, updatedState);
};

