export const editNameOnCard = (productIds, cardType, value, status) => state => {
    const mutation = productIds.reduce((acc, productCode) => {
        return acc.setIn(["data", "products", productCode, "nameOnCard"], value);
    }, state);

    mutation.setIn(["ui", "accountDetails", `${NAME_ON_CARD}_${cardType}`, "value"], value);
    mutation.setIn(["ui", "accountDetails", `${NAME_ON_CARD}_${cardType}`, "isValid"], status.length === 0);
    mutation.setIn(["ui", "accountDetails", `${NAME_ON_CARD}_${cardType}`, "isDirty"], true);
    if (status.length !== 0) {
        mutation.setIn(["ui", "accountDetails", `${NAME_ON_CARD}_${cardType}`, "errorMsg"], status[0].errorMsg);
    }

    return mutation;
};

