export const initAccountDetailsWithCasa = ({productCurrencies, defaultValues}) => state => {
    productCurrencies.map((currenciesList, productId) => {
        const currencies = currenciesList.reduce((acc, currency) => acc.set(currency, defaultValues), Map());
        const sameToggle = state.getIn(["data", "samePurposeToggle"], true);

        state.setIn(["data", "products", productId, "currencies"], currencies);

        currencies.map((currency, currencyKey) => {
            const purpose = defaultValues.get("purposeOfFunds", "") === "" ? false : true;
            const source = defaultValues.get("sourceOfFunds", "") === "" ? false : true;
            const country = defaultValues.get("countryOfFunds", "") === "" ? false : true;

            if (!sameToggle) {
                state.setIn(
                    ["ui", "accountDetails", `${productId}-${PURPOSE_OF_FUNDS}-${currencyKey}`, "isValid"],
                    purpose
                );
                state.setIn(
                    ["ui", "accountDetails", `${productId}-${PURPOSE_OF_FUNDS}-${currencyKey}`, "isDirty"],
                    false
                );
                state.setIn(
                    ["ui", "accountDetails", `${productId}-${SOURCE_OF_FUNDS}-${currencyKey}`, "isValid"],
                    source
                );
                state.setIn(
                    ["ui", "accountDetails", `${productId}-${SOURCE_OF_FUNDS}-${currencyKey}`, "isDirty"],
                    false
                );
            }
            state.setIn(["ui", "accountDetails", `${productId}-${COUNTRY_OF_FUNDS}-${currencyKey}`, "isValid"], country);
            state.setIn(["ui", "accountDetails", `${productId}-${COUNTRY_OF_FUNDS}-${currencyKey}`, "isDirty"], true);

            return state;
        });
        return currencies;
    });
    return state;
};

