export const addStepToState = () => state => {
    // if (action.currentStep === "summary" && state.getIn(isFinalSubmitPath())) {
    //     console.log(state);
    //     return state;
    // }
    // console.log("running");
    const currentShownSteps = state.getIn(shownStepsPath(), List());
    const uiState = state.getIn(ui, Map());
    const currentStep = state.getIn(currentStepPath());
    const stepSupposedToShow = uiState
        .filter(x => x.count())
        .map((value, step) => step)
        .toList();
    const currentStepIndex = stepSupposedToShow.indexOf(currentStep);
    const newShownSteps = stepSupposedToShow.filter((step, i) => i <= currentStepIndex);
    const mergeShownSteps =
        currentShownSteps.count() < newShownSteps.count() ? currentShownSteps.merge(newShownSteps) : currentShownSteps;
    return state.setIn(shownStepsPath(), mergeShownSteps);
};

export const removeStepFromState = () => state => {
    const currentShownSteps = state.getIn(shownStepsPath(), List());
    const uiState = state.getIn(ui, Map());
    const stepToRemove = uiState
        .filter(x => !x.count())
        .map((value, step) => step)
        .toList();
    const newShownSteps = currentShownSteps.filter(step => !stepToRemove.includes(step));
    return state.setIn(shownStepsPath(), newShownSteps);
};

export const updateCurrentStep = () => state => {
    const currentShownSteps = state.getIn(shownStepsPath(), List());
    const lastStep = currentShownSteps.last();
    return state.setIn(currentStepPath(), lastStep);
};

