export const generateProductCachedDataAndValidations = productsData => state => {
    const products = state.getIn(dataPath(["products"]), Map());
    const productCachedDataAndValidations = products.reduce((uiMap, productProp, pid) => {
        const familyType = productsData.products[pid].family;
        const defaultValues = Map({
            isValid: true,
            isDirty: true
        });
        const productData = {
            [`nameOntheCard_${familyType}`]: defaultValues.merge(Map({value: productProp.get("nameOnCard")})),
            [`cardLimit_${familyType}`]: defaultValues.merge(Map({value: productProp.get("dailyCardLimit")})),
            [`creditLimit_${familyType}`]: defaultValues.merge(Map({value: productProp.get("creditLimit")})),
            [`activateAtms_${familyType}`]: defaultValues.merge(Map({value: productProp.get("atmAccess")}))
        };
        if (productProp.get("krisflyerMembershipNumber")) {
            productData[`${pid}-krisflyerMembershipNumber`] = defaultValues;
        }
        if (productProp.get("krisflyerMembershipFName")) {
            productData[`${pid}-krisflyerMembershipFName`] = defaultValues;
        }
        if (productProp.get("krisflyerMembershipLName")) {
            productData[`${pid}-krisflyerMembershipLName`] = defaultValues;
        }
        if (productProp.get("singtelMembershipNumber")) {
            productData["singtelMembershipNumber"] = defaultValues;
        }

        return uiMap.merge(Map(productData));
    }, Map());
    return state.mergeIn(uiStepPath("accountDetails"), productCachedDataAndValidations);
};

