export const generateProductCurrencyValidations = () => state => {
    const products = state.getIn(dataPath(["products"]));
    const defaultValues = Map({
        isValid: true,
        isDirty: true
    });
    const productCurrenciesValidations = products.reduce((uiMap, productProp, pid) => {
        if (!productProp.has("currencies")) {
            return uiMap;
        }

        const currentUiMap = productProp.get("currencies").reduce((currencyValidation, currencyProp, currency) => {
            return Map({
                [`${pid}-purposeOfFunds-${currency}`]: defaultValues,
                [`${pid}-sourceOfFunds-${currency}`]: defaultValues,
                [`${pid}-countryOfFunds-${currency}`]: defaultValues
            });
        }, Map());
        return uiMap.merge(currentUiMap);
    }, Map());
    return state.mergeIn(uiStepPath("accountDetails"), productCurrenciesValidations);
};

export const addMandatoryProductIdToStore = productsData => state => {
    const currentProductIds = state.getIn(productIdPath(), List());
    const firstProductId = currentProductIds.first();
    const productCrossSellItems = productsData.crossSellReference[firstProductId];
    if (productsData.crossSellReference[firstProductId]) {
        const mandatoryProducts = productCrossSellItems.filter(product => product.mandatory);
        const mandatoryProductIds = mandatoryProducts.map(product => product.pid);
        const newProductIds = currentProductIds.concat(mandatoryProductIds);
        return state.setIn(productIdPath(), newProductIds);
    }
    return state;
};

