export const prefillEmployer = action => state => {

    if (
        action.type === "EDIT_INPUT" &&
        action.inputId === "natureOfEmployment" &&
        (action.value === "salaried" || action.value === "SELFE")
    ) {
        const isReadOnly = state.getIn(isReadOnlyPath("workInfo", "nameOfEmployer"), false);
        const employer = state.getIn(["app", "cpf"], List()).first();
        return !isReadOnly && employer
            ? state
                  .setIn(["data", "nameOfEmployer"], employer.get("employer", ""))
                  .setIn(["ui", "workInfo", "nameOfEmployer", "isValid"], true)
            : state;
    }
    return state;
};

