import React, {Component} from "react";
import {List, OrderedMap, Map, fromJS} from "immutable";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import ProductBlock from "../../ProductBlock/ProductBlock";

import {productIdPath} from "../../../constants/path";

import "./AccountDetails.css";

import {
    addValidFlag,
    updateCurrencies,
    updateCountryOfFunds,
    onToggleSamePurpose,
    initAccountDetailsWithCasa,
    initAccountDetailsWithCards,
    getTenorTableData,
    getBankList
} from "../../../actions/actionCreators";

import {sellingCards, sellingCasa} from "../../../utils";
import Accordian from "../../Accordian/Accordian";

const sortProductIds = (productIds, typeOfProduct) =>
    productIds.sort((a, b) => typeOfProduct.getIn([a, "rank"], 0) < typeOfProduct.getIn([b, "rank"], 0));

const defineProductFamilies = (productIds, familyOfProduct) => {
    return sortProductIds(productIds, familyOfProduct).reduce((reduction, value) => {
        const family = familyOfProduct.getIn([value, "pid"]);
        const currentTypes = reduction.getIn([family], List());

        return reduction.setIn([family], currentTypes.push(value));
    }, OrderedMap());
};

class AccountDetails extends Component {
    componentDidMount() {
        const props = this.props;
        const {productsConfig} = props;
        const typeOfProduct = fromJS(productsConfig.products);
        const products = props.state.getIn(["app", "productId"]);
        props.dispatch(getBankList());
        products.map(pid => {
            const productType = typeOfProduct.getIn([pid, "type"]);
            if (productType === "personalLoan" || productType === "balanceTransfer") {
                if (productType === "personalLoan") {
                    props.dispatch(getTenorTableData(pid, "PL"));
                } else {
                    props.dispatch(getTenorTableData(pid, "BT"));
                }
            }
            if (!props.state.hasIn(["data", "products", pid])) {
                const productCurrencies = Map({
                    [pid]: typeOfProduct.getIn([pid, "currencies", "values"])
                });
                const familyType = typeOfProduct.getIn([pid, "pid"]);
                const name = props.state.getIn(["data", "fullName"]);
                const preferedDailyLimit = "2000";
                const activateAtms = typeOfProduct.getIn([pid, "atmActivited"], true);
                const defaultValues = Map({purposeOfFunds: "", sourceOfFunds: "", countryOfFunds: "SG"});

                if (productType === "CASA") {
                    props.dispatch(
                        initAccountDetailsWithCasa(
                            pid,
                            productCurrencies,
                            familyType,
                            {
                                name: name
                                    .toUpperCase()
                                    .substr(0, 19)
                                    .replace(/[^a-zA-Z ]/g, ""),
                                preferedDailyLimit,
                                activateAtms
                            },
                            defaultValues
                        )
                    );
                } else {
                    props.dispatch(
                        initAccountDetailsWithCards(
                            pid,
                            familyType,
                            name
                                .toUpperCase()
                                .substr(0, 19)
                                .replace(/[^a-zA-Z ]/g, ""),
                            preferedDailyLimit,
                            activateAtms
                        )
                    );
                }
            }
            return products;
        });
    }
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const typeOfProduct = fromJS(productsConfig.products);
        const familyOfProduct = fromJS(productsConfig.products);
        const accountDetails = new FormConfig(props.step);
        const {title, subtitle} = accountDetails;
        const [casaPostMessage, casaPostMessageContent] = accountDetails.getPostMessage("CASA");
        const [cardPostMessage, cardPostMessageContent] = accountDetails.getPostMessage("Card");
        const productIds = props.state.getIn(productIdPath());
        const firstProduct = productIds.first();
        const crossSellProductIds = productsConfig.crossSellReference[firstProduct];
        const crossSellProducts = crossSellProductIds
            ? crossSellProductIds.map(product => productsConfig.products[product.pid])
            : [];
        const productFamilies = defineProductFamilies(productIds, familyOfProduct);
        const productsAdditionalMsg = productIds
            .filter(pid => typeOfProduct.getIn([pid, "additionalMsg"], "") !== "")
            .map((pid, i) => (
                <div key={i}>
                    {typeOfProduct.getIn([pid, "additionalMsg"])}
                    <br />
                </div>
            ));
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                {productFamilies
                    .map((products, familyType) => {
                        const pid = products.first();
                        const productType = typeOfProduct.getIn([pid, "type"]);
                        return (
                            <ProductBlock
                                familyType={familyType}
                                productType={productType}
                                products={products}
                                key={`productBlock-${familyType}`}
                                doesHaveCrossSell={crossSellProducts.length > 0}
                                {...props}
                            />
                        );
                    })
                    .toList()}
                {productsAdditionalMsg.count() > 0 && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={productsAdditionalMsg} inputID={"extraMsg"} />
                    </div>
                )}
                {casaPostMessage &&
                    sellingCasa(productIds, productsConfig) && (
                        <div className='basicInfoSeparator--negative-margin'>
                            <Accordian
                                {...props}
                                title={casaPostMessage}
                                content={casaPostMessageContent}
                                inputID={"casa"}
                            />
                        </div>
                    )}
                {cardPostMessage &&
                    sellingCards(productIds, productsConfig) && (
                        <div className='basicInfoSeparator--negative-margin'>
                            <Accordian
                                {...props}
                                title={cardPostMessage}
                                content={cardPostMessageContent}
                                inputID={"card"}
                            />
                        </div>
                    )}
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch),
        dispatchUpdateCurrencies: bindActionCreators(updateCurrencies, dispatch),
        dispatchUpdateCountryOfFunds: bindActionCreators(updateCountryOfFunds, dispatch),
        dispatchOnToggleSamePurpose: bindActionCreators(onToggleSamePurpose, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(AccountDetails);



