import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {addValidFlag} from "../../../actions/actionCreators";

import ToggleInput from "../../ToggleInput/ToggleInput";
import LocalAddressInput from "../../LocalAddressInput/LocalAddressInput";
import OverseasAddressInput from "../../OverseasAddressInput/OverseasAddressInput";
import DropdownWithFlag from "../../DropdownWithFlag/DropdownWithFlag";
import Dropdown from "../../Dropdown/Dropdown";
import TextInput from "../../TextInput/TextInput";
import Accordian from "../../Accordian/Accordian";

import {dataPath, productIdPath, uiInputPath} from "../../../constants/path";
import {onToggle, editInput, setPropertyType, removeInputToUIState} from "../../../actions/actionCreators";
import {countriesLabelValue} from "../../../constants/countriesMaps";
import {validateFilledInput, checkNumbers, checkMaxMonths, checkMaxSize} from "../../../validations/inputValidation";

import "./HomeMailingAddress.css";

import {sellingCards,sellingCashPlus} from "../../../utils";
import {List} from "immutable";

import {
    SAME_HOME_AND_MAILING_ADD,
    MAILING_COUNTRY,
    HOME,
    MAILING,
    PROPERTY_TYPE,
    RESIDENCE_STATUS,
    LENGTH_OF_RESIDENCY_YEARS,
    LENGTH_OF_RESIDENCY_MONTHS,
    HOME_POSTAL_CODE,
    IS_SG
} from "../../../constants/inputsConstant";

const CardsVariation = props => {
    const {FormConfig} = props;
    const localAddress = new FormConfig(props.step);
    const {inputLabels, inputValues} = localAddress;
    const addressFormat = props.state.getIn(dataPath(["homeAddressFormat"]), "");
    if (addressFormat !== "") {
        if (!props.state.getIn(dataPath([PROPERTY_TYPE]), "") && props.state.getIn(dataPath([HOME_POSTAL_CODE]), false)) {
            props.dispatch(setPropertyType(PROPERTY_TYPE, addressFormat));
            const propertyUIData = props.state.getIn(uiInputPath(props.step, PROPERTY_TYPE));
            if (propertyUIData && propertyUIData.get("isValid") === false) {
                props.dispatch(removeInputToUIState(props.step, PROPERTY_TYPE));
            }
        }
    }
    return (
        <div>
            {addressFormat === "" && (
                <div className='homeMailingAddress--separator'>
                    <Dropdown
                        inputLabel={inputLabels[PROPERTY_TYPE]}
                        inputID={PROPERTY_TYPE}
                        dropdownItems={inputValues[PROPERTY_TYPE]}
                        {...props}
                    />
                </div>
            )}

            <div className='homeMailingAddress--separator'>
                <Dropdown
                    inputLabel={inputLabels[RESIDENCE_STATUS]}
                    inputID={RESIDENCE_STATUS}
                    dropdownItems={inputValues[RESIDENCE_STATUS]}
                    {...props}
                />
            </div>

            <div className='homeMailingAddress--separator'>
                <div className='homeMailingAddress-unitNumber-container'>
                    <div style={{flex: "1"}}>
                        <TextInput
                            {...props}
                            inputLabel={inputLabels[LENGTH_OF_RESIDENCY_YEARS]}
                            inputID={LENGTH_OF_RESIDENCY_YEARS}
                            validators={[validateFilledInput, checkNumbers, checkMaxSize(2)]}
                        />
                    </div>
                    <div className='homeMailingAddress-unitNumber-dash'> - </div>
                    <div style={{flex: "1"}}>
                        <TextInput
                            {...props}
                            inputLabel={inputLabels[LENGTH_OF_RESIDENCY_MONTHS]}
                            inputID={LENGTH_OF_RESIDENCY_MONTHS}
                            validators={[validateFilledInput, checkNumbers, checkMaxMonths, checkMaxSize(2)]}
                        />
                    </div>
                </div>
            </div>
        </div>
    );
};

CardsVariation.propTypes = {
    state: ImmutablePropTypes.map,
    dispatch: PropTypes.func.isRequired,
    step: PropTypes.string.isRequired,
    FormConfig: PropTypes.func.isRequired
};

class HomeMailingAddress extends Component {
    componentDidMount() {
        this.props.dispatch(onToggle(this.props.step, SAME_HOME_AND_MAILING_ADD, false));
        this.props.dispatch(editInput(MAILING_COUNTRY, "SG", true, this.props.step));
    }
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const sameAddress = props.state.getIn(["data", SAME_HOME_AND_MAILING_ADD], false);
        const mailingCountry = props.state.getIn(dataPath([MAILING_COUNTRY]));
        const homeMailingAddress = new FormConfig(props.step);
        const {inputLabels, title, subtitle} = homeMailingAddress;
        const [postMessage, postMessageContent] = homeMailingAddress.getPostMessage();
        const productIds = props.state.getIn(productIdPath(), List());
        const isSG = props.state.getIn(dataPath([IS_SG]), true);
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <LocalAddressInput inputLabels={inputLabels} {...props} inputID={HOME} />
                {sellingCards(productIds, productsConfig) || sellingCashPlus(productIds, productsConfig) ? <CardsVariation {...props} /> : null}
                <div className='homeMailingAddress--separator'>
                    <ToggleInput
                        description={inputLabels[SAME_HOME_AND_MAILING_ADD]}
                        inputID={SAME_HOME_AND_MAILING_ADD}
                        {...props}
                    />
                </div>

                {sameAddress
                    ? <div>
                          <p>{inputLabels.subtitle2}</p>
                          <DropdownWithFlag
                              inputLabel={inputLabels[MAILING_COUNTRY]}
                              inputID={MAILING_COUNTRY}
                              dropdownWithFlagItems={countriesLabelValue}
                              {...props}
                          />
                          {mailingCountry === "SG"
                              ? <div className='homeMailingAddress--separator'>
                                    <LocalAddressInput {...props} inputID={MAILING} inputLabels={inputLabels} />
                                </div>
                              : <div className='homeMailingAddress--separator'>
                                    <OverseasAddressInput {...props} inputID={MAILING} inputLabels={inputLabels} />
                                </div>}
                      </div>
                    : null}

                {postMessage && isSG &&
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>}

            </div>
        );
    }
}

HomeMailingAddress.propTypes = {
    state: ImmutablePropTypes.map,
    dispatch: PropTypes.func.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string,
    dispatchOnToggle: PropTypes.func
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(HomeMailingAddress);



