/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {dataPath, ui, isFinalSubmitPath} from "../../../../constants/path";
import {countriesNamesMap} from "../../../../constants/countriesMaps";

import {
    FULL_NAME,
    NRIC,
    EMAIL,
    MOBILE_NUMBER,
    HAN_YU_PIN_YI_NAME,
    ALIAS_NAME,
    HAN_YU_PIN_YI_ALIAS_NAME,
    MARRIED_NAME,
    PASSPORT,
    PASSPORT_EXPIRY,
    PASSPORT_IDENTITY_COUNTRY,
    NRIC_PR,
    NRIC_SC,
    IS_SG,
    NATIONALITY
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";

const SummaryBlockBasicInfo = props => {
    const fullName = props.state.getIn(dataPath([FULL_NAME]));
    const hanyuPinYinName = props.state.getIn(dataPath([HAN_YU_PIN_YI_NAME]));
    const aliasName = props.state.getIn(dataPath([ALIAS_NAME]));
    const hanyuPinYinAliasName = props.state.getIn(dataPath([HAN_YU_PIN_YI_ALIAS_NAME]));
    const marriedName = props.state.getIn(dataPath([MARRIED_NAME]));
    const nric = props.state.getIn(dataPath([NRIC]));
    const email = props.state.getIn(dataPath([EMAIL]));
    const mobileNumber = props.state.getIn(dataPath([MOBILE_NUMBER]));
    const passport = props.state.getIn(dataPath([PASSPORT]));
    const passportExpiry = props.state.getIn(dataPath([PASSPORT_EXPIRY]));
    const passportIdentityCountry = props.state.getIn(dataPath([PASSPORT_IDENTITY_COUNTRY]));
    const {FormConfig} = props;
    const {title, inputLabels} = new FormConfig("basicInfo");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);
    const showEditIcon = false;

    const isSG = props.state.getIn(dataPath([IS_SG]), true);
    const isPR = isSG && props.state.getIn(dataPath([NATIONALITY])) !== "SG";

    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && showEditIcon && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "basicInfo"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                <div className='summary--block-content-container-flex'>
                    <SummaryBlockSimpleElement label={inputLabels[FULL_NAME]} value={fullName} />
                    {hanyuPinYinName && (
                        <SummaryBlockSimpleElement label={inputLabels[HAN_YU_PIN_YI_NAME]} value={hanyuPinYinName} />
                    )}
                    {aliasName && <SummaryBlockSimpleElement label={inputLabels[ALIAS_NAME]} value={aliasName} />}
                    {hanyuPinYinAliasName && (
                        <SummaryBlockSimpleElement
                            label={inputLabels[HAN_YU_PIN_YI_ALIAS_NAME]}
                            value={hanyuPinYinAliasName}
                        />
                    )}
                    {marriedName && <SummaryBlockSimpleElement label={inputLabels[MARRIED_NAME]} value={marriedName} />}
                    {nric && <SummaryBlockSimpleElement label={isPR ? inputLabels[NRIC_PR] : inputLabels[NRIC_SC]} value={nric} />}
                    {passport && <SummaryBlockSimpleElement label={inputLabels[PASSPORT]} value={passport} />}
                    {passportExpiry && (
                        <SummaryBlockSimpleElement label={inputLabels[PASSPORT_EXPIRY]} value={passportExpiry} />
                    )}
                    {passportIdentityCountry && (
                        <SummaryBlockSimpleElement label={inputLabels[PASSPORT_IDENTITY_COUNTRY]} value={countriesNamesMap[passportIdentityCountry]}/>
                    )}
                    <SummaryBlockSimpleElement label={inputLabels[EMAIL]} value={email} />
                    <SummaryBlockSimpleElement label={inputLabels[MOBILE_NUMBER]} value={mobileNumber} />
                </div>
            </div>
        </div>
    );
};

SummaryBlockBasicInfo.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired,
    FormConfig: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockBasicInfo);



