import {
    uiStepPath,
    uiFocusPath,
    uiIsValidPath,
    uiIsDirtyPath,
    dataPath,
    uiErrorMsgPath,
    uiInputPath
} from "./constants/path";
import {Map} from "immutable";
import {asYouType} from "libphonenumber-js";
import {push} from "react-router-redux";

// import {addCrossProductCasa, addCrossProductCards, removeCrossProduct} from "./actions/actionCreators";

const applyFn = (state, fn) => fn(state);
export const pipe = (fns, state) => state.withMutations(s => fns.reduce(applyFn, s));
export const variancePipe = (fns, state, step) =>
    state.withMutations(s => fns.reduce((currentState, fn) => fn(currentState, step), s));

export const checkRegExp = (value, regexp) => {
    return regexp.test(value);
};

export const swapMap = map => {
    var ret = {};
    for (var key in map) {
        if (map.hasOwnProperty(key)) {
            ret[map[key]] = key;
        }
    }
    return ret;
};

export const createActions = (action, type) => Object.assign({}, action, {type, async: false});
export const createTypes = type => [`${type}_PENDING`, `${type}_SUCCESS`, `${type}_ERROR`, `${type}_LOADING`];
export const createAsyncConstantTypes = type => [type, ...createTypes(type)];
export const fileUploadHelper = (file, url, inputId, {successDispatch, errorDispatch, progressDispatch}) => {
    const xhr = new window.XMLHttpRequest();
    const formData = new window.FormData();
    if (inputId === "signature" || inputId === "drawSignature") {
        formData.append("file", file, "signature.png");
    } else {
        formData.append("file", file);
    }
    xhr.upload.addEventListener(
        "progress",
        e => {
            const percentage = e.loaded / e.total * 100;
            progressDispatch(Math.round(percentage));
        },
        false
    );
    xhr.onloadend = e =>
        e.target.status === 200 ? successDispatch(e.target.response) : errorDispatch(e.target.response);
    xhr.open("POST", `${url}?documentType=${inputId}`);
    xhr.send(formData);
};

export const bytesToSize = bytes => {
    var sizes = ["Bytes", "KB", "MB", "GB", "TB"];
    if (bytes === 0) {
        return "0 Byte";
    }
    var i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)), 10);
    return Math.round(bytes / Math.pow(1024, i), 2) + " " + sizes[i];
};

export const addValidFlagToState = (inputIds, props) => {
    const {dispatchAddValidFlag, state, step} = props;
    const inputsInStep = state.getIn(uiStepPath(step));
    inputIds.forEach(x => {
        if (!inputsInStep.get(x)) {
            dispatchAddValidFlag(step, x);
        }
    });
};

export const addNewInputToState = (inputsToAdd, customStep) => (state, step) => {
    const newStep = customStep || step;
    const currentStepInputs = state.getIn(uiStepPath(newStep));
    const uiDefaultState = Map({
        isFocus: false,
        isDirty: false,
        isValid: false,
        errorMsg: ""
    });
    const addStepInputs = inputsToAdd.reduce((r, f) => {
        const previousValues = state.getIn(uiInputPath(newStep, f), uiDefaultState);
        return r.set(f, previousValues);
    }, Map());
    const newStepInputs = currentStepInputs.merge(addStepInputs);
    return state.setIn(uiStepPath(newStep), newStepInputs);
};

export const removeInputFromState = (inputsToRemove, customStep) => (state, step) => {
    const newStep = customStep || step;
    const currentStepInputs = state.getIn(uiStepPath(newStep));
    const newStepInputs = currentStepInputs.filter((v, k) => !inputsToRemove.includes(k));
    // inputsToRemove.map(input => state.setIn(dataPath([input]), ""));
    return state.setIn(uiStepPath(newStep), newStepInputs);
};

export const mapAPIJsonToStore = apiJson => {
    return apiJson.myInfo
        ? {
            fullName: apiJson.myInfo.name.value,
            nric: apiJson.myInfo.myinfolegalIdentity,
            email: apiJson.myInfo.email.value,
            mobileNumber: new asYouType().input(
                  `${apiJson.myInfo.mobileno.prefix}${apiJson.myInfo.mobileno.code}${apiJson.myInfo.mobileno.nbr}`
              ),
            nameOntheCard: apiJson.myInfo.name.value,
            hanYuPinYinName: apiJson.myInfo.hanyupinyinname.value,
            aliasName: apiJson.myInfo.aliasname.value,
            hanyuPinYinAliasName: apiJson.myInfo.hanyupinyinaliasname.value,
            marriedName: apiJson.myInfo.marriedname.value,
            username: apiJson.myInfo.username.value
        }
        : {};
};

export const getInputStates = (state, step, inputID) => {
    // console.log(step, inputID);
    const isFocus = state.getIn(uiFocusPath(step, inputID), false);
    const isValid = state.getIn(uiIsValidPath(step, inputID));
    const isDirty = state.getIn(uiIsDirtyPath(step, inputID), false);
    const inputValue = state.getIn(dataPath([inputID]), "");
    const errorMsg = state.getIn(uiErrorMsgPath(step, inputID), "");
    return [isFocus, isValid, isDirty, errorMsg, inputValue];
};

export const getInputStatesByPath = (state, step, inputID, path) => {
    // console.log(step, inputID);
    const isFocus = state.getIn(uiFocusPath(step, inputID), false);
    const isValid = state.getIn(uiIsValidPath(step, inputID));
    const isDirty = state.getIn(uiIsDirtyPath(step, inputID), false);
    const inputValue = state.getIn(path, "");
    const errorMsg = state.getIn(uiErrorMsgPath(step, inputID), "");
    return [isFocus, isValid, isDirty, errorMsg, inputValue];
};

export const getErrorMsg = (type, config) => config["formInputErrorMessages"][type];

export const createFormClass = config =>
    class FormConfig {
        constructor(step) {
            this.step = step;
            this.title = config["formSteps"][step]["title"];
            this.subtitle = config["formSteps"][step]["subtitle"];
            this.inputLabels = config["formSteps"][step]["inputLabels"];
            this.inputValues = config["formSteps"][step]["inputValues"]
                ? this.mapDropdownValues(config["formSteps"][step]["inputValues"])
                : "";
            this.inputDescriptions = config["formSteps"][step]["inputDescription"];
            this.dropDownValues = config["formSteps"][step]["inputValues"];
            this.termsAndConditions = config["formSteps"][step]["termsAndConditions"];
            this.plTableRowTitles = config["formSteps"][step]["plTableRowTitles"];
            this.btTableRowTitles = config["formSteps"][step]["btTableRowTitles"];
            this.congratulation = config["formSteps"]["congratulation"];
            this.errorPage = config["formSteps"]["errorPage"];
            this.buttons = config["formSteps"]["accountDetails"]["buttons"];
        }
        getPostMessage(type) {
            this.postMessage = config["formSteps"][this.step]["postInputsMessage"][type || "default"]["title"];
            this.postMessageContent = config["formSteps"][this.step]["postInputsMessage"][type || "default"]["content"];
            return [this.postMessage, this.postMessageContent];
        }
        mapDropdownValues(inputValues) {
            const dropdownInputs = Object.keys(inputValues);
            const dropdownValueAndLabel = dropdownInputs.map(x => inputValues[x]).map(valueAndLabel => {
                const value = Object.keys(valueAndLabel);
                return value.map(v => ({value: v, label: valueAndLabel[v]}));
            });
            return dropdownInputs.reduce(
                (dropdownConfig, inputId, i) => (dropdownConfig[inputId] = dropdownValueAndLabel[i]) && dropdownConfig,
                {}
            );
        }
    };

export const detectPathName = () => {
    const router = [];
    let pn = window.location.pathname;
    // TODO
    return pn.replace(router.filter(a => pn.indexOf(a) !== -1)[0], "");
};

export const sellingCards = (productIds, productConfig) => {
    return productIds.map(productId => productConfig.products[productId].type).includes("Card");
};

export const sellingCasa = (productIds, productConfig) => {
    return productIds.map(productId => productConfig.products[productId].type).includes("CASA");
};

export const sellingCashPlus = (productIds, productConfig) => {
    return productIds.map(productId => productConfig.products[productId].type).includes("CASH+");
};

export const sellinBalanceTransfer = (productIds, productConfig) => {
    return productIds.map(productId => productConfig.products[productId].type).includes("balanceTransfer");
};

export const sellinPersonalLoan = (productIds, productConfig) => {
    return productIds.map(productId => productConfig.products[productId].type).includes("personalLoan");
};

export const isSG = ic => {
    return ic.split("")[0] === "S";
};

export const convertDataURLToFile = (file, fileName) => {
    const blobBin = window.atob(file.split(",")[1]);
    const array = [];
    for (var i = 0; i < blobBin.length; i++) {
        array.push(blobBin.charCodeAt(i));
    }
    return new window.File([new Uint8Array(array)], fileName, {type: "image/png"});
};

const PMT = (rate, nper, pv, fv, type) => {
    if (rate === 0) {
        return -(pv + fv) / nper;
    }

    var pvif = Math.pow(1 + rate, nper);
    var pmt = rate / (pvif - 1) * -(pv * pvif + fv);

    if (type === 1) {
        pmt /= 1 + rate;
    }

    return pmt;
};

export const calProcessingFee = (amt, processingFee) => (Number(processingFee) / 100 * Number(amt)).toFixed(2);
export const calMonthlyRepayments = (amt, interestRate, tenor) => {
    const interestRateInDec = interestRate / 100;
    return PMT(interestRateInDec / 12, tenor, -amt, 0, 0).toFixed(2);
};
export const caltotalInterestPayable = (amt, interestRate, tenor) => {
    const interestRateInDec = interestRate / 100;
    const pmt = PMT(interestRateInDec / 12, tenor, -amt, 0, 0);
    return (pmt * tenor - amt).toFixed(2);
};

export const isValidOrNotDirty = (isValid, isDirty) => isValid || !isDirty;

export const handleSessionTimeout = (res, dispatch) =>
    res.status === 401 ? dispatch(push(`${detectPathName()}error`)) : res;
export const handleValidationMismatch = (res, dispatch) =>
    res.status === 406 ? dispatch(push(`${detectPathName()}error`)) : res;
export const handleServerDown = (res, dispatch) =>
    res.status >= 500 ? dispatch(push(`${detectPathName()}error`)) : res;
export const getHighestRankProduct = productsInfo => productsInfo.max((a, b) => a.get("rank", 0) > b.get("rank", 0));

export const checkItemInList = (items , targetList, isCheck) => {
    if (!isCheck) {
        return false;
    }
    if (targetList && targetList.length === 0) {
        return false;
    }
    const isExist = (items || []).map(item => targetList.includes(item));
    return isExist.first();
};

export const addPrefixUOB = (name, type) => {
    return ["CPPL", "CPBT", "CCPL", "CCBT"].indexOf(type) > -1 ? "UOB " + name : name;
};

export const sortByDefaultList = (defaultList, list) => {
    let arr = [];
    if (list && list.count() === 0) {
        return list;
    }
    (defaultList || []).forEach(entry =>
    {
        const filterList = list.filter(e => e.get("pid") === entry);
        if (filterList.count() > 0) {
            arr.push(filterList.first());
        }
    }
    );
    return arr.length > 0 ? arr : list;
};

export const isExistCreditOrDebit = (productId, productIds, credits, debits) => {
    let typeCard = "";
    if (debits.indexOf(productId) > -1) {
        typeCard = "DEBIT";
    }
    if (credits.indexOf(productId) > -1) {
        typeCard = "CREDIT";
    }
    if (!typeCard) {
        return false;
    }
    const isFilter = productIds.filter(entry => {
        if (typeCard === "DEBIT" && entry !== productId) {
            return debits.includes(entry);
        }
        if (typeCard === "CREDIT" && entry !== productId) {
            return credits.includes(entry);
        }
        return false;
    });
    return isFilter.count() > 0 ? true : false;
};




