/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {onFocusInput, onBlurInput, editInput} from "../../actions/actionCreators";
import {dataPath, uiFocusPath, uiIsValidPath, uiIsDirtyPath} from "../../constants/path";

import "./ReadOnlyTextInput.css";

// import {validatorHOC} from "../HOC/ValidatorHOC";

const ReadOnlyTextInput = props => {
    const isFocus = props.state.getIn(uiFocusPath(props.step, props.inputID), false);
    const isValid = props.state.getIn(uiIsValidPath(props.step, props.inputID), false);
    const isDirty = props.state.getIn(uiIsDirtyPath(props.step, props.inputID), false);
    const inputValue = props.state.getIn(dataPath([props.inputID]), "");
    const value = props.value ? props.value : inputValue;
    const focus = isFocus || value.length > 0 || !(isValid || !isDirty) ? "--focused" : "";

    return (
        <div className='read-only-text-input--container'>
            <div className='read-only-text-input--content'>
                <label className={`read-only-text-input--label${focus}`}>{`${props.inputLabel}`}</label>
                <div className={`read-only-text-input--input${focus}`}>{value}</div>
            </div>
            <img
                className={"read-only-text-input--tick--visible"}
                src={`${FORMS_CNFG_GLBL.imgPath}/non-editable-icon4.svg`}
                alt='tick'
            />
        </div>
    );
};

ReadOnlyTextInput.propTypes = {
    state: ImmutablePropTypes.map,
    inputLabel: PropTypes.string,
    inputID: PropTypes.string,
    step: PropTypes.string.isRequired,
    value: PropTypes.string
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchEditInput: bindActionCreators(editInput, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(ReadOnlyTextInput);



