/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {dataPath, ui, isFinalSubmitPath} from "../../../../constants/path";
import {
    NATURE_OF_EMPLOYMENT,
    NAME_OF_EMPLOYER,
    INDUSTRY,
    ANNUAL_INCOME,
    OCCUPATION,
    COMPANY,
    EPASS_EXPIRY,
    ASSESS_YEAR
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";

const SummaryBlockWorkInfo = props => {
    const natureOfEmployment = props.state.getIn(dataPath([NATURE_OF_EMPLOYMENT]));
    const nameOfEmployer = props.state.getIn(dataPath([NAME_OF_EMPLOYER]));
    const jobTitle = props.state.getIn(dataPath([OCCUPATION]));
    const industry = props.state.getIn(dataPath([INDUSTRY]));
    const annualIncome = props.state.getIn(dataPath([ANNUAL_INCOME]));
    const company = props.state.getIn(dataPath([COMPANY]));
    const ePassExpiry = props.state.getIn(dataPath([EPASS_EXPIRY]));
    const lengthOfEmploymentYears = props.state.getIn(dataPath(["lengthOfEmploymentYears"]));
    const lengthOfEmploymentMonths = props.state.getIn(dataPath(["lengthOfEmploymentMonths"]));
    const {FormConfig} = props;
    const {dropDownValues, title, inputLabels} = new FormConfig("workInfo");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);
    const assessYear = props.state.getIn(dataPath([ASSESS_YEAR]));

    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "workInfo"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                <div className='summary--block-content-container-flex'>
                    <SummaryBlockSimpleElement
                        label={inputLabels[NATURE_OF_EMPLOYMENT]}
                        value={dropDownValues[NATURE_OF_EMPLOYMENT][natureOfEmployment]}
                    />
                    {ePassExpiry && <SummaryBlockSimpleElement label={inputLabels[EPASS_EXPIRY]} value={ePassExpiry} />}
                    {jobTitle && (
                        <SummaryBlockSimpleElement
                            label={inputLabels[OCCUPATION]}
                            value={dropDownValues[OCCUPATION][jobTitle]}
                        />
                    )}
                    {nameOfEmployer ? (
                        <SummaryBlockSimpleElement label={inputLabels[NAME_OF_EMPLOYER]} value={nameOfEmployer} />
                    ) : null}
                    {company ? <SummaryBlockSimpleElement label={inputLabels[COMPANY]} value={company} /> : null}
                    {industry ? (
                        <SummaryBlockSimpleElement
                            label={inputLabels[INDUSTRY]}
                            value={dropDownValues[INDUSTRY][industry]}
                        />
                    ) : null}
                    {lengthOfEmploymentYears ? (
                        <SummaryBlockSimpleElement
                            label={inputLabels["lengthOfEmploymentYears"]}
                            value={lengthOfEmploymentYears}
                        />
                    ) : null}
                    {lengthOfEmploymentMonths ? (
                        <SummaryBlockSimpleElement
                            label={inputLabels["lengthOfEmploymentMonths"]}
                            value={lengthOfEmploymentMonths}
                        />
                    ) : null}
                    {annualIncome && (
                        <SummaryBlockSimpleElement label={assessYear && assessYear !== "" ? `${inputLabels[ANNUAL_INCOME]} for ${assessYear}` : inputLabels[ANNUAL_INCOME]} value={annualIncome} />
                    )}
                </div>
            </div>
        </div>
    );
};

SummaryBlockWorkInfo.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    FormConfig: PropTypes.func.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockWorkInfo);



