import React, {Component} from "react";
import {List, Map, fromJS} from "immutable";

import Dropdown from "../Dropdown/Dropdown";
import DropdownWithFlag from "../DropdownWithFlag/DropdownWithFlag";
import ToggleInput from "../ToggleInput/ToggleInput";

const currencyFullName = {
    SGD: "Singapore Dollar",
    EUR: "Euro",
    USD: "U.S. Dollar",
    AUD: "Australian Dollar",
    GBP: "British Pound",
    CHN: "Chinese Yuan",
    JPY: "Japanese Yuan",
    HKD: "Hong Kong Dollar",
    CAD: "Canadian Dollar",
    NZD: "New Zealand Dollar",
    CHF: "Swiss Franc"
};

import {
    updatePurposeOfFunds,
    updatePurposeOfFundsForAll,
    updateSourceOfFunds,
    updateSourceOfFundsForAll,
    updateCountryOfFunds,
    updateCountryOfFundsForAll,
    toggleSameStatus,
    updatePurposeOfFundsForFx,
    updateSourceOfFundsForFx,
    updateCountryOfFundsForFx
} from "../../actions/actionCreators";

import {countriesLabelValue} from "../../constants/countriesMaps";

import {PURPOSE_OF_FUNDS, SOURCE_OF_FUNDS, COUNTRY_OF_FUNDS} from "../../constants/inputsConstant";

const purposeOfFundsFunction = (sameValues, props) => {
    if (sameValues) {
        return item => props.dispatch(updatePurposeOfFundsForAll(item.value));
    }
    return props.pid === "CURFX"
        ? item => props.dispatch(updatePurposeOfFundsForFx(item.value, "CURFX"))
        : item => props.dispatch(updatePurposeOfFunds(props.currency, item.value, props.pid, props.familyType));
};

const sourceOfFundsFunction = (sameValues, props) => {
    if (sameValues) {
        return item => props.dispatch(updateSourceOfFundsForAll(item.value));
    }
    return props.pid === "CURFX"
        ? item => props.dispatch(updateSourceOfFundsForFx(item.value, "CURFX"))
        : item => props.dispatch(updateSourceOfFunds(props.currency, item.value, props.pid, props.familyType));
};

const countryOfFundsFunction = (sameValues, props) => {
    if (sameValues) {
        return item => props.dispatch(updateCountryOfFundsForAll(item.value));
    }
    return props.pid === "CURFX"
        ? item => props.dispatch(updateCountryOfFundsForFx(item.value, "CURFX"))
        : item => props.dispatch(updateCountryOfFunds(props.currency, item.value, props.pid, props.familyType));
};

class CurrencyDetails extends Component {
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const typeOfProduct = fromJS(productsConfig.products);
        const sameValues = props.state.getIn(["data", "samePurposeToggle"], false);
        const purposeOfFundsOnClick = purposeOfFundsFunction(sameValues, props);
        const sourceOfFundsOnClick = sourceOfFundsFunction(sameValues, props);
        const countryOfFundsOnClick = countryOfFundsFunction(sameValues, props);
        const {inputLabels} = new FormConfig("accountDetails");
        let additionalTitle;
        if (props.pid !== "SAVJS") {
            additionalTitle =
                props.pid !== ("CURDAA" || "CURJP")
                    ? `Additional information for ${typeOfProduct.getIn([props.pid, "productName"])}`
                    : `Additional information for ${currencyFullName[props.currency]}`;
        } else {
            additionalTitle = null;
        }
        const familyType = typeOfProduct.getIn([props.pid, "family"]);
        const isDebt1 = familyType === "debt1";

        return (
            <div>
                <p className='account-details--currency'>{additionalTitle}</p>

                <Dropdown
                    inputID={`${props.pid}-${PURPOSE_OF_FUNDS}-${props.currency}`}
                    inputLabel={inputLabels[PURPOSE_OF_FUNDS]}
                    dropdownItems={props.inputValues[PURPOSE_OF_FUNDS]}
                    onClick={purposeOfFundsOnClick}
                    value={props.state.getIn([
                        "data",
                        "products",
                        props.pid,
                        "currencies",
                        props.currency,
                        "purposeOfFunds"
                    ])}
                    {...props}
                />
                <div className='account-details--separator'>
                    <Dropdown
                        inputID={`${props.pid}-${SOURCE_OF_FUNDS}-${props.currency}`}
                        inputLabel={inputLabels[SOURCE_OF_FUNDS]}
                        dropdownItems={props.inputValues[SOURCE_OF_FUNDS]}
                        onClick={sourceOfFundsOnClick}
                        value={props.state.getIn([
                            "data",
                            "products",
                            props.pid,
                            "currencies",
                            props.currency,
                            "sourceOfFunds"
                        ])}
                        {...props}
                    />
                </div>

                <div className='account-details--separator'>
                    <DropdownWithFlag
                        inputLabel={inputLabels[COUNTRY_OF_FUNDS]}
                        inputID={`${props.pid}-${COUNTRY_OF_FUNDS}-${props.currency}`}
                        dropdownWithFlagItems={countriesLabelValue}
                        onClick={countryOfFundsOnClick}
                        value={props.state.getIn([
                            "data",
                            "products",
                            props.pid,
                            "currencies",
                            props.currency,
                            "countryOfFunds"
                        ])}
                        {...props}
                    />
                </div>

                {props.isFirst &&
                    props.displayToggle && (
                        <div className='account-details--separator'>
                            <ToggleInput
                                description='Is the purpose and source of funds same for all accounts ?'
                                inputID={"samePurposeToggle"}
                                componentDidMount={() => {
                                    const primaryValue = props.state
                                        .getIn(
                                            ["data", "products", props.pid, "currencies"],
                                            List(
                                                Map({
                                                    purposeOfFunds: "",
                                                    sourceOfFunds: "",
                                                    countryOfFunds: "SG"
                                                })
                                            )
                                        )
                                        .first();
                                    return props.dispatch(
                                        toggleSameStatus(true, primaryValue, props.pid, props.currency)
                                    );
                                }}
                                onClick={() => {
                                    const primaryValue = props.state
                                        .getIn(["data", "products", props.pid, "currencies"])
                                        .first();
                                    if (isDebt1) {
                                        return null;
                                    }
                                    return props.dispatch(
                                        toggleSameStatus(!sameValues, primaryValue, props.pid, props.currency)
                                    );
                                }}
                                value={sameValues}
                                {...props}
                            />
                        </div>
                    )}
            </div>
        );
    }
}

export default CurrencyDetails;



