/* global FORMS_CNFG_GLBL */

import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

import {bindActionCreators} from "redux";
import {connect} from "react-redux";
// import TextInput from "../../TextInput/TextInput";
import "./NotFound.css";
import {rightPanelExpandedPath} from "../../../constants/path";
import {setRightPanelExpanded} from "../../../actions/actionCreators";
import {passProps} from "../../HOC/passProps";

const NotFound = props => {
    const config = new props.FormConfig("errorPage");
    const isExpanded = props.state.getIn(rightPanelExpandedPath(), false);
    const rightPannelState = isExpanded ? "--expanded" : "";
    return (
        <div className='notfound'>
            <div className='notfound--left-panel'>
                <div
                    className={"notfound--form-cover" + rightPannelState}
                    onClick={() => {
                        props.dispatchSetRightPanelExpanded(!isExpanded);
                    }}
                />
                <div className='notfound--left-panel-infos-container'>
                    <div className='notfound--left-panel-logo'>
                        <img src={`${FORMS_CNFG_GLBL.imgPath}/uobLogo.svg`} alt='uobLogo' />
                    </div>
                    <div className='notfound--error-message'>{config.errorPage.title}</div>
                    <div className='notfound--left-panel-title-separator' />
                    <div className='notfound--left-panel-text'>
                        <div
                            dangerouslySetInnerHTML={{
                                __html: config.errorPage.body
                            }}
                        />
                    </div>
                </div>
            </div>
            <div
                className={"notfound--right-panel" + rightPannelState}
                onClick={() => {
                    props.dispatchSetRightPanelExpanded(!isExpanded);
                }}
            >
                <div className='uob-form--expand-right-panel-button-flex-container'>
                    <img
                        src={`${FORMS_CNFG_GLBL.imgPath}/info_icon.svg`}
                        className={"uob-form-info--icon" + rightPannelState}
                        alt='infoIcon'
                    />
                    <div className={"uob-form--expand-right-panel-button" + rightPannelState}>
                        Need help?
                    </div>
                </div>
                <div className={"notfound-right-column-container" + rightPannelState}>
                    <h2 className={"notfound-right-column-text"}>Need help?</h2>
                    <p className='notfound--right-column-description'>Feel free to contact our team for support</p>
                    <p className='notfound--right-column-sub-title'>Singapore</p>
                    <p className='notfound--right-column-text'> 1800 222 2121</p>
                    <p className='notfound--right-column-sub-title'>Overseas Customers</p>
                    <p className='notfound--right-column-text'> +65 6222 2121 </p>
                </div>
            </div>
        </div>
    );
};

NotFound.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchSetRightPanelExpanded: PropTypes.func.isRequired,
    FormConfig: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => ({
    dispatchSetRightPanelExpanded: bindActionCreators(setRightPanelExpanded, dispatch)
});

export default passProps(connect(null, mapDispatchToProps)(NotFound));



