import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import Accordian from "../../Accordian/Accordian";

import {
    addValidFlag,
    getMyInfo,
    selectPendingApplication,
    removePendingApplication,
    postPartialApplication
} from "../../../actions/actionCreators";

import "./PendingApplication.css";
import {List} from "immutable";

import {dataPath, pendingApplicationPath} from "../../../constants/path";

const ApplicationItem = props => {
    const isSelected = props.state
        .getIn(dataPath(["pendingApplications"]), List())
        .find(application => application.get("applicationNumber") === props.card.applicationNo);
    const selectApplication = (pid, applicationNo) =>
        props.dispatch(selectPendingApplication("pendingApplications", pid, applicationNo));
    const removeApplication = applicationNo =>
        props.dispatch(removePendingApplication("pendingApplications", applicationNo));
    const itemClass = isSelected ? "pendingApplication-item--selected" : "pendingApplication-item";
    return (
        <div
            className={itemClass}
            onClick={() =>
                isSelected
                    ? removeApplication(props.card.applicationNo)
                    : selectApplication(props.card.pid, props.card.applicationNo)}
        >
            <img className='pendingApplication-item-image' src={props.card.imgPath} alt='card-img' />
            <div className='pendingApplication-item-name'>{props.card.productName}</div>
        </div>
    );
};

class PendingApplication extends Component {
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const {products} = productsConfig;
        const pendingApplication = new FormConfig(props.step);
        const {title, subtitle} = pendingApplication;
        const [postMessage, postMessageContent] = pendingApplication.getPostMessage();
        const pendingApplications = props.state.getIn(pendingApplicationPath(), List());
        const mapApplicationData = pendingApplications.map(application => {
            const product = products[application.get("id")];
            product.applicationNo = application.get("applicationNumber");
            return product;
        });

        const handleOnClick = () => props.dispatch(postPartialApplication("pendingApplication", "accountDetails"));
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <div className='pendingApplication-container'>
                    {mapApplicationData.map((card, i) => <ApplicationItem key={i} card={card} {...props} />)}
                </div>
                {postMessage &&
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>}
                <div className='pendingApplication-skip-step'>
                    Do not want to continue any pending application?
                    <span className='pendingApplication-skip-step-click-here' onClick={handleOnClick}>
                        Start a new application.
                    </span>{" "}
                </div>
            </div>
        );
    }
}

ApplicationItem.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    card: PropTypes.object
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch),
        dispatchInit: bindActionCreators(getMyInfo, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(PendingApplication);



