import React, {Component} from "react";

import TextInput from "../TextInput/TextInput";
import ToggleInput from "../ToggleInput/ToggleInput";

import {editInputByPath} from "../../actions/actionCreators";

import {
    validateFilledInput,
    checkFullName,
    checkSize,
    checkKrisFlyer,
    checkMaxSize
} from "../../validations/inputValidation";

import {
    KRYSFLYER_MEMBERSHIP_NUMBER,
    KRISFLYER_LAST_NAME,
    KRISFLYER_FIRST_NAME,
    IS_EXISTING_KRISFLYER_MEMBER
} from "../../constants/inputsConstant";

class Krisflyer extends Component {
    render() {
        const props = this.props;
        const {FormConfig, formData} = props;
        const isMember = props.state.getIn(["data", `${props.product}-${IS_EXISTING_KRISFLYER_MEMBER}`], true);
        const {inputLabels} = new FormConfig(props.step);

        return (
            <div>
                <div className='account-details--separator'>
                    <ToggleInput
                        description={inputLabels[IS_EXISTING_KRISFLYER_MEMBER]}
                        inputID={`${props.product}-${IS_EXISTING_KRISFLYER_MEMBER}`}
                        value={isMember}
                        {...props}
                    />
                </div>

                {isMember
                    ? <div className='account-details--separator'>
                          <TextInput
                              inputLabel={inputLabels[KRYSFLYER_MEMBERSHIP_NUMBER]}
                              inputID={`${props.product}-${KRYSFLYER_MEMBERSHIP_NUMBER}`}
                              // validators={[validateFilledInput, checkSize(10)]}
                              onChange={e => {
                                  const value = e.target.value;
                                  const validators = [validateFilledInput, checkKrisFlyer, checkSize(10)];
                                  const status = validators
                                      .map(x => x(formData))
                                      .map(x => x(value))
                                      .filter(x => !x.status);

                                  return props.dispatch(
                                      editInputByPath(
                                          props.step,
                                          `${props.product}-${KRYSFLYER_MEMBERSHIP_NUMBER}`,
                                          ["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER],
                                          value,
                                          status
                                      )
                                  );
                              }}
                              value={props.state.getIn(
                                  ["data", "products", props.products.first(), KRYSFLYER_MEMBERSHIP_NUMBER],
                                  ""
                              )}
                              {...props}
                          />
                      </div>
                    : null}
                <div className='account-details--separator'>
                    <TextInput
                        inputLabel={inputLabels[KRISFLYER_FIRST_NAME]}
                        inputID={`${props.product}-${KRISFLYER_FIRST_NAME}`}
                        // validators={[validateFilledInput, checkFullName]}
                        onChange={e => {
                            const value = e.target.value;
                            const validators = [validateFilledInput, checkFullName, checkMaxSize(30)];
                            const status = validators.map(x => x(formData)).map(x => x(value)).filter(x => !x.status);

                            return props.dispatch(
                                editInputByPath(
                                    props.step,
                                    `${props.product}-${KRISFLYER_FIRST_NAME}`,
                                    ["data", "products", props.products.first(), KRISFLYER_FIRST_NAME],
                                    value,
                                    status
                                )
                            );
                        }}
                        value={props.state.getIn(["data", "products", props.products.first(), KRISFLYER_FIRST_NAME], "")}
                        {...props}
                    />
                </div>
                <div className='account-details--separator'>
                    <TextInput
                        inputLabel={inputLabels[KRISFLYER_LAST_NAME]}
                        inputID={`${props.product}-${KRISFLYER_LAST_NAME}`}
                        onChange={e => {
                            const value = e.target.value;
                            const validators = [validateFilledInput, checkFullName, checkMaxSize(30)];
                            const status = validators.map(x => x(formData)).map(x => x(value)).filter(x => !x.status);

                            return props.dispatch(
                                editInputByPath(
                                    props.step,
                                    `${props.product}-${KRISFLYER_LAST_NAME}`,
                                    ["data", "products", props.products.first(), KRISFLYER_LAST_NAME],
                                    value,
                                    status
                                )
                            );
                        }}
                        value={props.state.getIn(["data", "products", props.products.first(), KRISFLYER_LAST_NAME], "")}
                        {...props}
                    />
                </div>
            </div>
        );
    }
}

export default Krisflyer;



