import React from "react";

import FileUpload from "../../FileUpload/FileUpload";

import "./IncomeDocuments.css";

const IncomeDocuments = props => {
    return (
        <div>
            <div>
                <FileUpload title='Notice of Assessment'
                            description='No files delected.'
                            inputID='noticeOfAssessment'
                            {...props}/>
            </div>
            <div className='declaration--info-separator'>
                <FileUpload title='IR8A'
                            description='No files selected.'
                            inputID='ir8a'
                            {...props}/>
            </div>
            <div className='declaration--info-separator'>
                <FileUpload title='Payslip'
                            description='No files selected.'
                            inputID='payslip'
                            {...props}/>
            </div>
            <div className='declaration--info-separator'>
                <FileUpload title='CBS Reports'
                            description='No files selected.'
                            inputID='cbsReports'
                            {...props}/>
            </div>
            <div className='declaration--info-separator'>
                <FileUpload title='Proof of balance from other banks'
                            description='No files selected.'
                            inputID='proofOfBalance'
                            {...props}/>
            </div>
        </div>
    );
};

export default IncomeDocuments;



