import {
  ACTION_VALIDATE_USERNAME_SUCCESS,
  ACTION_VALIDATE_USERNAME_ERROR
} from "../constants/actionsConstant";

const validateUsernameMiddleware = () => next => action => {
    if (action.type === ACTION_VALIDATE_USERNAME_SUCCESS && action.successCallback) {
        action.successCallback();
    }
    if (action.type === ACTION_VALIDATE_USERNAME_ERROR && action.errorCallback) {
        action.errorCallback();
    }
    return next(action);
};
export default validateUsernameMiddleware;



