import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import EditableDropdownWithFlag from "../EditableDropdownWithFlag/EditableDropdownWithFlag";
import ReadOnlyDropdownWithFlag from "../ReadOnlyDropdownWithFlag/ReadOnlyDropdownWithFlag";
import {isReadOnlyPath} from "../../constants/path";

const DropdownWithFlag = props => {
    const isReadOnly = props.state.getIn(isReadOnlyPath(props.step, props.inputID), false);
    return isReadOnly ? <ReadOnlyDropdownWithFlag {...props} /> : <EditableDropdownWithFlag {...props} />;
};

DropdownWithFlag.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired
};

export default DropdownWithFlag;



