/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

// import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";
import SummaryBlockToggleElement from "../components/SummaryBlockToggleElement/SummaryBlockToggleElement";
import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";
import {Map} from "immutable";
import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {dataPath, ui, uiSignatureImageContentPath, productIdPath, isFinalSubmitPath} from "../../../../constants/path";
import {
    US_RESIDENT,
    US_CITIZEN,
    RESIDENT_CARD_HOLDER,
    DRAW_SIGNATURE,
    SIGNATURE
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";

import {countriesNamesMap} from "../../../../constants/countriesMaps";
import {sellingCasa, sellingCards, sellingCashPlus} from "../../../../utils";

const TaxResidencyDetails = props => {
    const {inputValues} = props;
    const country = countriesNamesMap[props.residency.get("country")];
    const tinReason = inputValues.tinReasons.find(reason => reason.value === props.residency.get("reasonCode"));

    return (
        <div className='summary--block-content-container-flex'>
            <SummaryBlockSimpleElement label='Tax Residency' value={country} />
            <SummaryBlockSimpleElement label='Tax Reason' value={tinReason.label} />
            {props.residency.get("id") && (
                <SummaryBlockSimpleElement label='Tax Identification No.' value={props.residency.get("id")} />
            )}
        </div>
    );
};

const SummaryBlockDeclaration = props => {
    const pids = props.state.getIn(productIdPath());
    const usResident = props.state.getIn(dataPath([US_RESIDENT]));
    const usCitizen = props.state.getIn(dataPath([US_CITIZEN]));
    const cardHolder = props.state.getIn(dataPath([RESIDENT_CARD_HOLDER]));
    const taxResidencyIds = props.state.getIn(["data", "countries"], Map());
    const signatureImg = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        window.navigator.userAgent
    )
        ? props.state.getIn(uiSignatureImageContentPath("declaration", DRAW_SIGNATURE), "none")
        : props.state.getIn(uiSignatureImageContentPath("declaration", SIGNATURE), "none");
    const {FormConfig, productsConfig} = props;
    const {title, inputLabels, inputValues} = new FormConfig("declaration");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);

    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "declaration"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                {(sellingCasa(pids, productsConfig) || sellingCashPlus(pids, productsConfig)) && (
                    <div>
                        <SummaryBlockToggleElement label={inputLabels[US_RESIDENT]} value={usResident ? "Yes" : "No"} />
                        <div className='summary--block-separator'>
                            <SummaryBlockToggleElement
                                label={inputLabels[US_CITIZEN]}
                                value={usCitizen ? "Yes" : "No"}
                            />
                        </div>
                        <div className='summary--block-separator'>
                            <SummaryBlockToggleElement
                                label={inputLabels[RESIDENT_CARD_HOLDER]}
                                value={cardHolder ? "Yes" : "No"}
                            />
                        </div>
                        {sellingCasa(pids, productsConfig) && <div className='summary--block-separator'>
                            {taxResidencyIds
                                .keySeq()
                                .map(residencyKey => (
                                    <TaxResidencyDetails
                                        key={residencyKey}
                                        inputValues={inputValues}
                                        residency={taxResidencyIds.get(residencyKey)}
                                        {...props}
                                    />
                                ))}
                        </div>}
                        <div className='summary--block-separator'>
                            <div className='summary--block-signature-container'>
                                <p className='simple-element-title'>{inputLabels[SIGNATURE]}</p>
                                <img className='summary--block-signarue-image' src={signatureImg} alt='sigImg' />
                            </div>
                        </div>
                    </div>
                )}

                {sellingCards(pids, productsConfig) &&
                    !sellingCasa(pids, productsConfig) && (
                        <div className='summary--block-signature-container'>
                            <p className='simple-element-title'>{inputLabels[SIGNATURE]}</p>
                            <img className='summary--block-signarue-image' src={signatureImg} alt='sigImg' />
                        </div>
                    )}
            </div>
        </div>
    );
};

TaxResidencyDetails.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    residency: PropTypes.object.isRequired,
    inputValues: PropTypes.object
};

SummaryBlockDeclaration.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    FormConfig: PropTypes.func.isRequired,
    productsConfig: PropTypes.object.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockDeclaration);



