import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators, compose} from "redux";

import {onCheck} from "../../actions/actionCreators";
import {getInputStates} from "../../utils";
import {checkBoxDefaultValue} from "../HOC/checkBoxDefaultValue";
import {validatorHOC} from "../HOC/ValidatorHOC";
import ToggleButton from "../../components/ToggleButton/ToggleButton";

import "./CheckboxInput.css";

const CheckboxInput = props => {
    const [, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const errorClass = isDirty && !isValid ? "--error" : "";
    const error = props.msgErrorCheckboxInput || errorMsg;

    return (
        <div id={props.inputID} className={`checkboxInput--container${errorClass}`}>
            <div className='checkboxInput--content'>
                <div dangerouslySetInnerHTML={{__html: props.description}} />
                {isDirty && !isValid && <span className='checkboxInput--error'>{`${error}`}</span>}
            </div>

            <div className='checkboxInput--checkbox'>
                <ToggleButton
                    inputID={props.inputID}
                    onClick={() => props.dispatchOnCheck(props.inputID, props.step, !inputValue)}
                    {...props}
                />
            </div>
        </div>
    );
};

CheckboxInput.propTypes = {
    state: ImmutablePropTypes.map,
    description: PropTypes.string,
    inputID: PropTypes.string,
    step: PropTypes.string.isRequired,
    dispatchOnCheck: PropTypes.func
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnCheck: bindActionCreators(onCheck, dispatch)
    };
};

const enchance = compose(validatorHOC, checkBoxDefaultValue(false), connect(null, mapDispatchToProps));

export default enchance(CheckboxInput);



