/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {onFocusInput, onBlurInput, getAddress, editPostalCode} from "../../actions/actionCreators";
import {getInputStates, isValidOrNotDirty} from "../../utils";

import "./PostalCodeInput.css";

import {postalCodeSpinnerPath, postalCodeServiceStatusPath, postalCodeCountExceededPath} from "../../constants/path";
import {validatorHOC} from "../HOC/ValidatorHOC";
const getErrorContainerStyle = validOrNotDirty => (validOrNotDirty ? "" : "postal-code-input-container--error");
const PostalCodeInput = props => {
    const [isFocus, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const {formData} = props;
    const focus = isFocus || inputValue.length > 0 || !isValidOrNotDirty(isValid, isDirty) ? "--focused" : "";
    const visibility = inputValue.length === 6 ? "--visible" : "--invisible";
    const showErrorLabel = isValidOrNotDirty(isValid, isDirty) ? "" : "textInputLabel--error";
    const isPostalServiceDown = props.state.getIn(postalCodeServiceStatusPath(), false);
    const isTriesExceeded = props.state.getIn(postalCodeCountExceededPath(props.inputID), false);
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirty(isValid, isDirty));
    const tick = <img className={`textInputTick${visibility}`} src={`${FORMS_CNFG_GLBL.imgPath}/Tick.svg`} alt='tick' />;
    const cross = <img className={"textInputTick--visible"} src={`${FORMS_CNFG_GLBL.imgPath}/cross.svg`} alt='tick' />;
    const spinner = (
        <img
            className='postal-code--spinner'
            src={`${FORMS_CNFG_GLBL.imgPath}/loading_icon_dark.svg`}
            alt='primary-button'
        />
    );
    const validator = (value, validators) => validators.map(x => x(formData)).map(x => x(value)).filter(x => !x.status);
    const isLoading = props.state.getIn(postalCodeSpinnerPath(), false);
    // const check_value = e =>
    //     e.target.value.length === 6
    //         ? props.dispatchEditPostCode(props.inputID, e.target.value, true, props.step) &&
    //               props.dispatchGetAddress(e.target.value, props.inputID)
    //         : props.dispatchEditPostCode(props.inputID, e.target.value, false, props.step);

    const handleOnChange = e => {
        const value = e.target.value;
        props.dispatchEditPostCode(props.inputID, value, validator(value, props.validators), props.step);
        return value.length === 6 && !isPostalServiceDown && !isTriesExceeded && props.dispatchGetAddress(e.target.value, props.inputID);
    };
    return (
        <div
            id={props.inputID}
            className={`postal-code-input--container ${errorContainerStyle}`}
            onFocus={() => {
                props.dispatchOnFocusInput(props.step, props.inputID, true);
            }}
            onBlur={() => {
                props.dispatchOnBlurInput(props.step, props.inputID, false);
            }}
        >
            <div className='postal-code-input--content'>
                <label className={`textInputLabel${focus} ${showErrorLabel}`}>
                    {`${props.inputLabel}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                </label>
                <input
                    value={inputValue}
                    onChange={handleOnChange}
                    type='text'
                    className={`postal-code-input--input${focus}`}
                />
            </div>
            {isLoading && spinner}
            {!isLoading && (isValidOrNotDirty(isValid, isDirty) ? tick : cross)}
        </div>
    );
};

PostalCodeInput.propTypes = {
    state: ImmutablePropTypes.map,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string,
    dispatchOnToggle: PropTypes.func,
    dispatchOnBlurInput: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    inputLabel: PropTypes.string
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchGetAddress: bindActionCreators(getAddress, dispatch),
        dispatchEditPostCode: bindActionCreators(editPostalCode, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(PostalCodeInput));



