import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {addValidFlag} from "../../../actions/actionCreators";

import TextInput from "../../TextInput/TextInput";
import ReadOnlyTextInput from "../../ReadOnlyTextInput/ReadOnlyTextInput";
import CpfTable from "../../CpfTable/CpfTable";

import Dropdown from "../../Dropdown/Dropdown";

import {
    NATURE_OF_EMPLOYMENT,
    NAME_OF_EMPLOYER,
    INDUSTRY,
    ANNUAL_INCOME,
    COMPANY,
    OCCUPATION,
    LENGTH_OF_EMPLOYMENT_YEARS,
    LENGTH_OF_EMPLOYMENT_MONTHS,
    IS_SG,
    EPASS_EXPIRY,
    ASSESS_YEAR,
    TYPE_OF_WORK_PASS
} from "../../../constants/inputsConstant";

import {ANUAL_INCOME_CARDS_TYPE, MIN_ANUAL_INCOME} from "../../../constants/common";

import {
    validateFilledInput,
    checkMaxSize,
    checkNumbers,
    checkMaxMonths,
    checkDateOfBirthFormat,
    checkDateIsOverTodayDate,
    checkDateFormat,
    checkAnualIncome,
    expiredMonth
} from "../../../validations/inputValidation";

import "./WorkInfo.css";

import {
    uiStepPath,
    readOnlyPath,
    productIdPath,
    cpfDataPath,
    dataPath,
    annualIncomeNotAvailFlagPath,
    myInfoFlagPath
} from "../../../constants/path";

import {
  sellingCards,
  sellingCashPlus,
  sellinBalanceTransfer,
  sellinPersonalLoan
} from "../../../utils";

import {List} from "immutable";

const CardsVariation = props => {
    const {FormConfig} = props;
    const localAddress = new FormConfig(props.step);
    const {inputLabels} = localAddress;
    const statusOfEmployment = props.state.getIn(["data", NATURE_OF_EMPLOYMENT], "NOINC");
    const isUnemployed = statusOfEmployment === "NOINC" || statusOfEmployment === "" || statusOfEmployment === "STUDT";
    return (
        <div>
            {!isUnemployed && (
                <div className='work-info--separator'>
                    <div className='work-info--length-of-employment-container'>
                        <div style={{flex: "1"}}>
                            <TextInput
                                {...props}
                                inputLabel={inputLabels[LENGTH_OF_EMPLOYMENT_YEARS]}
                                inputID={LENGTH_OF_EMPLOYMENT_YEARS}
                                validators={[validateFilledInput, checkNumbers, checkMaxSize(2)]}
                            />
                        </div>
                        <div className='work-info--length-of-employment-dash'> -</div>
                        <div style={{flex: "1"}}>
                            <TextInput
                                {...props}
                                inputLabel={inputLabels[LENGTH_OF_EMPLOYMENT_MONTHS]}
                                inputID={LENGTH_OF_EMPLOYMENT_MONTHS}
                                validators={[validateFilledInput, checkNumbers, checkMaxMonths, checkMaxSize(2)]}
                            />
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

import Accordian from "../../Accordian/Accordian";

class WorkInfo extends Component {
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const structure = props.state.getIn(uiStepPath(props.step));
        const workInfo = new FormConfig(props.step);
        const {inputLabels, title, subtitle, inputValues} = workInfo;
        const [postMessage, postMessageContent] = workInfo.getPostMessage();
        const isNameOfEmployerReadOnly = props.state.getIn([...readOnlyPath(), NAME_OF_EMPLOYER]);
        const productIds = props.state.getIn(productIdPath(), List());
        const cpfData = props.state.getIn(cpfDataPath(), List());
        const natureOfEmployment = props.state.getIn(dataPath([NATURE_OF_EMPLOYMENT]), "");
        const isSelfEmployed = natureOfEmployment === "SELFE";
        const isSalaried = natureOfEmployment === "salaried";
        const isAnnualIncomeNotAvail = props.state.getIn(annualIncomeNotAvailFlagPath(), false);
        const isSG = props.state.getIn(dataPath([IS_SG]), true);
        const assessYear = props.state.getIn(dataPath([ASSESS_YEAR]));
        const isMyInfoFlow = props.state.getIn(myInfoFlagPath());
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <div>
                    <Dropdown
                        inputLabel={inputLabels[NATURE_OF_EMPLOYMENT]}
                        inputID={NATURE_OF_EMPLOYMENT}
                        dropdownItems={inputValues[NATURE_OF_EMPLOYMENT]}
                        {...props}
                    />
                </div>
                {!isSG &&
                    (isSelfEmployed || isSalaried) && (
                        <div className='work-info--separator'>
                            <TextInput
                                inputLabel={inputLabels.ePassExpiry}
                                inputID={EPASS_EXPIRY}
                                validators={[validateFilledInput, checkDateOfBirthFormat, checkDateFormat, checkDateIsOverTodayDate, expiredMonth(6)]}
                                {...props}
                                dateFormat
                            />
                        </div>
                    )}
                {structure.has(OCCUPATION) ? (
                    <div className='work-info--separator'>
                        <Dropdown
                            inputLabel={inputLabels[OCCUPATION]}
                            inputID={OCCUPATION}
                            dropdownItems={inputValues[OCCUPATION]}
                            {...props}
                        />
                    </div>
                ) : null}
                {structure.has(NAME_OF_EMPLOYER) || isNameOfEmployerReadOnly ? (
                    <div className='work-info--separator'>
                        <TextInput
                            inputLabel={isSelfEmployed ? inputLabels[COMPANY] : inputLabels[NAME_OF_EMPLOYER]}
                            inputID={NAME_OF_EMPLOYER}
                            readOnly={isNameOfEmployerReadOnly}
                            validators={[validateFilledInput, checkMaxSize(35)]}
                            {...props}
                        />
                    </div>
                ) : null}
                {structure.has(INDUSTRY) ? (
                    <div className='work-info--separator'>
                        <Dropdown
                            inputLabel={inputLabels[INDUSTRY]}
                            inputID={INDUSTRY}
                            dropdownItems={inputValues[INDUSTRY]}
                            {...props}
                        />
                    </div>
                ) : null}
                {
                    (!isSG && !isMyInfoFlow) &&
                    <div className='work-info--separator'>
                        <Dropdown
                            inputLabel={inputLabels[TYPE_OF_WORK_PASS]}
                            inputID={TYPE_OF_WORK_PASS}
                            dropdownItems={inputValues[TYPE_OF_WORK_PASS]}
                            {...props}
                        />
                    </div>
                }
                <div className='work-info--separator'>
                    {!isAnnualIncomeNotAvail ? (
                        <TextInput
                            validators={[validateFilledInput, checkNumbers, checkAnualIncome(ANUAL_INCOME_CARDS_TYPE, productIds.first(), MIN_ANUAL_INCOME), checkMaxSize(7)]}
                            inputLabel={assessYear && assessYear !== "" ? `${inputLabels[ANNUAL_INCOME]} for ${assessYear}` : inputLabels[ANNUAL_INCOME]}
                            inputID={ANNUAL_INCOME}
                            {...props}
                        />
                    ) : (
                        <ReadOnlyTextInput
                            inputLabel={inputLabels[ANNUAL_INCOME]}
                            value={inputLabels["notAvailable"]}
                            {...props}
                        />
                    )}
                </div>

                {sellingCards(productIds, productsConfig) ||
                sellingCashPlus(productIds, productsConfig) ||
                sellinBalanceTransfer(productIds, productsConfig) ||
                sellinPersonalLoan(productIds, productsConfig) ? (
                    <CardsVariation {...props} />
                ) : null}

                {cpfData.count() ? (
                    <div className='basicInfoSeparator'>
                        <CpfTable
                            colTitles={["Date", "Amount", "Month", "Employer"]}
                            tableContent={cpfData.toJS()}
                            {...props}
                        />
                    </div>
                ) : null}

                {postMessage && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>
                )}
            </div>
        );
    }
}

CardsVariation.propTypes = {
    FormConfig: PropTypes.func.isRequired,
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired
};

WorkInfo.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(WorkInfo);



