import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import "./FileUploadDefault.css";
import {uploadFile, updateUploadProgress, showErrorMsg, errorUploadFile, removeValidFlag} from "../../actions/actionCreators";
import {uiUploadStatusPath, uiUploadProgressPath, uploadFileSizePath} from "../../constants/path";
import {bytesToSize, getInputStates, isValidOrNotDirty} from "../../utils";
import {validatorHOC} from "../HOC/ValidatorHOC";

const getErrorContainerStyle = validOrNotDirty => (validOrNotDirty ? "" : "file-upload--container--error");
const FileUploadDefault = props => {
    const [, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const showErrorLabel = isValid || !isDirty ? "" : "file-upload--error";
    const fileSize = props.state.getIn(uploadFileSizePath(props.inputID));
    const fileSizeString = fileSize ? " - " + fileSize : "";
    const descriptionContent = inputValue ? inputValue.replace(/^.*[\\\/]/, "") + fileSizeString : props.description;
    const checkInputStatus = props.state.getIn(uiUploadStatusPath(props.step, props.inputID), "none");
    const isFileSelected = checkInputStatus === "uploading" ? "--selected" : "";
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirty(isValid, isDirty));
    const inputProgressValue = props.state.getIn(uiUploadProgressPath(props.step, props.inputID), 0);
    let fileInputRef = null;
    const checkFileExtension = (file, allowedExtensions) => allowedExtensions.includes(file.split(".").pop());

    const handleOnChange = () => {
        const file = fileInputRef.files[0];
        const extensionIsAllowed = checkFileExtension(file.name, ["jpg", "png", "pdf", "JPG", "PNG", "PDF"]);
        if (extensionIsAllowed) {
            if (file.size <= 1000000) {
                props.dispatchupdateUploadProgress(props.step, props.inputID, 100);
                props.dispatchUploadFile(props.step, props.inputID, file.name, bytesToSize(file.size), []);
            } else {
                props.dispatchShowErrorMsg(props.step, props.inputID, "file size cannot exceed 1MB");
                props.dispatch(removeValidFlag(props.step, props.inputID));
            }
        } else {
            props.dispatchShowErrorMsg(props.step, props.inputID, "can only be either jpg, png or pdf");
            props.dispatch(removeValidFlag(props.step, props.inputID));
        }
    };
    const fileUploadContainer = () => {
        return (
            <div className={"file-upload-button" + isFileSelected}>
                <progress className='file-upload-progress' max='100' value={inputProgressValue} />
                <div className='file-upload-button--text'>
                    {checkInputStatus === "none" ? "UPLOAD" : "EDIT"}
                </div>
                <input
                    accept='.jpg,.JPG,.png,.pdf,.PDF,.PNG'
                    id ={`file${props.inputID}`}
                    ref={i => {
                        fileInputRef = i;
                    }}
                    type='file'
                    className='file-upload-input'
                    onClick={() => {
                        fileInputRef.value = null;
                        props.dispatchupdateUploadProgress(props.step, props.inputID, 0);
                    }}
                    onChange={handleOnChange}
                />
            </div>
        );
    };
    return (
        <div id={props.inputID} className={`file-upload--container ${errorContainerStyle}`}>
            <div className='file-upload--text-container'>
                <div className={"file-upload--title " + showErrorLabel}>
                    {`${props.title}${isValid || !isDirty ? "" : ` ${errorMsg}`}`}
                </div>
                <div className='file-upload--description'>
                    {descriptionContent}
                </div>
            </div>
            <div className='file-upload--button-container'>
                {fileUploadContainer()}
            </div>
        </div>
    );
};

FileUploadDefault.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    dispatchUploadFile: PropTypes.func.isRequired,
    dispatchShowErrorMsg: PropTypes.func.isRequired,
    dispatchupdateUploadProgress: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchUploadFile: bindActionCreators(uploadFile, dispatch),
        dispatchupdateUploadProgress: bindActionCreators(updateUploadProgress, dispatch),
        dispatchShowErrorMsg: bindActionCreators(showErrorMsg, dispatch),
        dispatchErrorUploadFile: bindActionCreators(errorUploadFile, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(FileUploadDefault));



