/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";

import {bindActionCreators} from "redux";
import AvatarEditor from "react-avatar-editor";
import elementResizeEvent from "element-resize-event";

import Accordian from "../Accordian/Accordian";

import "./Signature.css";
import {
    uploadFile,
    setSignatureImageContent,
    setSignatureTmpImagContent,
    setSignatureImageRotation,
    setSignatureImageScale,
    setSignatureImageWidth,
    setSignatureImageHeight,
    showErrorMsg,
    updateUploadProgress,
    setSignatureStatusToTrue,
    errorUploadFile
} from "../../actions/actionCreators";
import {
    uiUploadStatusPath,
    uiUploadProgressPath,
    uiSignatureImageContentPath,
    uiSignatureImageScalePath,
    uiSignatureImageRotationPath,
    uiSignatureImageTmpContentPath,
    uiSignatureImageHeightPath,
    uiSignatureImageWidthPath
} from "../../constants/path";
import {getInputStates, fileUploadHelper, isValidOrNotDirty} from "../../utils";
import {fileUploadUrl} from "../../api/api";
import {validatorHOC} from "../HOC/ValidatorHOC";

let fileInputRef = null;
let avatarEditor = null;
let rtime;
let timeout = false;
let delta = 200;

const ButtonBar = props => {
    const tmpImg = props.state.getIn(uiSignatureImageTmpContentPath(props.step, props.inputID), "");
    const uploadInProgress = props.state.getIn(["app", props.step, props.inputID], false);
    return (
        <div className='signature--avatar-editor-buttons-bar'>
            <div
                className='signature--avatar-editor-button'
                value='scale up'
                onClick={() => {
                    return props.scale < 5
                        ? props.dispatchSetSignatureImageScale(props.step, props.inputID, props.scale + 0.25)
                        : null;
                }}
            >
                <img
                    className='signature--avatar-editor-button-icon'
                    src={`${FORMS_CNFG_GLBL.imgPath}/icon-zoom-in.svg`}
                    alt='zoomInIcon'
                />
            </div>
            <div
                className='signature--avatar-editor-button'
                value='scale down'
                onClick={() => {
                    return props.scale > 1
                        ? props.dispatchSetSignatureImageScale(props.step, props.inputID, props.scale - 0.25)
                        : null;
                }}
            >
                <img
                    className='signature--avatar-editor-button-icon'
                    src={`${FORMS_CNFG_GLBL.imgPath}/icon-zoom-out.svg`}
                    alt='zoomOutIcon'
                />
            </div>
            <div
                className='signature--avatar-editor-button'
                value='rotate left'
                onClick={() => {
                    return props.rotation === -360
                        ? props.dispatchSetSignatureImageRotation(props.step, props.inputID, -90)
                        : props.dispatchSetSignatureImageRotation(props.step, props.inputID, props.rotation - 90);
                }}
            >
                <img
                    className='signature--avatar-editor-button-icon'
                    src={`${FORMS_CNFG_GLBL.imgPath}/icon-rotate-left.svg`}
                    alt='rotateLeftIcon'
                />
            </div>
            <div
                className='signature--avatar-editor-button'
                value='rotate right'
                onClick={() => {
                    return props.rotation === 360
                        ? props.dispatchSetSignatureImageRotation(props.step, props.inputID, 90)
                        : props.dispatchSetSignatureImageRotation(props.step, props.inputID, props.rotation + 90);
                }}
            >
                <img
                    className='signature--avatar-editor-button-icon'
                    src={`${FORMS_CNFG_GLBL.imgPath}/icon-rotate-right.svg`}
                    alt='rotateRightIcon'
                />
            </div>
            <div className='signature--avatar-editor-right-side-buttons-wrapper-flex'>
                <div className={uploadInProgress ? "signature--right-side-button-progress" : "signature--right-side-button"} onClick={props.handleCrop}>
                    Save
                </div>
                <div
                    className='signature--right-side-button-undo'
                    onClick={() => {
                        props.dispatchSetSignatureImageRotation(props.step, props.inputID, 0);
                        props.dispatchSetSignatureImageScale(props.step, props.inputID, 1);
                        props.dispatchSetSignatureImageContent(props.step, props.inputID, tmpImg);
                    }}
                >
                    Clear
                </div>
            </div>
        </div>
    );
};

ButtonBar.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired,
    dispatchSetSignatureImageRotation: PropTypes.func.isRequired,
    dispatchSetSignatureImageScale: PropTypes.func.isRequired,
    dispatchSetSignatureImageContent: PropTypes.func.isRequired,
    handleCrop: PropTypes.func.isRequired,
    dispatchShowErrorMsg: PropTypes.func.isRequired,
    dispatchUploadFile: PropTypes.func.isRequired
};

const getShowErrorLabel = (isValid, isDirty) => (isValidOrNotDirty(isValid, isDirty) ? "" : "file-upload--error");
const getDescriptionContent = (inputValue, description) =>
    inputValue ? inputValue.replace(/^.*[\\\/]/, "") : description;
const getIsFileSelected = checkInputStatus => (checkInputStatus === "uploading" ? "--selected" : "");
const getVisibility = img => (img === "" ? "--hidden" : "");
const getBrowseText = inputValue => (inputValue ? "BROWSE AGAIN" : "BROWSE");
const getCanvasWidth = (editorFrameDiv, state, step, inputID) =>
    editorFrameDiv
        ? state.getIn(uiSignatureImageWidthPath(step, inputID), editorFrameDiv.offsetWidth)
        : state.getIn(uiSignatureImageWidthPath(step, inputID), 600);
const spinner = (
    <img
        className='signature-progress--spinner'
        src={`${FORMS_CNFG_GLBL.imgPath}/loading_icon_dark.svg`}
        alt='primary-button'
    />
);
const Signature = props => {
    const [, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const showErrorLabel = getShowErrorLabel(isValid, isDirty);
    const descriptionContent = getDescriptionContent(inputValue, props.description);
    const checkInputStatus = props.state.getIn(uiUploadStatusPath(props.step, props.inputID), "none");
    const isFileSelected = getIsFileSelected(checkInputStatus);
    const inputProgressValue = props.state.getIn(uiUploadProgressPath(props.step, props.inputID), 0);
    const img = props.state.getIn(uiSignatureImageContentPath(props.step, props.inputID), "");
    const rotation = props.state.getIn(uiSignatureImageRotationPath(props.step, props.inputID), 0);
    const scale = props.state.getIn(uiSignatureImageScalePath(props.step, props.inputID), 1);
    const visibility = getVisibility(img);
    const uploadInProgress = props.state.getIn(["app", props.step, props.inputID], false);
    const editorFrameDiv = document.getElementById("avatar-editor-frame-div");
    const canvasWidth = getCanvasWidth(editorFrameDiv, props.state, props.step, props.inputID);
    const canvasHeight = props.state.getIn(uiSignatureImageHeightPath(props.step, props.inputID), 200);
    const browseText = uploadInProgress ? "" : getBrowseText(inputValue);

    const {FormConfig, formData} = props;
    const fileUploadErrorMsg = props.formData["formInputErrorMessages"]["fileUploadError"];
    const signature = new FormConfig(props.step);
    const {termsAndConditions} = signature;
    const errorContainerStyle = isValidOrNotDirty(isValid, isDirty) ? "" : "signature--main-container--error";
    const MAX_QUALITY_IMAGE = 0.9;
    const TYPE_IMAGE = "image/jpeg";
    const handleCrop = () => {
        if (uploadInProgress) {
            return;
        }
        const fileData = avatarEditor.getImage().toDataURL();
        const actions = {
            successDispatch: () => props.dispatchSetSignatureStatusToTrue(props.step, props.inputID),
            errorDispatch: () => props.dispatchErrorUploadFile(fileUploadErrorMsg),
            progressDispatch: progress => props.dispatchupdateUploadProgress(props.step, props.inputID, progress)
        };
        props.dispatchSetSignatureImageContent(props.step, props.inputID, fileData);
        props.dispatchSetSignatureImageRotation(props.step, props.inputID, 0);
        props.dispatchSetSignatureImageScale(props.step, props.inputID, 1);
        avatarEditor.getImage().toBlob(function (blob) {
            fileUploadHelper(blob, fileUploadUrl(), props.inputID, actions);
        },TYPE_IMAGE, MAX_QUALITY_IMAGE);
    };

    const resizeend = () => {
        if (new Date() - rtime < delta) {
            setTimeout(resizeend, delta);
        } else {
            timeout = false;
            props.dispatchSetSignatureImageWidth(props.step, props.inputID, editorFrameDiv.offsetWidth);
            //  props.dispatchSetSignatureImageHeight(props.step, props.inputID, editorFrameDiv.offsetHeight);
        }
    };

    if (editorFrameDiv) {
        elementResizeEvent(editorFrameDiv, () => {
            rtime = new Date();
            if (timeout === false) {
                timeout = true;
                setTimeout(resizeend, delta);
            }
        });
    }

    const setEditorRef = editor => {
        avatarEditor = editor;
    };
    const checkFileExtension = (file, allowedExtensions) => allowedExtensions.includes(file.split(".").pop());

    const handleOnChange = () => {
        let file = fileInputRef.files[0];
        const extensionIsAllowed = checkFileExtension(file.name, ["jpg", "png", "JPG", "PNG"]);
        if (!extensionIsAllowed) {
            return props.dispatchShowErrorMsg(props.step, props.inputID, "can only be either jpg or png");
        }
        if (file.size > 1000000) {
            return props.dispatchShowErrorMsg(props.step, props.inputID, "file size cannot exceed 1MB");
        }
        let read = new window.FileReader();
        fileInputRef = file;
        read.readAsDataURL(fileInputRef);
        read.onloadend = function () {
            props.dispatchSetSignatureImageContent(props.step, props.inputID, read.result);
            props.dispatchSetSignatureTmpImageContent(props.step, props.inputID, read.result);
            props.dispatchSetSignatureImageRotation(props.step, props.inputID, 0);
            props.dispatchSetSignatureImageScale(props.step, props.inputID, 1);
        };
        return props.dispatchUploadFile(props.step, props.inputID, file.name, file.size, [
            {status: false, errorMsg: formData.formInputErrorMessages.signatureConfirmToUpload}
        ]);
    };

    const handleFileSelectOnClick = (e) => {
        if (uploadInProgress) {
            e.preventDefault();
            return;
        }
    };

    const fileUploadContainer = () => {
        return (
            <div className={"signature-button " + isFileSelected}>
                <progress className='signature-progress' max='100' value={inputProgressValue} />
                <div className={uploadInProgress ? "signature-button--text-progress" : "signature-button--text"}>
                    {uploadInProgress && spinner}
                    {browseText}
                </div>
                <input
                    ref={i => {
                        fileInputRef = i;
                    }}
                    type='file'
                    className={uploadInProgress ? "signature-input-progress" : "signature-input"}
                    onClick={handleFileSelectOnClick}
                    onChange={handleOnChange}
                />
            </div>
        );
    };

    const updateReferencial = () => rotation === -270 || rotation === -90 || rotation === 270 || rotation === 90;

    return (
        <div id={props.inputID} className={`signature--main-container-flex ${errorContainerStyle}`}>
            <div className='signature--container'>
                <div className='signature--text-container'>
                    <div className={"signature--title " + showErrorLabel}>
                        <p>{`${props.title}`}</p>{`${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                    </div>
                    <div className='signature--description'>
                        {descriptionContent}
                        {isValid && isDirty && <b> Uploaded </b>}
                        {isValid && isDirty && <img src={`${FORMS_CNFG_GLBL.imgPath}/Tick.svg`} alt='tick' />}
                    </div>
                </div>
                <div className='signature--button-container'>
                    {fileUploadContainer()}
                </div>
            </div>
            <div className={"signature--avatar-editor-main-container" + visibility}>
                <div id='avatar-editor-frame-div' className='signature--avatar-editor-frame-container'>
                    <AvatarEditor
                        ref={setEditorRef}
                        image={img}
                        height={updateReferencial() ? canvasWidth - 0 : canvasHeight - 0}
                        width={updateReferencial() ? canvasHeight - 0 : canvasWidth - 0}
                        border={1}
                        borderRadius={2}
                        onSave={handleCrop}
                        color={[0, 0, 0, 0.3]} // RGBA
                        scale={parseFloat(scale)}
                        rotate={rotation}
                    />
                </div>
                <ButtonBar
                    scale={scale}
                    rotation={rotation}
                    dispatchSetSignatureImageRotation={props.dispatchSetSignatureImageRotation}
                    dispatchSetSignatureImageScale={props.dispatchSetSignatureImageScale}
                    dispatchSetSignatureImageContent={props.dispatchSetSignatureImageContent}
                    dispatchShowErrorMsg={props.dispatchShowErrorMsg}
                    dispatchUploadFile={props.dispatchUploadFile}
                    handleCrop={handleCrop}
                    {...props}
                />
                <div className='draw-signature--separator' />
                <Accordian
                    {...props}
                    title={termsAndConditions["signature"]}
                    content={termsAndConditions["signatureContent"]}
                    inputID={"default"}
                />
            </div>
        </div>
    );
};

Signature.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    dispatchSetSignatureImageRotation: PropTypes.func.isRequired,
    dispatchSetSignatureImageScale: PropTypes.func.isRequired,
    dispatchSetSignatureImageContent: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    FormConfig: PropTypes.func.isRequired,
    dispatchShowErrorMsg: PropTypes.func.isRequired,
    dispatchUploadFile: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchSetSignatureImageContent: bindActionCreators(setSignatureImageContent, dispatch),
        dispatchSetSignatureImageScale: bindActionCreators(setSignatureImageScale, dispatch),
        dispatchSetSignatureImageRotation: bindActionCreators(setSignatureImageRotation, dispatch),
        dispatchSetSignatureImageWidth: bindActionCreators(setSignatureImageWidth, dispatch),
        dispatchSetSignatureImageHeight: bindActionCreators(setSignatureImageHeight, dispatch),
        dispatchSetSignatureTmpImageContent: bindActionCreators(setSignatureTmpImagContent, dispatch),
        dispatchUploadFile: bindActionCreators(uploadFile, dispatch),
        dispatchShowErrorMsg: bindActionCreators(showErrorMsg, dispatch),
        dispatchupdateUploadProgress: bindActionCreators(updateUploadProgress, dispatch),
        dispatchSetSignatureStatusToTrue: bindActionCreators(setSignatureStatusToTrue, dispatch),
        dispatchErrorUploadFile: bindActionCreators(errorUploadFile, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(Signature));



