/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";
import SummaryBlockToggleElement from "../components/SummaryBlockToggleElement/SummaryBlockToggleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";

import {ui, dataPath, isFinalSubmitPath} from "../../../../constants/path";
import {
    UPLOAD_BILLING_PROOF,
    UPLOAD_NRIC_FRONT,
    UPLOAD_NRIC_BACK,
    UPLOAD_PASSPORT,
    UPLOAD_PASSPORT_BACK,
    UPLOAD_INCOME_PROOF,
    UPLOAD_WORK_PERMIT,
    SUBMIT_CPF_ONLINE,
    NRIC_ISSUE_DATE,
    UPLOAD_EMPLOYMENT_PROOF
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";
import ImmutablePropTypes from "react-immutable-proptypes";

const getDataFromState = state => ({
    billingProof: state.getIn(dataPath([UPLOAD_BILLING_PROOF])),
    nricFront: state.getIn(dataPath([UPLOAD_NRIC_FRONT])),
    nricBack: state.getIn(dataPath([UPLOAD_NRIC_BACK])),
    passport: state.getIn(dataPath([UPLOAD_PASSPORT])),
    passportBack: state.getIn(dataPath([UPLOAD_PASSPORT_BACK])),
    incomeDocuments: state.getIn(dataPath([UPLOAD_INCOME_PROOF])),
    submitCpf: state.getIn(dataPath([SUBMIT_CPF_ONLINE])),
    workPermit: state.getIn(dataPath([UPLOAD_WORK_PERMIT])),
    nricIssueDate: state.getIn(dataPath([NRIC_ISSUE_DATE])),
    employmentProof: state.getIn(dataPath([UPLOAD_EMPLOYMENT_PROOF]))
});

const SummaryBlockUploadDocuments = props => {
    const {billingProof, nricFront, nricBack, passport, passportBack, incomeDocuments, submitCpf, workPermit, nricIssueDate, employmentProof} = getDataFromState(
        props.state
    );

    const {FormConfig} = props;
    const {title, inputLabels} = new FormConfig("uploadDocuments");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);

    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{title}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                "uploadDocuments"
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>
            {/* CONTENT */}
            <div className='summary--block-content-container'>
                <div className='summary--block-content-container-flex'>
                    {billingProof && (
                        <SummaryBlockSimpleElement label={inputLabels[UPLOAD_BILLING_PROOF]} value={billingProof} />
                    )}
                    {nricFront && <SummaryBlockSimpleElement label={inputLabels[UPLOAD_NRIC_FRONT]} value={nricFront} />}
                    {nricBack && <SummaryBlockSimpleElement label={inputLabels[UPLOAD_NRIC_BACK]} value={nricBack} />}
                    {nricIssueDate && <SummaryBlockSimpleElement label={inputLabels[NRIC_ISSUE_DATE]} value={nricIssueDate} />}
                    {passport && <SummaryBlockSimpleElement label={inputLabels[UPLOAD_PASSPORT]} value={passport} />}
                    {passportBack && <SummaryBlockSimpleElement label={inputLabels[UPLOAD_PASSPORT_BACK]} value={passportBack} />}
                    {workPermit && (
                        <SummaryBlockSimpleElement label={inputLabels[UPLOAD_WORK_PERMIT]} value={workPermit} />
                    )}
                    {submitCpf && (
                        <SummaryBlockToggleElement
                            label={inputLabels[SUBMIT_CPF_ONLINE]}
                            value={submitCpf ? "Yes" : "No"}
                        />
                    )}
                    {incomeDocuments ? (
                        <SummaryBlockSimpleElement label={inputLabels[UPLOAD_INCOME_PROOF]} value={incomeDocuments} />
                    ) : null}
                    {employmentProof && <SummaryBlockSimpleElement label={inputLabels[UPLOAD_EMPLOYMENT_PROOF]} value={employmentProof} />}
                </div>
            </div>
        </div>
    );
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

SummaryBlockUploadDocuments.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    FormConfig: PropTypes.func.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

export default connect(null, mapDispatchToProps)(SummaryBlockUploadDocuments);



