import React, {Component, PropTypes} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {getOTPPrefix, addValidFlag, onFocusInput} from "../../../actions/actionCreators";
import {otpPrefixPath} from "../../../constants/path";
import PrependTextInput from "../../PrependTextInput/PrependTextInput";

import {validateFilledInput, checkSize, checkNumbers} from "../../../validations/inputValidation";

import {OTP} from "../../../constants/inputsConstant";

import "./Otp.css";

import Accordian from "../../Accordian/Accordian";

class Otp extends Component {
    componentDidMount() {
        this.props.dispatchOnFocusInput(this.props.step, OTP, true);
    }
    render() {
        const props = this.props;
        const {FormConfig} = props;
        const prefix = props.state.getIn(otpPrefixPath(), "");
        const otp = new FormConfig(props.step);
        const {inputLabels, title, subtitle} = otp;
        const [postMessage, postMessageContent] = otp.getPostMessage();

        return (
            <div className='otp-container'>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>
                    {subtitle}
                </p>
                <div>
                    <PrependTextInput
                        inputLabel={inputLabels[OTP]}
                        inputID={OTP}
                        prefix={prefix}
                        validators={[validateFilledInput, checkSize(6), checkNumbers]}
                        readOnly={!prefix}
                        isLoading={!prefix}
                        {...props}
                    />
                </div>
                <p className='otpExpireMsg'>This otp will expire after 180 seconds.</p>
                <p className='otpDisclaimer'>
                    Did not receive ?{" "}
                    <span
                        className='otpUnderline'
                        onClick={() => {
                            props.dispatchGetOTPPrefix();
                        }}
                    >
                        Resend OTP
                    </span>.
                </p>
                {postMessage &&
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>}

            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        dispatchGetOTPPrefix: bindActionCreators(getOTPPrefix, dispatch),
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch),
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch)
    };
};

Otp.propTypes = {
    dispatchOnFocusInput: PropTypes.func.isRequired,
    step: PropTypes.string.isRequired
};

export default connect(null, mapDispatchToProps)(Otp);



