import {ACTION_POST_PARTIAL_APPLICATION_SUCCESS} from "../constants/actionsConstant";
import {getOTPPrefix} from "../actions/actionCreators";
import {myInfoFlagPath} from "../constants/path";

const getOTPMiddleware = ({dispatch, getState}) => next => action => {
    const isMyInfoFlow = getState().state.getIn(myInfoFlagPath(), false);
    if (action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && !isMyInfoFlow && action.nextStep === "otp") {
        const mobileNumber = getState().state.getIn(["data", "mobileNumber"]);
        dispatch(getOTPPrefix(mobileNumber));
    }
    return next(action);
};
export default getOTPMiddleware;



