import React, {PropTypes} from "react";
import {Provider} from "react-redux";

// import AppRouter from './AppRouter'
import store from "../store";

import {Router, Route, IndexRoute} from "react-router";

import StateToProps from "./StateToProps";
import Form from "./Form";
import Congratulation from "./steps/Congratulation/Congratulation";
import NotFound from "./steps/NotFound/NotFound";
import {history} from "../store";
import {detectPathName} from "../utils";
require("smoothscroll-polyfill").polyfill();
const pathname = detectPathName();

const App = props => {
    return (
        <Provider store={store}>
            <Router history={history}>
                <Route path={`${pathname}congratulation`} component={StateToProps}>
                    <IndexRoute
                        component={Congratulation({FormConfig: props.FormConfig, productsConfig: props.productConfig})}
                    />
                </Route>
                <Route path={`${pathname}error`} component={StateToProps}>
                    <IndexRoute component={NotFound({FormConfig: props.FormConfig})} />
                </Route>
                <Route path={pathname} component={StateToProps}>
                    <IndexRoute
                        component={Form({
                            FormConfig: props.FormConfig,
                            productsConfig: props.productConfig,
                            formData: props.formData
                        })}
                    />
                    <Route
                        path='/:pid'
                        component={Form({
                            FormConfig: props.FormConfig,
                            productsConfig: props.productConfig,
                            formData: props.formData
                        })}
                    />
                </Route>
            </Router>
        </Provider>
    );
};

App.propTypes = {
    FormConfig: PropTypes.func.isRequired,
    productConfig: PropTypes.object.isRequired,
    formData: PropTypes.object.isRequired
};

export default App;



