import React, {PropTypes} from "react";
import {connect} from "react-redux";
import ImmutablePropTypes from "react-immutable-proptypes";
import {bindActionCreators} from "redux";

import {closeDialogModal} from "../../actions/actionCreators";
import "./DialogModal.css";

const DialogModal = props => {
    const {formData, dialogType} = props;
    return (
        <div className='dialog-back-container'>
            <div className='dialog-containter'>
                <div className='dialog-content-container'>
                    <div className='dialog-content'>
                        {formData[dialogType].content}
                    </div>
                    {formData[dialogType].primaryButton && <a href={formData[dialogType].primaryButton.link} className='dialog-primary-button'>
                        {formData[dialogType].primaryButton.text}
                    </a>}
                    {formData[dialogType].secondaryButton &&
                        <button
                            onClick={() => props.dispatchCloseDialogModal(dialogType)}
                            className='dialog-secondary-button'
                        >
                            {formData[dialogType].secondaryButton.text}
                        </button>}
                </div>
            </div>
        </div>
    );
};

DialogModal.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchCloseDialogModal: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    dialogType: PropTypes.string.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchCloseDialogModal: bindActionCreators(closeDialogModal, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(DialogModal);



