import React, {Component} from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import TextInput from "../../TextInput/TextInput";
import PasswordInput from "../../PasswordInput/PasswordInput";
import {addValidFlag, validateUsername, removeValidFlag} from "../../../actions/actionCreators";
import {
checkMaxSize,
checkMinSize,
validateFilledInput,
checkpassword,
validatePassword,
checkUsername,
validateConfirmPassword
} from "../../../validations/inputValidation";

import {
  appDataPath,
  dataPath,
  uiIsValidPath
} from "../../../constants/path";
import "./DigitalServices.css";

import {USERNAME,PASSWORD,CONFIRM_PASSWORD} from "../../../constants/inputsConstant";

class DigitalServices extends Component {
    render() {
        const props = this.props;
        const {FormConfig, formData} = props;
        const digitalServices = new FormConfig(props.step);
        const {inputLabels, title, subtitle} = digitalServices;
        const pwd = props.state.getIn(appDataPath([PASSWORD]), "");
        const confirmPwd = props.state.getIn(appDataPath([CONFIRM_PASSWORD]), "");
        const isPwdSame = confirmPwd === pwd;
        const username = props.state.getIn(dataPath([USERNAME]), "");
        const isUsernameValid = props.state.getIn(uiIsValidPath("digitalServices", "username"),false);
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <div>
                    <TextInput
                        inputLabel={inputLabels.username}
                        inputID={USERNAME}
                        upperCase={true}
                        validators={[validateFilledInput, checkMinSize(8), checkMaxSize(16), checkUsername]}
                        additionalOnBlur={() =>
                            isUsernameValid && username &&
                                      props.dispatch(validateUsername(username, formData))}
                        {...props}
                    />
                </div>
                <div className='basicInfoSeparator'>
                    <PasswordInput
                        inputLabel={inputLabels.password}
                        inputID={PASSWORD}
                        validators={[validateFilledInput, checkMinSize(8), checkMaxSize(16),
                                        validatePassword(username), checkpassword]}
                        additionalOnBlur={() => {
                            if (isPwdSame) {
                                props.dispatch(addValidFlag("digitalServices", CONFIRM_PASSWORD));
                            } else {
                                props.dispatch(removeValidFlag("digitalServices", CONFIRM_PASSWORD));
                            }
                        }
                        }
                        {...props}
                    />
                </div>

                <div className='basicInfoSeparator'>
                    <PasswordInput
                        inputLabel={inputLabels.confirmPassword}
                        inputID={CONFIRM_PASSWORD}
                        validators={[validateFilledInput, validateConfirmPassword(pwd)]}
                        {...props}
                    />
                </div>
            </div>
        );
    }
}

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(DigitalServices);



