import {isValidNumber} from "libphonenumber-js";
import {getErrorMsg} from "../utils";
import moment from "moment";
import {
  HOME_POSTAL_CODE,
  HOME_LEVEL,
  HOME_UNIT_NUMBER,
  MAILING_POSTAL_CODE,
  MAILING_LEVEL,
  MAILING_UNIT_NUMBER
} from "../constants/inputsConstant";

import {
    dataPath
} from "../constants/path";

const sumOfICWithWeightFactor = icNoArray => {
    const weight = [2, 7, 6, 5, 4, 3, 2];
    return icNoArray.map((x, i) => x * weight[i]).reduce((s, v) => s + v, 0);
};

const getICLastLetter = icArray => {
    const mapLetter = ["J", "A", "B", "C", "D", "E", "F", "G", "H", "I", "Z", "J"];
    const icNoArray = icArray.slice(1, -1);
    const total = icArray[0] === "S" ? sumOfICWithWeightFactor(icNoArray) : sumOfICWithWeightFactor(icNoArray) + 4;
    const checkDigit = 11 - total % 11;
    return mapLetter[checkDigit];
};

const validateNRIC = ic => {
    const icArray = ic.split("");
    const icLastLetter = getICLastLetter(icArray);
    const icRegExp = new RegExp(`[S|T][0-9]{7}[${icLastLetter}]`, "g");
    return icRegExp.test(ic);
};

const isNRIC = ic => {
    const firstLetter = ic.substr(0, 1);
    return ["S", "T"].indexOf(firstLetter) > -1;
};

const validateTNRIC = ic => {
    const icArray = ic.split("");
    const icLastLetter = getICLastLetter(icArray);
    const icRegExp = new RegExp(`[T][0-9]{7}[${icLastLetter}]`, "g");
    return icRegExp.test(ic);
};

export const validateNRICAndPassport = config => value => {
    let status = false;
    if (isNRIC(value)) {
        status = validateNRIC(value);
    }
    const errorMsg = status ? "" : getErrorMsg("invalidNRIC", config);
    return {status, errorMsg};
};
export const validateOnlYTNRIC = config => value => {
    const status = validateTNRIC(value);
    const errorMsg = status ? "" : getErrorMsg("invalidNRIC", config);
    return {status, errorMsg};
};
export const validatePhoneNumber = config => value => {
    const status = isValidNumber(value);
    const errorMsg = status ? "" : getErrorMsg("invalidPhone", config);
    return {status, errorMsg};
};
export const validateFilledInput = config => value => {
    const status = value.trim().length > 0;
    const errorMsg = status ? "" : getErrorMsg("fieldEmpty", config);
    return {status, errorMsg};
};

export const checkFullName = config => value => {
    const regex = /^([a-zA-Z ,.'-])+$/;

    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidName", config);

    return {status: testRegexp, errorMsg};
};

export const checkNumbers = config => value => {
    const regex = /^([0-9])+$/;
    const testRegexp = value.length === 0 || regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("numbersOnly", config);

    return {status: testRegexp, errorMsg};
};

export const checkAddress = config => value => {
    const regex = /^([0-9 a-zA-Z ,.'-])+$/;

    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidAddress", config);

    return {status: testRegexp, errorMsg};
};

export const checkEmail = config => value => {
    const regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidEmail", config);

    return {status: testRegexp, errorMsg};
};

export const checkDateOfBirthFormat = config => value => {
    const regex = /^(?:(?:31(\/|-|\.)(?:0?[13578]|1[02]))\1|(?:(?:29|30)(\/|-|\.)(?:0?[1,3-9]|1[0-2])\2))(?:(?:1[6-9]|[2-9]\d)?\d{2})$|^(?:29(\/|-|\.)0?2\3(?:(?:(?:1[6-9]|[2-9]\d)?(?:0[48]|[2468][048]|[13579][26])|(?:(?:16|[2468][048]|[3579][26])00))))$|^(?:0?[1-9]|1\d|2[0-8])(\/|-|\.)(?:(?:0?[1-9])|(?:1[0-2]))\4(?:(?:1[6-9]|[2-9]\d)\d{2})$/;
    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidDate", config);

    return {status: testRegexp, errorMsg};
};

export const checkDateFormat = config => value => {
    const regex = /(^\d{2}\/\d{2}\/\d{4}$)/;
    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidDate", config);

    return {status: testRegexp, errorMsg};
};

export const checkMaxSize = maxSize => config => value => {
    const status = value.length <= maxSize;
    const errorMsg = status ? "" : getErrorMsg("invalidMaxSize", config).replace("{size}", maxSize);

    return {status, errorMsg};
};

export const checkMinSize = minSize => config => value => {
    const status = value.length >= minSize;
    const errorMsg = status ? "" : getErrorMsg("invalidMinSize", config).replace("{size}", minSize);
    return {status, errorMsg};
};

export const compareWithDate = (date, title) => config => value => {
    let status = true;
    let errorMsg = "";
    if (date.length === 10 && value.length === 10) {
        status = moment(value, "DD-MM-YYYY") > moment(date, "DD-MM-YYYY");
        errorMsg = status ? "" : getErrorMsg("plMinAmount", config).replace("{minAmt}", title);
    }
    return {status, errorMsg};
};

export const checkSize = maxSize => config => value => {
    const status = value.length === maxSize;
    const errorMsg = status ? "" : getErrorMsg("exactSize", config).replace("{size}", maxSize);

    return {status, errorMsg};
};

/* eslint-disable */
export const checkMaxSizeComponent = (props, editInput ,id, valueComponent, maxSize) => config => value => {
    let status = valueComponent.length < maxSize;
    let errorMsg = status ? "" : getErrorMsg("invalidMaxSize", config).replace("{size}", maxSize);
    props.dispatch(editInput(id, valueComponent, status ? [] : [{status, errorMsg}], props.step));
   
  
    status = true;
    errorMsg = "";
    return {status, errorMsg};
};

export const checkAnualIncome = (typeLists, type, minIncome, maxIncome) => config => value => {
    let status = true;
    let errorMsg = "";
    if (typeLists.indexOf(type) > -1) {
        status = value >= minIncome && value < (maxIncome || 100000000000);
        errorMsg = status ? "" : getErrorMsg("plMinAmount", config).replace("{minAmt}", minIncome.toLocaleString());
    }
    return {status, errorMsg};
};

export const checkRange = (minSize, maxSize) => config => value => {
    const status = value.length >= minSize && value.length <= maxSize;
    const errorMsg = status
        ? ""
        : getErrorMsg("invalidRange", config)
              .replace("{minSize}", minSize)
              .replace("{maxSize}", maxSize);

    return {status, errorMsg};
};

export const checkMinimumAge = minAge => config => value => {
    const dob = moment(value, "DD-MM-YYYY");
    const benchmarkDate = moment().subtract(minAge, "years");
    const status = dob.isSameOrBefore(benchmarkDate, "days");
    const errorMsg = status ? "" : getErrorMsg("minAge", config).replace("{minAge}", minAge);

    return {status, errorMsg};
};

export const checkDateNotOverTodayDate = config => value => {
    const dateEntered = moment(value, "DD-MM-YYYY").format("YYYY-MM-DD");
    const dateNow = moment().format("YYYY-MM-DD");
    const status = moment(dateEntered).isSameOrBefore(dateNow);
    const errorMsg = status ? "" : getErrorMsg("yearExceeded", config);

    return {status, errorMsg};
};

export const checkDateIsOverTodayDate = config => value => {
    const dateEntered = moment(value, "DD-MM-YYYY").format("YYYY-MM-DD");
    const dateNow = moment().format("YYYY-MM-DD");
    const status = moment(dateEntered).isSameOrAfter(dateNow);
    const errorMsg = status ? "" : getErrorMsg("expiredYear", config);

    return {status, errorMsg};
};

export const expiredMonth = maxMonth => config => value => {
    let status = false;
    let errorMsg = "";
    if(value.length !== 10) {
        status = true;
        return {status, errorMsg};
    }
    const dateEntered = moment(value, "DD-MM-YYYY").format("YYYY-MM-DD");
    let currentDate = new Date();
    currentDate.setMonth(currentDate.getMonth() + maxMonth);
    status = moment(dateEntered) > currentDate;
    errorMsg = status ? "" : getErrorMsg("expiredMaxMonth", config).replace("{maxMonth}", maxMonth);

    return {status, errorMsg};
};

export const checkMaximumAge = maxAge => config => value => {
    const dob = moment(value, "DD-MM-YYYY").add(maxAge, "years");
    const benchmarkDate = moment();
    const status = dob.isSameOrAfter(benchmarkDate);
    const errorMsg = status ? "" : getErrorMsg("maxAge", config).replace("{maxAge}", maxAge);

    return {status, errorMsg};
};

export const noValidation = config => value => {
    const status = value.length === 0 || (value.length > 0 && value.trim().length === value.length);
    const errorMsg = status ? "" : getErrorMsg("noSpaceAllowed", config);
    return {status, errorMsg};
};

export const checkKrisFlyer = config => number => {
    const modulus = parseInt(number.substr(0, 9), 10) % 7 + 3;
    const lastNumber = parseInt(number.substr(9, number.length), 10);
    const status = modulus === lastNumber;
    const errorMsg = status ? "" : getErrorMsg("invalidKrisFlyer", config);

    return {status, errorMsg};
};

export const checkAsiaMiles = config => number => {
    let status = false;
    let errorMsg = "";
    if (number.length === 10) {
        const arr = number.split("");
        let sum = 0;
        for (let i = 0; i < number.length - 1; i++) {
            sum += arr[i] * (i + 1);
        }
        status = (sum % 10) === parseInt(arr[9]);
        errorMsg = status ? "" : getErrorMsg("invalidKrisFlyer", config);
    }
    return {status, errorMsg};
};

export const onlyLettersAndSpace = config => value => {
    const regex = /^[a-zA-Z\s]*$/;
    const testRegexp = value.length === 0 || regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("invalidCharacters", config);

    return {status: testRegexp, errorMsg};
};

export const onlyAlphanumeric = config => value => {
    const regex = /^[a-z0-9\s]+$/i;
    const testRegexp = value.length === 0 || regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("onlyAlphanumeric", config);

    return {status: testRegexp, errorMsg};
};

export const checkMaxMonths = config => value => {
    const status = value <= 11;
    const errorMsg = status ? "" : getErrorMsg("maxMonth", config);

    return {status, errorMsg};
};

export const personalLoanMinAmount = minAmt => config => value => {
    const status = Number(value) >= (minAmt || 10000);
    const errorMsg = status ? "" : getErrorMsg("plMinAmount", config).replace("{minAmt}", minAmt || 10000);

    return {status, errorMsg};
};

export const balanceTransferMinAmount = minAmt => config => value => {
    const status = Number(value) >= (minAmt || 10000);
    const errorMsg = status ? "" : getErrorMsg("btMinAmount", config).replace("{minAmt}", minAmt || 10000);

    return {status, errorMsg};
};

export const plBtMaxAmount = maxAmt => config => value => {
    const status = Number(value) <= (maxAmt || 500000);
    const errorMsg = status ? "" : getErrorMsg("plbtMaxAmount", config).replace("{maxAmt}", maxAmt || 500000);

    return {status, errorMsg};
};

export const checkpassword = config => value => {
    const regex = /^(?=.*[0-9])(?=.*[a-zA-Z])([a-zA-Z0-9]+)$/;
    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("alphanumeric", config);
    return {status: testRegexp, errorMsg};
};

export const checkUsername = config => value => {
    const regex = /^[a-z0-9\_]+$/i;
    const testRegexp = regex.test(value);
    const errorMsg = testRegexp ? "" : getErrorMsg("checkUsername", config);
    return {status: testRegexp, errorMsg};
};

export const validateConfirmPassword = pwd => config => value => {
    const isMatch = pwd === value;
    const errorMsg = isMatch ? "" : getErrorMsg("passwordMismatch", config);
    return {status: isMatch, errorMsg};
};

export const validatePassword = username => config => value => {
    const isMatch = username !== value;
    const errorMsg = isMatch ? "" : getErrorMsg("passwordEqualsUsername", config);
    return {status: isMatch, errorMsg};
};

export const validateSameAddress = (field, state) => config => value => {
    const homePostalCode = field === HOME_POSTAL_CODE ? value : state.getIn(dataPath([HOME_POSTAL_CODE]),"");
    const homeLevel = field === HOME_LEVEL ? value : state.getIn(dataPath([HOME_LEVEL]),"");
    const homeUnitNumber = field === HOME_UNIT_NUMBER ? value : state.getIn(dataPath([HOME_UNIT_NUMBER]),"");

    const mailingPostalCode = field === MAILING_POSTAL_CODE ? value : state.getIn(dataPath([MAILING_POSTAL_CODE]),"");
    const mailingLevel = field === MAILING_LEVEL ? value : state.getIn(dataPath([MAILING_LEVEL]),"");
    const mailingUnitNumber = field === MAILING_UNIT_NUMBER ? value : state.getIn(dataPath([MAILING_UNIT_NUMBER]),"");

    const isSamePostCode = homePostalCode === mailingPostalCode;
    const isSameUnit = homeUnitNumber === mailingUnitNumber;
    const isSameLevel = homeLevel === mailingLevel;

    const isMatch = isSamePostCode && isSameUnit && isSameLevel;
    const errorMsg = !isMatch ? "" : getErrorMsg("sameHomeMailingAddress", config);
    return {status: !isMatch, errorMsg};
};



