import React, {PropTypes} from "react";

import "./SummaryBlockSimpleElement.css";

const SummaryBlockSimpleElement = props => {
    return (
        <div className='simple-element-container'>
            <p className='simple-element-title'>{props.label}</p>
            <p className='simple-element-content'>{props.value}</p>
        </div>
    );
};

SummaryBlockSimpleElement.propTypes = {
    label: PropTypes.string,
    value: PropTypes.string
};

export default SummaryBlockSimpleElement;



