import {
    ACTION_POST_PARTIAL_APPLICATION_SUCCESS
    //ACTION_POST_PARTIAL_APPLICATION_LOADING
} from "../constants/actionsConstant";
import {checkPendingApplication} from "../actions/actionCreators";
import {dataPath, myInfoFlagPath} from "../constants/path";
import {NRIC, EMAIL, MOBILE_NUMBER} from "../constants/inputsConstant";

const isMyInfoFlow = (state, action) => {
    const isMyInfo = state.getIn(myInfoFlagPath(), false);
    return action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && action.currentStep === "basicInfo" && isMyInfo;
};

const isFromOTP = action => action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && action.currentStep === "otp";

const shouldGetPendingApplication = (state, action) => isMyInfoFlow(state, action) || isFromOTP(action);

const getPendingApplicationMiddleware = ({dispatch, getState}) => next => action => {
    const state = getState().state;
    if (shouldGetPendingApplication(state, action)) {
        const email = getState().state.getIn(dataPath([EMAIL]));
        const nric = getState().state.getIn(dataPath([NRIC]));
        const mobileNumber = getState().state.getIn(dataPath([MOBILE_NUMBER]));
        const payload = {
            email,
            nric,
            mobileNumber
        };
        next(action);
        return dispatch(checkPendingApplication(action.currentStep, payload, action.productsData));
    }
    return next(action);
};
export default getPendingApplicationMiddleware;



