/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import "./PrependTextInput.css";

import {onFocusInput, onBlurInput, editInput} from "../../actions/actionCreators";
import {getInputStates, isValidOrNotDirty} from "../../utils";

const Icon = props => {
    // const visibility = props.isValid ? "" : "--invisible";
    // const tick = (
    //     <img className={`prependTextInputTick${visibility}`} src={`${FORMS_CNFG_GLBL.imgPath}/Tick.svg`} alt='tick' />
    // );
    const cross = <img className={"prependTextInputTick"} src={`${FORMS_CNFG_GLBL.imgPath}/cross.svg`} alt='tick' />;

    return isValidOrNotDirty(props.isValid, props.isDirty) ? null : cross;
};

const spinner = (
    <img
        className='postal-code--spinner'
        src={`${FORMS_CNFG_GLBL.imgPath}/loading_icon_dark.svg`}
        alt='primary-button'
    />
);
const PrependText = props => {
    const [isFocus, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const {formData, readOnly, isLoading} = props;
    const focus = isFocus || inputValue.length > 0 || !isValidOrNotDirty(isValid, isDirty) ? "--focused" : "";
    const prependVisibility = focus === "--focused" ? "" : "--invisible";
    const showErrorLabel = isValidOrNotDirty(isValid, isDirty) ? "" : "prependTextInput--error";
    const errorContainerStyle = isValidOrNotDirty(isValid, isDirty) ? "" : "PrependTextInputContainer--error";
    const validator = (value, validators) => validators.map(x => x(formData)).map(x => x(value)).filter(x => !x.status);

    const handleOnChange = e =>
        props.dispatchEditInput(props.inputID, e.target.value, validator(e.target.value, props.validators), props.step);

    return (
        <div id={props.inputID} className={`PrependTextInputContainer ${errorContainerStyle}`}>
            <label className={`prependTextInputLabel${focus} ${showErrorLabel}`}>
                {`${props.inputLabel}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
            </label>
            <div className={`prependTextInputText${prependVisibility}`}>
                {props.prefix} -{" "}
            </div>
            <input
                onFocus={() => {
                    props.dispatchOnFocusInput(props.step, props.inputID, true);
                }}
                onBlur={() => {
                    props.dispatchOnBlurInput(props.step, props.inputID, false);
                }}
                value={inputValue}
                onChange={handleOnChange}
                type='text'
                className={`prependTextInput${focus}`}
                readOnly={readOnly}
            />
            {!isLoading && <Icon isValid={isValid} isDirty={isDirty} />}
            {isLoading && spinner}
        </div>
    );
};

PrependText.propTypes = {
    state: ImmutablePropTypes.map,
    inputID: PropTypes.string,
    step: PropTypes.string,
    inputLabel: PropTypes.string,
    prefix: PropTypes.string,
    dispatchOnBlurInput: PropTypes.func,
    readOnly: PropTypes.bool,
    isLoading: PropTypes.bool,
    formData: PropTypes.object.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchEditInput: bindActionCreators(editInput, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(PrependText);



