/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import "./GenericErrorMessage.css";

import {genericErrorPath} from "../../constants/path";
import {clearGenericErrorMessage} from "../../actions/actionCreators";

const GenericErrorMessage = props => {
    const messageContent = props.state.getIn(genericErrorPath(), "");
    const visibility = messageContent === "" ? "--hidden" : "";

    return (
        <div className={"generic-error-message--wrapper-flex" + visibility}>
            <div className='generic-error-message--icon-wrapper'>
                <img
                    className='generic-error-message--icon-signal'
                    src={`${FORMS_CNFG_GLBL.imgPath}/error-message-icon.svg`}
                    alt='errorIcon'
                />
            </div>
            <div className='generic-error-message--message-text'>{messageContent}</div>
            <div
                className='generic-error-message--icon-wrapper'
                onClick={() => {
                    props.dispatchClearGenericErrorMessage();
                }}
            >
                <img
                    className='generic-error-message--icon-cross'
                    src={`${FORMS_CNFG_GLBL.imgPath}/cross-white.svg`}
                    alt='removeIcon'
                />
            </div>
        </div>
    );
};

GenericErrorMessage.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchClearGenericErrorMessage: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchClearGenericErrorMessage: bindActionCreators(clearGenericErrorMessage, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(GenericErrorMessage);



