import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

import ToggleInputWithTitle from "../../ToggleInputWithTitle/ToggleInputWithTitle";

import "./Declaration.css";

import {
    SIGNATURE,
    DRAW_SIGNATURE,
    US_RESIDENT,
    US_CITIZEN,
    RESIDENT_CARD_HOLDER
} from "../../../constants/inputsConstant";

import TextInput from "../../TextInput/TextInput";
import Signature from "../../Signature/Signature";
import DrawSignature from "../../DrawSignature/DrawSignature";

import DropdownWithFlag from "../../DropdownWithFlag/DropdownWithFlag";
import Dropdown from "../../Dropdown/Dropdown";

import {
    updateTaxResidencyCountry,
    updateTaxResidencyId,
    addTaxResidency,
    removeTaxResidency,
    updateTaxResidencyReason,
    validateTIN
} from "../../../actions/actionCreators";

import {validateFilledInput, checkMaxSize, onlyAlphanumeric} from "../../../validations/inputValidation";
import {countriesLabelValue} from "../../../constants/countriesMaps";

import {List} from "immutable";

import {sellingCasa, sellingCashPlus, checkItemInList} from "../../../utils";
import {productIdPath, uiIsValidPath} from "../../../constants/path";
import {CARDS_NOT_REQUIRE_CRS} from "../../../constants/common";

import Accordian from "../../Accordian/Accordian";

const highestIndex = countries =>
    countries.keySeq().reduce((acc, val) => {
        const nb = parseInt(val, 10);
        return nb > acc ? nb : acc;
    }, 0);

const calculateNextIndex = countries => {
    const nextIndex = highestIndex(countries) + 1;

    return nextIndex.toString(10);
};

class TaxResidencyCountry extends Component {
    componentDidMount() {}
    render() {
        const props = this.props;
        const {formData, inputValues} = props;
        const stringIndex = props.countryIndex + "";
        const countryName = props.state.getIn(["data", "countries", stringIndex, "country"]);
        const countryId = props.state.getIn(["data", "countries", stringIndex, "id"]);
        const countryReason = props.state.getIn(["data", "countries", stringIndex, "reasonCode"]);
        const countriesSelected = props.countries.map(country => country.get("country"));
        const isTINhasValidFormat = props.state.getIn(uiIsValidPath("declaration",
                                                        `tax-residency-id-${stringIndex}`),false);

        const validator = (value, validators) =>
            validators
                .map(x => x(formData))
                .map(x => x(value))
                .filter(x => !x.status);
        const taxIdValidators = [validateFilledInput, onlyAlphanumeric, checkMaxSize(19)];
        const filteredCountries = countriesLabelValue.filter(country => !countriesSelected.includes(country.value));
        return (
            <div className='tax-residency-countries-container'>
                {/* <div className='tax-residency-countries--primary-country'> */}
                <div style={{flex: "1", display: "flex", flexDirection: "column"}}>
                    <DropdownWithFlag
                        inputLabel={props.inputLabels["taxResidency"]}
                        inputID={"tax-residency-country-" + stringIndex}
                        dropdownWithFlagItems={filteredCountries}
                        onClick={item => props.dispatch(updateTaxResidencyCountry(stringIndex, item.value, formData))}
                        value={countryName}
                        {...props}
                    />
                    <div style={{height: "10px"}} />
                    <Dropdown
                        inputLabel={props.inputLabels["tinReasons"]}
                        inputID={"tinReason-" + stringIndex}
                        dropdownItems={inputValues["tinReasons"]}
                        onClick={item => props.dispatch(updateTaxResidencyReason(stringIndex, item.value, formData))}
                        value={countryReason}
                        {...props}
                    />
                    <div style={{height: "10px"}} />
                    {countryReason === "0" && (
                        <TextInput
                            inputLabel={props.inputLabels["taxIdentificationNumber"]}
                            inputID={"tax-residency-id-" + stringIndex}
                            validators={[validateFilledInput, onlyAlphanumeric, checkMaxSize(19)]}
                            onChange={e => {
                                props.dispatch(
                                    updateTaxResidencyId(
                                        stringIndex,
                                        e.target.value,
                                        validator(e.target.value, taxIdValidators),
                                        formData
                                    )
                                );
                            }}
                            additionalOnBlur={() =>
                                isTINhasValidFormat && countryId &&
                                          props.dispatch(validateTIN(countryName, countryId, stringIndex, formData))}
                            value={countryId}
                            {...props}
                        />
                    )}
                    <div className='tax-residency-controllers'>
                        {props.countries.size > 1 ? (
                            <div
                                className='tax-residency-controllers-delete'
                                onClick={() => props.dispatch(removeTaxResidency(stringIndex))}
                            >
                                Delete
                            </div>
                        ) : (
                            <div />
                        )}

                        {props.countries.size < 4 &&
                        props.countryIndex === highestIndex(props.countries).toString(10) ? (
                            <div
                                className='tax-residency-controllers-add'
                                onClick={() => {
                                    const n = calculateNextIndex(props.countries);

                                    return (
                                        props.countries.size < 4 && props.dispatch(addTaxResidency(n, "", "", formData))
                                    );
                                }}
                            >
                                Add new
                            </div>
                        ) : (
                            <div />
                        )}
                    </div>
                </div>
            </div>
        );
    }
}

const TaxResidencyCountries = props => {
    const countries = props.state.getIn(["data", "countries"], List());
    return (
        <div className='tax-residency-countries-outer-container'>
            {countries
                .map((v, i) => <TaxResidencyCountry key={i} countryIndex={i} countries={countries} {...props} />)
                .toList()}
        </div>
    );
};

class Declaration extends Component {
    componentDidMount() {
        const countries = this.props.state.getIn(["data", "countries"], List());
        const nric = this.props.state.getIn(["data", "nric"]);
        const productIds = this.props.state.getIn(productIdPath(), List());
        const {productsConfig} = this.props;
        if ((sellingCasa(productIds, productsConfig) || sellingCashPlus(productIds, productsConfig)) && countries.count() === 0) {
            if (!checkItemInList(productIds, CARDS_NOT_REQUIRE_CRS, true)) {
                this.props.dispatch(addTaxResidency("0", "SG", nric, this.props.formData, "0"));
            }
            if (nric) {
                this.props.dispatch(validateTIN("SG", nric, "0", this.props.formData));
            }
        }
    }
    render() {
        const props = this.props;
        const {FormConfig, productsConfig} = props;
        const declaration = new FormConfig(props.step);
        const {inputLabels, title, subtitle, inputDescriptions, inputValues} = declaration;
        const [postMessage, postMessageContent] = declaration.getPostMessage();
        const productIds = props.state.getIn(productIdPath(), List());
        //const residentailStatus = props.state.getIn(dataPath([RESIDENTIAL_STATUS])); // check value is new customer
        const signaTureComponentChoice = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
            window.navigator.userAgent
        ) ? (
            <DrawSignature
                title={inputLabels[DRAW_SIGNATURE]}
                description={inputDescriptions[DRAW_SIGNATURE]}
                inputID={DRAW_SIGNATURE}
                {...props}
            />
        ) : (
            <Signature
                title={inputLabels[SIGNATURE]}
                description={inputDescriptions[SIGNATURE]}
                inputID={SIGNATURE}
                {...props}
            />
        );
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                {(sellingCasa(productIds, productsConfig) || sellingCashPlus(productIds, productsConfig)) && (
                        <div>
                            <div>
                                <ToggleInputWithTitle
                                    title={inputLabels[US_RESIDENT]}
                                    description={inputDescriptions[US_RESIDENT]}
                                    inputID={US_RESIDENT}
                                    {...props}
                                />
                            </div>
                            <div className='declaration--info-separator'>
                                <ToggleInputWithTitle
                                    title={inputLabels[US_CITIZEN]}
                                    description={inputDescriptions[US_CITIZEN]}
                                    inputID={US_CITIZEN}
                                    {...props}
                                />
                            </div>
                            <div className='declaration--info-separator'>
                                <ToggleInputWithTitle
                                    title={inputLabels[RESIDENT_CARD_HOLDER]}
                                    description={inputDescriptions[RESIDENT_CARD_HOLDER]}
                                    inputID={RESIDENT_CARD_HOLDER}
                                    {...props}
                                />
                            </div>
                            <div className='declaration--info-separator--big'>
                            {
                                !checkItemInList(productIds, CARDS_NOT_REQUIRE_CRS, true) &&
                                <TaxResidencyCountries
                                    inputValues={inputValues}
                                    inputLabels={inputLabels}
                                    {...props}
                                />
                            }
                            </div>
                        </div>
                    )}
                {(sellingCasa(productIds, productsConfig) || sellingCashPlus(productIds, productsConfig)) && (
                        <div className='declaration--info-separator'>{signaTureComponentChoice}</div>
                    )}
                {postMessage && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>
                )}
            </div>
        );
    }
}

TaxResidencyCountries.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatch: PropTypes.func.isRequired
};

Declaration.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatch: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    productsConfig: PropTypes.object
};

export default Declaration;



