/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import ImmutablePropTypes from "react-immutable-proptypes";

import {
    updateProductId,
    postPartialApplication,
    postOTP,
    showErrorMsg,
    submitFullApplication,
    setRightPanelExpanded,
    getPendingApplications,
    showGenericErrorMessage,
    validateUsername
} from "../actions/actionCreators";
import {
    rightPanelExpandedPath,
    currentStepPath,
    transitToPath,
    transitFromPath,
    shownStepsPath,
    uiStepPath,
    ui,
    dataPath,
    dialogModalPath,
    productIdPath,
    uiIsSpinnerActivePath,
    pageLoadingPath,
    uiIsValidPath
} from "../constants/path";
import compConifg from "../config/componentConfig";
import {Map, List} from "immutable";
// import {uiStructurePath} from "../constants/path";
import "../resources/css/uob-forms-demo.css";
import PrimaryButton from "./PrimaryButton/PrimaryButton";
import GenericErrorMessage from "./GenericErrorMessage/GenericErrorMessage";
import Loader from "./Loader/Loader";
import DialogModal from "./DialogModal/DialogModal";
import {getErrorMsg, sellingCasa, fileUploadHelper} from "../utils";
import {fileUploadUrl} from "../api/api";
import {passProps} from "./HOC/passProps";
// import {formConfig} from "../config/form";
import stepsEnum from "../constants/stepsEnum";
import scrollIntoView from "scroll-into-view";
const OTP_STEP = "otp";
const SUMMARY = "summary";
const PENDING_APP = "pendingApplication";
const UPLOAD_DOCUMENT = "uploadDocuments";

const getInvalidInputs = state => {
    const currentShownSteps = state.getIn(shownStepsPath(), List());
    const uiState = state.getIn(ui);
    const currentUIStateToCheck = uiState.filter((v, uiStep) => currentShownSteps.includes(uiStep));
    const filteredStepInput = currentUIStateToCheck.map(inputsInStep =>
        inputsInStep.filter(x => x.has("isValid") && !(x.get("isValid") || x.get("readOnly")))
    );
    return filteredStepInput.filter(inputsInStep => inputsInStep.count());
};

const scrollToUsername = () => {
    const element = document.querySelector("#username");
    scrollIntoView(element, {time: 500, align: {top: 0}});
};

const submitPartialApplication = (currentStep, nextStep, props) =>
    // nextStep &&
    props.dispatchPostPartialApplication(currentStep, nextStep, props.productsConfig);

const renderButtonAccordingToStep = (props, step, nextStep) => {
    const {state} = props;
    const {formData, productsConfig} = props;
    const currentStep = state.getIn(currentStepPath());
    const transitTo = state.getIn(transitToPath());
    const inputsToDispatchForError = getInvalidInputs(state);
    const isButtonLoading = state.getIn(uiIsSpinnerActivePath(step), false);

    const username = props.state.getIn(dataPath(["username"]), "");
    const isUsernameValid = props.state.getIn(uiIsValidPath("digitalServices", "username"),false);

    const submit = () => {
        if (step === OTP_STEP) {
            return props.dispatchPostOTP(step, nextStep, state.getIn(["data", "otp"]));
        }
        if (step === SUMMARY) {
            if (isUsernameValid && username) {
                return props.dispatch(validateUsername(username, formData, props.dispatchSubmitFullApplication, scrollToUsername));
            }
            return props.dispatchSubmitFullApplication();
        }
        if (step === PENDING_APP) {
            const selectPendingApplications = state.getIn(dataPath(["pendingApplications"]), List());
            return selectPendingApplications.count() > 0
                ? props.dispatchGetPendingApplications(productsConfig)
                : props.dispatchGenericError("Please select at least one pending application");
        }
        if (step === UPLOAD_DOCUMENT) {
            if (state.getIn(dataPath(["residentialStatus"])) === "P" && state.getIn(dataPath(["submitPR1-2Year"]), false)) {
                const actions = {successDispatch: () => ({}), errorDispatch: () => ({}), progressDispatch: () => ({})};
                const file = document.getElementById("fileuploadDocumentsNricBack");
                fileUploadHelper(file.files[0], fileUploadUrl(), "uploadDocumentsNricBack", actions);
                console.log("residentialStatus");
            }
        }
        return submitPartialApplication(step, nextStep, props);
    };
    const handleOnClick = () => {
        let isAlreadyScrolled = false;
        if (inputsToDispatchForError.count() > 0) {
            return inputsToDispatchForError.map((invalidInputs, inValidStep) => {
                return invalidInputs.map((inputState, invalidInput) => {
                    if (!isAlreadyScrolled) {
                        const element = document.querySelector(`#${invalidInput}`);
                        scrollIntoView(element, {time: 500, align: {top: 0}});
                        isAlreadyScrolled = true;
                    }

                    return invalidInput.includes("tnc")
                        ? props.dispatchShowErrorMsg(inValidStep, invalidInput, getErrorMsg("t&c", formData))
                        : props.dispatchShowErrorMsg(inValidStep, invalidInput, getErrorMsg("fieldEmpty", formData));
                });
            });
        }
        if (step === "declaration" || step === "summary") {
            const countriesSelected = state.getIn(dataPath(["countries"]), List());
            const isUSResident = state.getIn(dataPath(["usResident"]));
            const isUSCitizen = state.getIn(dataPath(["usCitizen"]));
            const isUSPermCard = state.getIn(dataPath(["residentCardHolder"]));
            const isUS = isUSResident || isUSCitizen || isUSPermCard;
            const countriesContainsUS = countriesSelected.find(country => country.get("country") === "US");
            const productIds = state.getIn(productIdPath(), List());
            const genericErrorToShow = sellingCasa(productIds, productsConfig)
                ? formData.formInputErrorMessages.selectUsResidencyError
                : formData.formInputErrorMessages.cashPlusUsResidencyError;
            return sellingCasa(productIds, productsConfig) && isUS && !countriesContainsUS ? props.dispatchGenericError(genericErrorToShow) : submit();
        }
        return submit();
    };
    return transitTo === step || currentStep === step
        ? <PrimaryButton
              idForButton={`${stepsEnum[currentStep]}_${currentStep}`}
              step={currentStep}
              {...props}
              onClick={() => !isButtonLoading && handleOnClick()}
          />
        : null;
};

const switchContainerAccordingToStep = (props, step, nextStep) => {
    const {state} = props;
    const currentStep = state.getIn(currentStepPath());
    const transitTo = state.getIn(transitToPath());
    const transitFrom = state.getIn(transitFromPath());

    return transitTo === nextStep || transitFrom === currentStep || currentStep === step
        ? "section-container-full"
        : "section-container";
};

const renderCurrentStep = (props, step, nextStep) => {
    if (!step) {
        return null;
    }
    const isStepInShownStep = props.state.getIn(shownStepsPath()).includes(step);
    const componentToRender = compConifg[step]({...props, step});

    return (isStepInShownStep)
        ? <div key={step} id={step} className={switchContainerAccordingToStep(props, step, nextStep)}>
              <div className='form-container'>
                  {componentToRender}
                  <div className='input-item'>
                      {renderButtonAccordingToStep(props, step, nextStep)}
                  </div>
              </div>
          </div>
        : null;
};
const renderAppStructure = props => {
    const appStructure = props.state.getIn(ui, Map());
    const doesContainInputs = k => props.state.getIn(uiStepPath(k)).count() > 0;
    const filteredAppStructure = appStructure.filter((v, k) => doesContainInputs(k));
    const listOfSteps = filteredAppStructure.keySeq();
    console.log(listOfSteps.toString());
    return listOfSteps.map((step, index) => renderCurrentStep(props, step, listOfSteps.get(index + 1)));
};

const Form = props => {
    const appStructure = renderAppStructure(props);
    const isExpanded = props.state.getIn(rightPanelExpandedPath(), false);
    // const tablet = /Android|iPad|IEMobile|Opera Mini/i.test(navigator.userAgent);
    const rightPannelState = isExpanded ? "--expanded" : "";
    const showDialogModal = props.state.getIn(dialogModalPath("MCAModal"), false);
    const isPageLoading = props.state.getIn(pageLoadingPath(), false);
    const showMyInfoDialog = props.state.getIn(dialogModalPath("myInfoModal"), false);
    const showMyInfoDownDialog = props.state.getIn(dialogModalPath("myInfoDownModal"), false);
    return (
        <div className='uob-form-demo'>
            <div className='left-panel'>
                <div
                    className={"uob-form--form-cover" + rightPannelState}
                    onClick={() => {
                        props.dispatchSetRightPanelExpanded(!isExpanded);
                    }}
                />
                <div className='left-panel-content'>
                    <div className='logo-alignement'>
                        <img className='left-panel--logo' src={`${FORMS_CNFG_GLBL.imgPath}/uobLogo.svg`} alt='logo' />
                        {appStructure}
                    </div>
                </div>
            </div>
            <div
                className={"right-panel" + rightPannelState}
                onClick={() => {
                    props.dispatchSetRightPanelExpanded(!isExpanded);
                }}
            >
                <div className='uob-form--expand-right-panel-button-flex-container'>
                    <img
                        src={`${FORMS_CNFG_GLBL.imgPath}/info_icon.svg`}
                        className={"uob-form-info--icon" + rightPannelState}
                        alt='infoIcon'
                    />
                    <div className={"uob-form--expand-right-panel-button" + rightPannelState}>Need help?</div>
                </div>
                <div className={"uob-form-right-column-container" + rightPannelState}>
                    <h2 className={"uob-form-right-column-text"}>Need help?</h2>
                    <p className='uob-form-right-column-description'>Feel free to contact our team for support</p>
                    <p className='uob-form-right-column-sub-title'>Singapore</p>
                    <p className='uob-form-right-column-text'> 1800 222 2121</p>
                    <p className='uob-form-right-column-sub-title'>Overseas Customers</p>
                    <p className='uob-form-right-column-text'> +65 6222 2121 </p>
                </div>
            </div>
            <GenericErrorMessage {...props} />
            {isPageLoading && <Loader />}
            {showDialogModal && <DialogModal dialogType='MCAModal' {...props} />}
            {showMyInfoDialog && <DialogModal dialogType='myInfoModal' {...props} />}
            {showMyInfoDownDialog && <DialogModal dialogType='myInfoDownModal' {...props} />}
        </div>
    );
};

Form.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchUpdateProductId: PropTypes.func.isRequired,
    dispatchSetRightPanelExpanded: PropTypes.func.isRequired,
    params: PropTypes.object.isRequired,
    dispatch: PropTypes.func.isRequired
};

renderButtonAccordingToStep.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    dispatchGenericError: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    productsConfig: PropTypes.object.isRequired
};

renderCurrentStep.propTypes = {
    state: ImmutablePropTypes.map.isRequired
};

const mapDispatchToProps = dispatch => ({
    dispatchUpdateProductId: bindActionCreators(updateProductId, dispatch),
    dispatchPostPartialApplication: bindActionCreators(postPartialApplication, dispatch),
    dispatchPostOTP: bindActionCreators(postOTP, dispatch),
    dispatchShowErrorMsg: bindActionCreators(showErrorMsg, dispatch),
    dispatchSubmitFullApplication: bindActionCreators(submitFullApplication, dispatch),
    dispatchSetRightPanelExpanded: bindActionCreators(setRightPanelExpanded, dispatch),
    dispatchGetPendingApplications: bindActionCreators(getPendingApplications, dispatch),
    dispatchGenericError: bindActionCreators(showGenericErrorMessage, dispatch)
});

export default passProps(connect(null, mapDispatchToProps)(Form));



