import {createActions, createTypes} from "../utils";

const apiMiddleware = apiFlow => ({dispatch, getState}) => next => action => {
    if (action.async) {
        const [pendingType, successType, errorType, loadingType] = createTypes(action.type);
        const [pendingAction, successDispatch, errorDispatch, loadingDispatch] = [
            createActions(action, pendingType),
            data => dispatch({...createActions(action, successType), data}),
            error => dispatch({...createActions(action, errorType), error}),
            loadingData => dispatch({...createActions(action, loadingType), loadingData})
        ];
        next(pendingAction);
        return apiFlow(action, successDispatch, errorDispatch, getState, dispatch, loadingDispatch);
    }
    return next(action);
};
export default apiMiddleware;



