import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {addValidFlag, editInputByPath, editDropdownByPath} from "../../../actions/actionCreators";

import TextInput from "../../TextInput/TextInput";
import GenderSelection from "../../GenderSelection/GenderSelection";
import Dropdown from "../../Dropdown/Dropdown";
import DropdownWithFlag from "../../DropdownWithFlag/DropdownWithFlag";

import {countriesLabelValue} from "../../../constants/countriesMaps";
import {productIdPath, dataPath} from "../../../constants/path";
import {
    validateFilledInput,
    checkMinimumAge,
    checkDateOfBirthFormat,
    checkFullName,
    checkMaxSize,
    checkMaximumAge,
    validateOnlYTNRIC,
    checkDateNotOverTodayDate,
    checkDateFormat
} from "../../../validations/inputValidation";

import {
    DATE_OF_BIRTH,
    MARITAL_STATUS,
    NATIONALITY,
    GENDER,
    COUNTRY_OF_BIRTH,
    EDUCATION_LEVEL,
    JUNIOR_SAVER_NAME,
    JUNIOR_SAVER_GENDER,
    JUNIOR_SAVER_DOB,
    JUNIOR_SAVER_BIRTH_CERT,
    JUNIOR_SAVER_NATIONALITY,
    JUNIOR_SAVER_COUNTRY_OF_BIRTH,
    COUNTRY_OF_RESIDENCE,
    IS_SG
} from "../../../constants/inputsConstant";

import "./PersonalInfo.css";

import Accordian from "../../Accordian/Accordian";

let keyCode;

const JuniorSaver = props => {
    const {formData} = props;
    return (
        <div
            className='account-details--separator-top-border'
            onKeyDown={e => {
                keyCode = e.keyCode;
            }}
        >
            <p>{props.inputLabels.juniorSaverSubtitle}</p>
            <div className='account-details--separator'>
                <TextInput
                    inputLabel={props.inputLabels[JUNIOR_SAVER_NAME]}
                    inputID={JUNIOR_SAVER_NAME}
                    // validators={[validateFilledInput, checkFullName, checkMaxSize(70)]}
                    onChange={e => {
                        const value = e.target.value;
                        const validators = [validateFilledInput, checkFullName, checkMaxSize(70)];
                        const status = validators
                            .map(x => x(formData))
                            .map(x => x(value))
                            .filter(x => !x.status);

                        return props.dispatch(
                            editInputByPath(
                                props.step,
                                JUNIOR_SAVER_NAME,
                                ["data", "products", "SAVJS", JUNIOR_SAVER_NAME],
                                value,
                                status
                            )
                        );
                    }}
                    value={props.state.getIn(["data", "products", "SAVJS", JUNIOR_SAVER_NAME], "")}
                    {...props}
                />
            </div>
            <div className='account-details--separator'>
                <GenderSelection
                    inputID={JUNIOR_SAVER_GENDER}
                    path={["data", "products", "SAVJS", JUNIOR_SAVER_GENDER]}
                    {...props}
                />
            </div>
            <div className='account-details--separator'>
                <TextInput
                    inputLabel={props.inputLabels[JUNIOR_SAVER_DOB]}
                    inputID={JUNIOR_SAVER_DOB}
                    // validators={[validateFilledInput, checkDateOfBirthFormat, checkMaximumAge(16)]}
                    onChange={e => {
                        const value =
                            (e.target.value.length === 2 && keyCode !== 8) ||
                            (e.target.value.length === 5 && keyCode !== 8)
                                ? `${e.target.value}${"/"}`
                                : e.target.value;
                        const validators = [
                            validateFilledInput,
                            checkDateOfBirthFormat,
                            checkDateFormat,
                            checkMaximumAge(16),
                            checkDateNotOverTodayDate
                        ];
                        const status = validators
                            .map(x => x(formData))
                            .map(x => x(value))
                            .filter(x => !x.status);

                        return props.dispatch(
                            editInputByPath(
                                props.step,
                                JUNIOR_SAVER_DOB,
                                ["data", "products", "SAVJS", JUNIOR_SAVER_DOB],
                                value,
                                status
                            )
                        );
                    }}
                    value={props.state.getIn(["data", "products", "SAVJS", JUNIOR_SAVER_DOB], "")}
                    {...props}
                />
            </div>
            <div className='account-details--separator'>
                <TextInput
                    inputLabel={props.inputLabels[JUNIOR_SAVER_BIRTH_CERT]}
                    inputID={JUNIOR_SAVER_BIRTH_CERT}
                    // validators={[validateFilledInput, validateOnlYTNRIC]}
                    onChange={e => {
                        const value = e.target.value.toUpperCase();
                        const validators = [validateFilledInput, validateOnlYTNRIC];
                        const status = validators
                            .map(x => x(formData))
                            .map(x => x(value))
                            .filter(x => !x.status);

                        return props.dispatch(
                            editInputByPath(
                                props.step,
                                JUNIOR_SAVER_BIRTH_CERT,
                                ["data", "products", "SAVJS", JUNIOR_SAVER_BIRTH_CERT],
                                value,
                                status
                            )
                        );
                    }}
                    value={props.state.getIn(["data", "products", "SAVJS", JUNIOR_SAVER_BIRTH_CERT], "")}
                    {...props}
                />
            </div>
            <div className='account-details--separator'>
                <DropdownWithFlag
                    inputLabel={props.inputLabels[JUNIOR_SAVER_NATIONALITY]}
                    inputID={JUNIOR_SAVER_NATIONALITY}
                    dropdownWithFlagItems={countriesLabelValue}
                    onClick={item =>
                        props.dispatch(
                            editDropdownByPath(
                                props.step,
                                JUNIOR_SAVER_NATIONALITY,
                                ["data", "products", "SAVJS", JUNIOR_SAVER_NATIONALITY],
                                item.value,
                                []
                            )
                        )
                    }
                    value={props.state.getIn(["data", "products", "SAVJS", JUNIOR_SAVER_NATIONALITY])}
                    {...props}
                />
            </div>
            <div className='account-details--separator'>
                <DropdownWithFlag
                    inputLabel={props.inputLabels[JUNIOR_SAVER_COUNTRY_OF_BIRTH]}
                    inputID={JUNIOR_SAVER_COUNTRY_OF_BIRTH}
                    dropdownWithFlagItems={countriesLabelValue}
                    onClick={item =>
                        props.dispatch(
                            editDropdownByPath(
                                props.step,
                                JUNIOR_SAVER_COUNTRY_OF_BIRTH,
                                ["data", "products", "SAVJS", JUNIOR_SAVER_COUNTRY_OF_BIRTH],
                                item.value,
                                []
                            )
                        )
                    }
                    value={props.state.getIn(["data", "products", "SAVJS", JUNIOR_SAVER_COUNTRY_OF_BIRTH])}
                    {...props}
                />
            </div>
        </div>
    );
};

class PersonalInfo extends Component {
    render() {
        const props = this.props;
        const productIds = props.state.getIn(productIdPath());
        const {FormConfig} = props;
        const personalInfo = new FormConfig(props.step);
        const {inputLabels, title, subtitle, inputValues} = personalInfo;
        const [postMessage, postMessageContent] = personalInfo.getPostMessage();
        const isSG = props.state.getIn(dataPath([IS_SG]), true);
        const countriesWithNoSG = countriesLabelValue.filter(country => country.value !== "SG");
        return (
            <div>
                <h1>{title}</h1>
                <div className='uob-form-separator' />
                <p>{subtitle}</p>
                <div>
                    <TextInput
                        inputLabel={inputLabels[DATE_OF_BIRTH]}
                        inputID={DATE_OF_BIRTH}
                        validators={[validateFilledInput, checkDateOfBirthFormat, checkDateFormat, checkMinimumAge(15)]}
                        {...props}
                    />
                </div>
                <div className='personalInfoSeparator'>
                    <GenderSelection inputID={GENDER} path={["data", GENDER]} {...props} />
                </div>
                <div className='personalInfoSeparator'>
                    <Dropdown
                        inputLabel={inputLabels[MARITAL_STATUS]}
                        inputID={MARITAL_STATUS}
                        dropdownItems={inputValues[MARITAL_STATUS]}
                        {...props}
                    />
                </div>
                <div className='personalInfoSeparator'>
                    <Dropdown
                        inputLabel={inputLabels[EDUCATION_LEVEL]}
                        inputID={EDUCATION_LEVEL}
                        dropdownItems={inputValues[EDUCATION_LEVEL]}
                        {...props}
                    />
                </div>
                <div className='personalInfoSeparator'>
                    <DropdownWithFlag
                        inputLabel={inputLabels[NATIONALITY]}
                        inputID={NATIONALITY}
                        dropdownWithFlagItems={isSG ? countriesLabelValue : countriesWithNoSG}
                        {...props}
                    />
                </div>
                <div className='personalInfoSeparator'>
                    <DropdownWithFlag
                        inputLabel={inputLabels[COUNTRY_OF_BIRTH]}
                        inputID={COUNTRY_OF_BIRTH}
                        dropdownWithFlagItems={countriesLabelValue}
                        {...props}
                    />
                </div>
                <div className='personalInfoSeparator'>
                    <DropdownWithFlag
                        inputLabel={inputLabels[COUNTRY_OF_RESIDENCE]}
                        inputID={COUNTRY_OF_RESIDENCE}
                        dropdownWithFlagItems={countriesLabelValue}
                        {...props}
                    />
                </div>

                {productIds.includes("SAVJS") ? <JuniorSaver {...props} inputLabels={inputLabels} /> : null}

                {postMessage && (
                    <div className='basicInfoSeparator'>
                        <Accordian {...props} title={postMessage} content={postMessageContent} inputID={"default"} />
                    </div>
                )}
            </div>
        );
    }
}

JuniorSaver.propTypes = {
    state: ImmutablePropTypes.map,
    step: PropTypes.string.isRequired,
    dispatch: PropTypes.func.isRequired,
    inputLabels: PropTypes.object.isRequired,
    formData: PropTypes.object.isRequired
};

PersonalInfo.propTypes = {
    state: ImmutablePropTypes.map,
    step: PropTypes.string.isRequired,
    dispatch: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchAddValidFlag: bindActionCreators(addValidFlag, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(PersonalInfo);



