import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import EditableDropdown from "../EditableDropdown/EditableDropdown";
import ReadOnlyDropdown from "../ReadOnlyDropdown/ReadOnlyDropdown";
import {isReadOnlyPath} from "../../constants/path";

const Dropdown = props => {
    const isReadOnly = props.state.getIn(isReadOnlyPath(props.step, props.inputID), false);
    return isReadOnly ? <ReadOnlyDropdown {...props} /> : <EditableDropdown {...props} />;
};

Dropdown.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    inputID: PropTypes.string,
    step: PropTypes.string.isRequired
};

export default Dropdown;



