import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";

import {bindActionCreators} from "redux";

import "./DrawSignature.css";

import elementResizeEvent from "element-resize-event";

import Accordian from "../Accordian/Accordian";

import {uiDrawSignatureIsEditablePath, uiSignatureImageContentPath} from "../../constants/path";

import {
    setSignatureImageContent,
    setSignatureImageWidth,
    setSignatureImageHeight,
    setDrawSignatureIsTouched,
    clearSignatureImageContent,
    setSignatureStatusToTrue,
    updateUploadProgress,
    setDrawSignatureDirtyToFalse,
    errorUploadFile
} from "../../actions/actionCreators";

import {getInputStates, isValidOrNotDirty, fileUploadHelper} from "../../utils";
import {validatorHOC} from "../HOC/ValidatorHOC";
import {fileUploadUrl} from "../../api/api";

const getErrorContainerStyle = validOrNotDirty => (validOrNotDirty ? "" : "draw-signature--main-container--error");

const DrawSignature = props => {
    const {FormConfig} = props;
    const [, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const fileUploadErrorMsg = props.formData["formInputErrorMessages"]["fileUploadError"];
    const showErrorLabel = isValidOrNotDirty(isValid, isDirty) ? "" : "file-upload--error";
    const descriptionContent = inputValue ? inputValue.replace(/^.*[\\\/]/, "") : props.description;
    const canvasContainerDivRef = document.getElementById("draw-signature--canvas-frame");
    const isCanvasEditable = props.state.getIn(uiDrawSignatureIsEditablePath(props.step, props.inputID), false);
    const isCanvasTouched = isCanvasEditable ? "--invisible" : "";
    const signatureContent = props.state.getIn(uiSignatureImageContentPath(props.step, props.inputID), "");
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirty(isValid, isDirty));
    let canvasWidth = canvasContainerDivRef ? canvasContainerDivRef.offsetWidth : 600;
    let canvasHeight = 200;
    let ctx;
    let canvasRef;
    let flag = false;
    let prevX = 0;
    let currX = 0;
    let prevY = 0;
    let currY = 0;
    let dot_flag = false;
    let x = "black";
    let y = 2;
    const signature = new FormConfig(props.step);
    const {termsAndConditions, inputDescriptions} = signature;

    if (canvasContainerDivRef) {
        elementResizeEvent(canvasContainerDivRef, () => {
            if (ctx) {
                ctx.canvas.width = canvasContainerDivRef.offsetWidth;
            }
        });
    }

    const draw = () => {
        ctx.beginPath();
        ctx.moveTo(prevX, prevY);
        ctx.lineTo(currX, currY);
        ctx.strokeStyle = x;
        ctx.lineWidth = y;
        ctx.stroke();
        ctx.closePath();
    };

    const findxy = (res, e) => {
        var rect = canvasRef.getBoundingClientRect();
        if (res === "down") {
            prevX = currX;
            prevY = currY;
            currX = e.touches[0].clientX - rect.left;
            currY = e.touches[0].clientY - rect.top;
            flag = true;
            dot_flag = true;

            if (dot_flag) {
                ctx.beginPath();
                ctx.fillStyle = x;
                ctx.fillRect(currX, currY, 2, 2);
                ctx.closePath();
                dot_flag = false;
            }
        }

        if (res === "up" || res === "out") {
            flag = false;
        }

        if (res === "move") {
            if (flag) {
                prevX = currX;
                prevY = currY;
                currX = e.touches[0].clientX - rect.left;
                currY = e.touches[0].clientY - rect.top;
                draw();
                e.preventDefault();
            }
        }
    };

    return (
        <div id={props.inputID}>
            <div className={`draw-signature--main-container-flex ${errorContainerStyle}`}>
                <div className='draw-signature--container'>
                    <div id='title' className='draw-signature--text-container'>
                        <div className={"draw-signature--title " + showErrorLabel}>
                            {`${props.title}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                        </div>
                        <div className='draw-signature--description'>
                            {descriptionContent}
                        </div>
                    </div>
                </div>
                <div className={"draw-signature--canvas-main-container"}>
                    <div id='draw-signature--canvas-frame' className='draw-signature--canvas-frame-container'>
                        <div
                            className={"draw-signature--canvas-text-wrapper" + isCanvasTouched}
                            onTouchEnd={() => {
                                return (
                                    !signatureContent && props.dispatchSetCanvasTouched(props.step, props.inputID, true)
                                );
                            }}
                            onClick={() => {
                                return (
                                    !signatureContent && props.dispatchSetCanvasTouched(props.step, props.inputID, true)
                                );
                            }}
                        >
                            <div className='draw-signature--canvas-text'>
                                {signatureContent
                                    ? inputDescriptions.drawSignatureClear
                                    : inputDescriptions.drawSignatureTouch}
                            </div>
                        </div>
                        {signatureContent
                            ? <img src={signatureContent} />
                            : <canvas
                                  className='draw-signature--canvas'
                                  ref={canvas => {
                                      canvasRef = canvas;
                                      ctx = canvas ? canvas.getContext("2d") : null;
                                      if (ctx) {
                                          ctx.fillStyle = "white";
                                          ctx.fillRect(0, 0, canvasRef.width, canvasRef.height);
                                      }
                                  }}
                                  id='canvas'
                                  width={canvasWidth}
                                  height={canvasHeight}
                                  onTouchStart={e => {
                                      e.preventDefault();
                                      //findxy("down", e);
                                      return false;
                                  }}
                                  onTouchMove={e => {
                                      e.preventDefault();
                                      //findxy("move", e);
                                      return false;
                                  }}
                                  onTouchEnd={e => {
                                      e.preventDefault();
                                      //findxy("up", e);
                                      return false;
                                  }}
                                  onTouchCancel={e => {
                                      e.preventDefault();
                                      findxy("out", e);
                                      return false;
                                  }}
                              />}
                    </div>
                    <div className='draw-signature--buttons-container'>
                        <div
                            className='draw-signature--button-save'
                            onClick={() => {
                                const content = canvasRef ? canvasRef.toDataURL() : signatureContent;
                                props.dispatchSetSignatureImageContent(props.step, props.inputID, content);
                                const actions = {
                                    successDispatch: () =>
                                        props.dispatchSetSignatureStatusToTrue(props.step, props.inputID),
                                    errorDispatch: () => props.dispatchErrorUploadFile(fileUploadErrorMsg),
                                    progressDispatch: progress =>
                                        props.dispatchupdateUploadProgress(props.step, props.inputID, progress)
                                };
                                canvasRef.toBlob(function (blob) {
                                    fileUploadHelper(blob, fileUploadUrl(), props.inputID, actions);
                                });
                            }}
                        >
                            Save
                        </div>
                        <div
                            className='draw-signature--button-cancel'
                            onClick={() => {
                                // ctx.clearRect(0, 0, canvasRef.width, canvasRef.height);
                                props.dispatchClearSignatureImageContent(props.step, props.inputID, "");
                            }}
                        >
                            Clear
                        </div>
                    </div>
                    <div className='draw-signature--separator' />
                    <Accordian
                        {...props}
                        title={termsAndConditions["signature"]}
                        content={termsAndConditions["signatureContent"]}
                        inputID={"default"}
                    />
                </div>
            </div>
        </div>
    );
};

DrawSignature.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    FormConfig: PropTypes.func.isRequired,
    formData: PropTypes.object.isRequired,
    dispatchClearSignatureImageContent: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchSetSignatureImageContent: bindActionCreators(setSignatureImageContent, dispatch),
        dispatchSetSignatureImageWidth: bindActionCreators(setSignatureImageWidth, dispatch),
        dispatchSetSignatureImageHeight: bindActionCreators(setSignatureImageHeight, dispatch),
        dispatchSetCanvasTouched: bindActionCreators(setDrawSignatureIsTouched, dispatch),
        dispatchClearSignatureImageContent: bindActionCreators(clearSignatureImageContent, dispatch),
        dispatchupdateUploadProgress: bindActionCreators(updateUploadProgress, dispatch),
        dispatchSetSignatureStatusToTrue: bindActionCreators(setSignatureStatusToTrue, dispatch),
        dispatchSetDrawSignatureDirtyToFalse: bindActionCreators(setDrawSignatureDirtyToFalse, dispatch),
        dispatchErrorUploadFile: bindActionCreators(errorUploadFile, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(DrawSignature));



