import React, {Component, PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {onCheck} from "../../actions/actionCreators";

export const checkBoxDefaultValue = () => WrappedComponent => {
    class DefaultValue extends Component {
        componentDidMount() {
            if (!this.props.isRequiredToggle) {
                this.props.dispatch(onCheck(this.props.inputID, this.props.step, this.props.toggleTrue));
            }
        }
        render() {
            return <WrappedComponent {...this.props} />;
        }
    }

    DefaultValue.propTypes = {
        state: ImmutablePropTypes.map,
        inputID: PropTypes.string,
        step: PropTypes.string.isRequired,
        dispatch: PropTypes.func.isRequired,
        toggleTrue: PropTypes.bool,
        isRequiredToggle: PropTypes.bool
    };

    return DefaultValue;
};



