/* global FORMS_CNFG_GLBL */

import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {onFocusInput, onBlurInput, editInput} from "../../actions/actionCreators";
import {DATE_OF_BIRTH} from "../../constants/inputsConstant";
import {getInputStates, isValidOrNotDirty} from "../../utils";
import "./TextInput.css";
import {validatorHOC} from "../HOC/ValidatorHOC";
import {uiInProgressPath, dataPath} from "../../constants/path";

const validator = (value, validators, formData) =>
    validators
        .map(x => x(formData))
        .map(x => x(value))
        .filter(x => !x.status);

const Icon = props => {
    const visibility = props.isValid ? "--visible" : "--invisible";
    const tick = <img className={`textInputTick${visibility}`} src={`${FORMS_CNFG_GLBL.imgPath}/Tick.svg`} alt='tick' />;
    const cross = <img className={"textInputTick--visible"} src={`${FORMS_CNFG_GLBL.imgPath}/cross.svg`} alt='tick' />;

    return isValidOrNotDirty(props.isValid, props.isDirty) ? tick : cross;
};

const getErrorContainerStyle = validOrNotDirty => (validOrNotDirty ? "" : "textInputLabel-container--error");
const TextInput = props => {
    const {formData} = props;
    const [isFocus, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const isLoading = props.state.getIn(uiInProgressPath(props.step, props.inputID), false);
    const value = props.value ? props.value : inputValue;
    const isValidOrNotDirtyVar = isValidOrNotDirty(isValid, isDirty);
    const focus = isFocus || value.length > 0 || !isValidOrNotDirtyVar ? "--focused" : "";

    const showErrorLabel = isValidOrNotDirtyVar ? "" : "textInputLabel--error";
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirtyVar);
    const isDOB = props.inputID === DATE_OF_BIRTH || props.dateFormat;
    const disabled = props.disabled ? props.disabled : false;
    let keyCode;
    const handleOnChange = e => {
        const regex = /(^\d{2}$)|(^\d{2}\/\d{2}$)/;
        const dateFormatRegex = /(^\d{1,2}$)|(^\d{2}\/$)|(^\d{2}\/\d{1,2}$)|(^\d{2}\/\d{2}\/$)|(^\d{2}\/\d{2}\/\d{1,4}$)/;
        let val = (isDOB && regex.test(e.target.value) && keyCode !== 8)
          ? `${e.target.value}${"/"}`
          : e.target.value;

        if (isDOB && !dateFormatRegex.test(e.target.value) && keyCode !== 8) {
            val = props.state.getIn(dataPath([props.inputID]), "");
        }

        const upperCaseValue = props.upperCase ? val.toUpperCase() : val;
        return props.dispatchEditInput(
            props.inputID,
            upperCaseValue,
            validator(upperCaseValue, props.validators, formData),
            props.step
        );
    };
    const spinner = (
        <img
            className='textInputTick--spinner'
            src={`${FORMS_CNFG_GLBL.imgPath}/loading_icon_dark.svg`}
            alt='primary-button'
        />
    );

    return (
        <div
            id={props.inputID}
            className={`textInputContainer ${errorContainerStyle}`}
            onKeyDown={e => {
                keyCode = e.keyCode;
            }}
            onFocus={() => {
                props.dispatchOnFocusInput(props.step, props.inputID, true);
            }}
            onBlur={() => {
                props.dispatchOnBlurInput(props.step, props.inputID, false);
                return props.additionalOnBlur && props.additionalOnBlur();
            }}
        >
            <div className='textInputContent'>
                <label className={`textInputLabel${focus} ${showErrorLabel}`}>
                    {`${props.inputLabel}${isValidOrNotDirty(isValid, isDirty) ? "" : ` ${errorMsg}`}`}
                </label>
                <input
                    disabled={disabled}
                    value={value}
                    onChange={props.onChange ? props.onChange : handleOnChange}
                    type='text'
                    className={`textInput${focus}`}
                />
                {isLoading && spinner}
                {!isLoading && value && <Icon isValid={isValid} isDirty={isDirty} />}
            </div>
            {/* {value && (isValidOrNotDirty(isValid, isDirty) ? tick : cross)} */}
        </div>
    );
};

TextInput.propTypes = {
    state: ImmutablePropTypes.map,
    title: PropTypes.string,
    inputLabel: PropTypes.string,
    inputID: PropTypes.string,
    description: PropTypes.string,
    dropdownItems: PropTypes.array,
    path: PropTypes.array,
    dispatchOnFocusInput: PropTypes.func,
    dispatchOnBlurInput: PropTypes.func,
    dispatchOnEditInput: PropTypes.func,
    step: PropTypes.string.isRequired,
    value: PropTypes.string,
    onChange: PropTypes.func,
    formData: PropTypes.object.isRequired,
    additionalOnBlur: PropTypes.func,
    dateFormat: PropTypes.bool,
    disabled: PropTypes.bool
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnFocusInput: bindActionCreators(onFocusInput, dispatch),
        dispatchOnBlurInput: bindActionCreators(onBlurInput, dispatch),
        dispatchEditInput: bindActionCreators(editInput, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(TextInput));



