import React, {PropTypes} from "react";

import TextInput from "../TextInput/TextInput";

import {validateFilledInput, noValidation, checkMaxSize} from "../../validations/inputValidation";

const OverseasAddressInput = props => {
    return (
        <div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    inputLabel={props.inputLabels[`${props.inputID}Add1`]}
                    inputID={`${props.inputID}Add1`}
                    validators={[validateFilledInput, checkMaxSize(30)]}
                />
            </div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    inputLabel={props.inputLabels[`${props.inputID}Add2`]}
                    inputID={`${props.inputID}Add2`}
                    validators={[noValidation, checkMaxSize(30)]}
                />
            </div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    inputLabel={props.inputLabels[`${props.inputID}Add3`]}
                    inputID={`${props.inputID}Add3`}
                    validators={[noValidation, checkMaxSize(30)]}
                />
            </div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    inputLabel={props.inputLabels[`${props.inputID}Add4`]}
                    inputID={`${props.inputID}Add4`}
                    validators={[noValidation, checkMaxSize(30)]}
                />
            </div>
            <div className='homeMailingAddress--separator'>
                <TextInput
                    {...props}
                    inputLabel={props.inputLabels[`${props.inputID}City`]}
                    inputID={`${props.inputID}City`}
                    validators={[validateFilledInput, checkMaxSize(20)]}
                />
            </div>
        </div>
    );
};

OverseasAddressInput.propTypes = {
    inputID: PropTypes.string.isRequired,
    inputLabels: PropTypes.object.isRequired
};

export default OverseasAddressInput;



