/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {fromJS, Map, List} from "immutable";

import SummaryBlockSimpleElement from "../components/SummaryBlockSimpleElement/SummaryBlockSimpleElement";
import SummaryBlockToggleElement from "../components/SummaryBlockToggleElement/SummaryBlockToggleElement";

import {scrollSummaryToStep} from "../../../../actions/actionCreators";
import {getHighestRankProduct} from "../../../../utils";

import {ui, isFinalSubmitPath, bankListDataPath, productIdPath, dataPath} from "../../../../constants/path";
import {
    NAME_ON_CARD,
    CARD_LIMIT,
    CREDIT_LIMIT,
    ACTIVATE_ATM,
    PURPOSE_OF_FUNDS,
    SOURCE_OF_FUNDS,
    COUNTRY_OF_FUNDS,
    KRISFLYER_LAST_NAME,
    KRISFLYER_FIRST_NAME,
    KRYSFLYER_MEMBERSHIP_NUMBER,
    UNSECURED_LOAN_AMOUNT,
    UNSECURED_LOAN_BENEFICIARY_BANK,
    UNSECURED_LOAN_BENEFICIARY_ACCOUNT,
    ASIA_MILES_MEMBERSHIP_NUMBER,
    CREDIT_SURE,
    SAME_DAY_DELIVERY,
    SINGTEL_MEMBERSHIP_NUMBER
} from "../../../../constants/inputsConstant";

import "./SummaryBlock.css";

import {countriesNamesMap} from "../../../../constants/countriesMaps";
const currencyFullName = {
    SGD: "Singapore Dollar",
    EUR: "Euro",
    USD: "U.S. Dollar",
    AUD: "Australian Dollar",
    GBP: "British Pound",
    CHN: "Chinese Yuan",
    JPY: "Japanese Yuan",
    HKD: "Hong Kong Dollar",
    CAD: "Canadian Dollar",
    NZD: "New Zealand Dollar",
    CHF: "Swiss Franc"
};

const mapValueToLabel = (arrayOfItems, value) => {
    const item = arrayOfItems.find(x => x.value === value);
    return item ? item.label : value;
};

const CurrencyDetails = props => {
    const {FormConfig, productsConfig} = props;
    const typeOfProduct = fromJS(productsConfig.products);
    const {dropDownValues, inputLabels} = new FormConfig("accountDetails");
    const purposeOfFunds = dropDownValues["purposeOfFunds"][props.currency.get("purposeOfFunds")];
    const sourceOfFunds = dropDownValues["sourceOfFunds"][props.currency.get("sourceOfFunds")];
    const countryOfFunds = countriesNamesMap[props.currency.get("countryOfFunds")];
    let additionalTitle;
    if (props.productKey !== "SAVJS") {
        additionalTitle =
            props.productKey !== ("CURDAA" || "CURJP")
                ? `Additional information for ${typeOfProduct.getIn([props.productKey, "productName"])}`
                : `Additional information for ${currencyFullName[props.currencyKey]}`;
    } else {
        additionalTitle = null;
    }
    return (
        <div className='summary--block-separator'>
            <div className='summary--block-currency-title'>{additionalTitle}</div>
            <div className='summary--block-content-container-flex'>
                <SummaryBlockSimpleElement label={inputLabels[PURPOSE_OF_FUNDS]} value={purposeOfFunds} />
                <SummaryBlockSimpleElement label={inputLabels[SOURCE_OF_FUNDS]} value={sourceOfFunds} />
                <SummaryBlockSimpleElement label={inputLabels[COUNTRY_OF_FUNDS]} value={countryOfFunds} />
            </div>
        </div>
    );
};

const PLBTContent = props => {
    const {FormConfig} = props;
    const {inputLabels} = new FormConfig("accountDetails");

    const beneficiaryBank = props.product.get(UNSECURED_LOAN_BENEFICIARY_BANK);

    const getTenorNumbeOfMonth = (pid) => {
        return props.state.getIn(["data", "products", pid, "tenor"], "") + " months";
    };

    const getBeneficiaryBankLabel = bank => {
        const bankList = props.state.getIn(bankListDataPath(), Map({}));
        const bankDetails = bankList.filter(value => value.get("value") === bank);

        if (bankDetails.first()) {
            return bankDetails.first().get("label");
        } else {
            return "";
        }
    };

    return (
        <div key={props.productKey} className='summary--block-content-container'>
            <div className='summary--block-content-container-flex'>
                <SummaryBlockSimpleElement
                    label={inputLabels.uLLoanAmount}
                    value={props.product.get(UNSECURED_LOAN_AMOUNT)}
                />
                <SummaryBlockSimpleElement
                    label={inputLabels.uLTenor}
                    value={getTenorNumbeOfMonth(props.productKey)}
                />
                <SummaryBlockSimpleElement
                    label={inputLabels.uLBeneficiaryBank}
                    value={getBeneficiaryBankLabel(beneficiaryBank)}
                />
                <SummaryBlockSimpleElement
                    label={inputLabels.uLBeneficiaryAccount}
                    value={props.product.get(UNSECURED_LOAN_BENEFICIARY_ACCOUNT)}
                />
                <SummaryBlockSimpleElement
                    label={inputLabels.uLBeneficiaryName}
                    value={props.state.getIn(["data", "fullName"])}
                />
                {/* {productType === "CASA" &&
                     <SummaryBlockSimpleElement
                         label={inputLabels[ACTIVATE_ATM]}
                         value={props.product.get("atmAccess") ? "Yes" : "No"}/> */}
            </div>
        </div>
    );
};

const CasaAndCreditCardContent = props => {
    const {FormConfig, productsConfig} = props;
    const {inputLabels, inputValues} = new FormConfig("accountDetails");
    const typeOfProduct = fromJS(productsConfig.products);
    const productType = typeOfProduct.getIn([props.productKey, "type"]);
    const currencies = props.product.get("currencies");

    const currentProduct = typeOfProduct.getIn([props.productKey]);
    const productsId = props.state.getIn(productIdPath(), List());
    const productsInfo = productsId.map(id => typeOfProduct.getIn([id]));
    const highestRank = getHighestRankProduct(productsInfo).get("rank");
    const productsToShowCardDetails = productsInfo
        .filter(product => product.get("rank") === highestRank)
        .map(product => product.get("pid"));
    const isCreditSure = props.state.getIn(dataPath([CREDIT_SURE]), false);
    const isCreditSureProduct = productType === "Card" || productType === "CASH+";

    const isSameDayDeliveryOn = productsConfig.sameDayDelivery ? productsConfig.sameDayDelivery : false;
    const isSameDayDelivery = props.state.getIn(dataPath([SAME_DAY_DELIVERY]), false);
    return (
        <div key={props.productKey} className='summary--block-content-container'>
            {props.productKey !== "SAVJS" &&
                (productsToShowCardDetails.includes(currentProduct.get("pid")) || ["001843"].indexOf(currentProduct.get("pid")) > -1) && (
                    <div className='summary--block-content-container-flex'>
                        <SummaryBlockSimpleElement
                            label={inputLabels[NAME_ON_CARD]}
                            value={props.product.get("nameOnCard")}
                        />
                        {productType === "CASA" && (
                            <SummaryBlockSimpleElement
                                label={inputLabels[CARD_LIMIT]}
                                value={mapValueToLabel(inputValues[CARD_LIMIT], props.product.get("dailyCardLimit"))}
                            />
                        )}
                        {(productType === "Card" || productType === "CASH+") &&
                            props.productKey !== "700778" && (
                                <SummaryBlockSimpleElement
                                    label={inputLabels[CREDIT_LIMIT]}
                                    value={mapValueToLabel(inputValues[CREDIT_LIMIT], props.product.get("creditLimit"))}
                                />
                            )}
                        {productType === "CASA" && (
                            <SummaryBlockSimpleElement
                                label={inputLabels[ACTIVATE_ATM]}
                                value={props.product.get("atmAccess") ? "Yes" : "No"}
                            />
                        )}

                        {productType === "Card" && isSameDayDeliveryOn && (
                            <SummaryBlockToggleElement
                                label={inputLabels[SAME_DAY_DELIVERY]}
                                value={isSameDayDelivery ? "Yes" : "No"}
                            />
                        )}

                        {isCreditSureProduct && (
                            <SummaryBlockToggleElement
                                label={inputLabels["isApplyingCreditSure"]}
                                value={isCreditSure ? "Yes" : "No"}
                            />
                        )}
                    </div>
                )}

            {currencies && (
                <CurrencyDetails
                    currencyKey={currencies.keySeq().first()}
                    currency={currencies.get(currencies.keySeq().first())}
                    {...props}
                />
            )}
            {props.product.get("asiaMilesMembershipNumber") && (
                <SummaryBlockSimpleElement
                    label={inputLabels[ASIA_MILES_MEMBERSHIP_NUMBER]}
                    value={props.product.get("asiaMilesMembershipNumber")}
                />
            )}
            {props.product.get("krisflyerMembershipNumber") && (
                <SummaryBlockSimpleElement
                    label={inputLabels[KRYSFLYER_MEMBERSHIP_NUMBER]}
                    value={props.product.get("krisflyerMembershipNumber")}
                />
            )}
            {props.product.get("krisflyerMembershipFName") && (
                <SummaryBlockSimpleElement
                    label={inputLabels[KRISFLYER_FIRST_NAME]}
                    value={props.product.get("krisflyerMembershipFName")}
                />
            )}
            {props.product.get("krisflyerMembershipLName") && (
                <SummaryBlockSimpleElement
                    label={inputLabels[KRISFLYER_LAST_NAME]}
                    value={props.product.get("krisflyerMembershipLName")}
                />
            )}
             {props.product.get(SINGTEL_MEMBERSHIP_NUMBER) && (
                <SummaryBlockSimpleElement
                    label={inputLabels[SINGTEL_MEMBERSHIP_NUMBER]}
                    value={props.product.get(SINGTEL_MEMBERSHIP_NUMBER)}
                />
            )}
        </div>
    );
};

const SummaryBlockAccountDetails = props => {
    const {FormConfig, productsConfig} = props;
    const typeOfProduct = fromJS(productsConfig.products);
    const {title} = new FormConfig("accountDetails");
    const isFinalSubmit = props.state.getIn(isFinalSubmitPath(), false);
    const productName = typeOfProduct.getIn([props.productKey, "productName"]);
    const productType = typeOfProduct.getIn([props.productKey, "type"]);
    const family = typeOfProduct.getIn([props.productKey, "pid"]);
    return (
        <div className='summary--block'>
            {/* TITLE */}
            <div className='summary--block-header-container'>
                <div className='summary--block-header-title'>{`${title} - ${productName}`}</div>
                {!isFinalSubmit && (
                    <img
                        className='summary--block-header-img'
                        src={`${FORMS_CNFG_GLBL.imgPath}/edit_blue_icon.svg`}
                        onClick={() => {
                            props.dispatchScrollSummaryToStep(
                                props.state
                                    .getIn(ui)
                                    .keySeq()
                                    .butLast()
                                    .last(),
                                `${family}`
                            );
                        }}
                        alt='header-img'
                    />
                )}
            </div>

            {/* CONTENT */}

            {productType !== "personalLoan" && productType !== "balanceTransfer" ? (
                <CasaAndCreditCardContent {...props} />
            ) : (
                <PLBTContent {...props} />
            )}
        </div>
    );
};

CurrencyDetails.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    currency: PropTypes.object.isRequired,
    currencyKey: PropTypes.string.isRequired,
    FormConfig: PropTypes.func.isRequired,
    productKey: PropTypes.string.isRequired,
    productsConfig: PropTypes.object.isRequired
};

PLBTContent.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    product: ImmutablePropTypes.map.isRequired,
    productKey: PropTypes.string.isRequired,
    FormConfig: PropTypes.func.isRequired,
    productsConfig: PropTypes.object.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

CasaAndCreditCardContent.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    product: ImmutablePropTypes.map.isRequired,
    productKey: PropTypes.string.isRequired,
    FormConfig: PropTypes.func.isRequired,
    productsConfig: PropTypes.object.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

SummaryBlockAccountDetails.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    product: ImmutablePropTypes.map.isRequired,
    productKey: PropTypes.string.isRequired,
    FormConfig: PropTypes.func.isRequired,
    productsConfig: PropTypes.object.isRequired,
    dispatchScrollSummaryToStep: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchScrollSummaryToStep: bindActionCreators(scrollSummaryToStep, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(SummaryBlockAccountDetails);



