import React, {Component, PropTypes} from "react";

import "./TenorTable.css";

import TenorTableMobile from "./TenorTableMobile/TenorTableMobile";
import TenorTableDesktop from "./TenorTableDesktop/TenorTableDesktop";
import {dataPath, allTableStatePath} from "../../constants/path";
import {UNSECRUED_LOAN_TABLE} from "../../constants/inputsConstant";
import {selectTableRow, expandTableRow} from "../../actions/actionCreators";

import {Map} from "immutable";
class TenorTable extends Component {
    componentWillReceiveProps(nextProps) {
        const isPromoCodeSelected = nextProps.state.getIn(dataPath(["products", this.props.pid, "promotionCodeTenor"]), "");
        const promoCodeData = nextProps.state.getIn(dataPath(["products", this.props.pid, "promotionCode"]), "");
        const tenorData = nextProps.state.getIn(dataPath(["products", this.props.pid, "tenor"]), "");
        const isTableOpenBefore =
            nextProps.state.getIn(allTableStatePath(`${UNSECRUED_LOAN_TABLE}-${nextProps.pid}`), Map()).count() > 0;
        if (nextProps.tableContent.length > 0 && !isPromoCodeSelected) {
            const firstRow = nextProps.tableContent[0];
            this.props.dispatch(selectTableRow(UNSECRUED_LOAN_TABLE, this.props.pid, promoCodeData || firstRow.promoCode, tenorData || firstRow.loanTenor));
        }
        if (!isTableOpenBefore) {
            this.props.dispatch(expandTableRow(`${UNSECRUED_LOAN_TABLE}-${nextProps.pid}`, 0));
        }
    }
    render() {
        const props = this.props;
        return (
            <div>
                <div className='tenorTable-desktop'><TenorTableDesktop {...props} /></div>
                <div className='tenorTable-mobile'><TenorTableMobile {...props} /></div>
            </div>
        );
    }
}

TenorTable.propTypes = {
    dispatch: PropTypes.func.isRequired,
    pid: PropTypes.string.isRequired,
    tableContent: PropTypes.array.isRequired
};

export default TenorTable;



