import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators, compose} from "redux";

import {openAccordian, closeAccordian, onCheck} from "../../actions/actionCreators";

import ToggleButton from "../../components/ToggleButton/ToggleButton";

import {accordianPath} from "../../constants/path";
import {checkBoxDefaultValue} from "../HOC/checkBoxDefaultValue";
import {validatorHOC} from "../HOC/ValidatorHOC";
import {getInputStates} from "../../utils";

import "./AccordianToggleButton.css";

const AccordianToggleButton = props => {
    const {title, content, additionalOnClick} = props;
    const [, isValid, isDirty, errorMsg, inputValue] = getInputStates(props.state, props.step, props.inputID);
    const isAccordianOpen = props.forcedOpen ? true : props.state.getIn(accordianPath(props.step, props.inputID), false);
    const accordianContentState = isAccordianOpen ? "--open" : "--close";
    const readMoreLess = isAccordianOpen ? "Read Less" : "Read More";
    const errorClass = isDirty && !isValid ? "--error" : "";

    return (
        <div id={props.inputID} className={`accordian-toggle-button--block${errorClass}`}>
            <div
                className='accordian-toggle-button--block-header-container'
                onClick={() =>
                    isAccordianOpen
                        ? props.dispatchCloseAccordian(props.step, props.inputID)
                        : props.dispatchOpenAccordian(props.step, props.inputID)}
            >
                <div className='accordian-toggle-button--block-header-title'>
                    <span>
                        {title}
                        {!isValid &&
                            errorMsg !== "" &&
                            <span className='accordian-toggle-button--error'>{`${errorMsg}`}</span>}
                        <span className='accordian-toggle-button--read-more'>
                            {props.forcedOpen || !props.content ? "" : readMoreLess}
                        </span>
                    </span>
                </div>
                {title &&
                    <ToggleButton
                        inputID={props.inputID}
                        onClick={e => {
                            e.stopPropagation();
                            if (additionalOnClick) {
                                additionalOnClick(props.inputID, props.step, !inputValue);
                            }
                            props.dispatchOnCheck(props.inputID, props.step, !inputValue);
                        }}
                        {...props}
                    />}
            </div>

            {/* CONTENT */}
            {content &&
                <div className={`accordian-toggle-button--block-content-container${accordianContentState}`}>
                    {isAccordianOpen &&
                        <div className='accordian-toggle-button--block-content'>
                            <ul className='accordian-toggle-button--block-content-list'>
                                {props.content.map((desc, i) =>
                                    <li key={i} className='bulletedPoint-item-desc'>
                                        {desc}
                                    </li>
                                )}
                            </ul>
                        </div>}
                </div>}
        </div>
    );
};

AccordianToggleButton.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    content: PropTypes.array,
    inputID: PropTypes.string.isRequired,
    forcedOpen: PropTypes.bool,
    additionalOnClick: PropTypes.func
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOpenAccordian: bindActionCreators(openAccordian, dispatch),
        dispatchCloseAccordian: bindActionCreators(closeAccordian, dispatch),
        dispatchOnCheck: bindActionCreators(onCheck, dispatch)
    };
};

const enchance = compose(validatorHOC, checkBoxDefaultValue(), connect(null, mapDispatchToProps));

export default enchance(AccordianToggleButton);



