/* global FORMS_CNFG_GLBL */
import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";

import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {dataPath} from "../../constants/path";
import {onToggle} from "../../actions/actionCreators";

import "./ToggleButton.css";

const ToggleButton = props => {
    const toggleStatus = props.value ? props.value : props.state.getIn(dataPath([props.inputID]), false);
    const isToggled = !toggleStatus ? "" : "--toggle";

    return (
        <div
            className={`switch-bg${isToggled}`}
            onClick={
                props.onClick
                    ? props.onClick
                    : () => {
                        props.dispatchOnToggle(props.step, props.inputID, !toggleStatus);
                        if (props.setPath) {
                            props.setPath(!toggleStatus);
                        }
                    }
            }
        >
            <img className='toggle-button--icon-tick' src={`${FORMS_CNFG_GLBL.imgPath}/tick_white.svg`} alt='tick' />
            <img className='toggle-button--icon-cross' src={`${FORMS_CNFG_GLBL.imgPath}/cross-white.svg`} alt='cross' />
            <div className={`switch${isToggled}`} />
        </div>
    );
};

ToggleButton.propTypes = {
    state: ImmutablePropTypes.map,
    inputID: PropTypes.string,
    dispatchOnToggle: PropTypes.func,
    value: PropTypes.bool,
    onClick: PropTypes.func,
    step: PropTypes.string,
    setPath: PropTypes.func
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOnToggle: bindActionCreators(onToggle, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(ToggleButton);



