import {ACTION_POST_PARTIAL_APPLICATION_SUCCESS} from "../constants/actionsConstant";
import {getUserStatus} from "../actions/actionCreators";

const getUserStatusMiddleware = ({dispatch}) => next => action => {
    if (action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && action.currentStep === "workInfo") {
        next(action);
        return dispatch(getUserStatus(action.nextStep));
    }
    return next(action);
};

export default getUserStatusMiddleware;



