import {ACTION_POST_OTP_SUCCESS} from "../constants/actionsConstant";
import {postPartialApplication} from "../actions/actionCreators";

const postOTPMiddleware = ({dispatch}) => next => action => {
    if (action.type === ACTION_POST_OTP_SUCCESS) {
        next(action);
        dispatch(postPartialApplication(action.currentStep, action.nextStep));
    }
    return next(action);
};

export default postOTPMiddleware;



