/* global FORMS_CNFG_GLBL */

import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {getInputStates, isValidOrNotDirty} from "../../utils";

import "./GenderSelection.css";

import {chooseGender} from "../../actions/actionCreators";
import {isReadOnlyPath} from "../../constants/path";
import {validatorHOC} from "../HOC/ValidatorHOC";

const getErrorContainerStyle = ValidOrNotDirty => (ValidOrNotDirty ? "" : "single-selection-input--error");
const GenderSelection = props => {
    const gender = props.state.getIn(props.path, null);
    const [, isValid, isDirty, errorMsg] = getInputStates(props.state, props.step, props.inputID);
    const isReadOnly = props.state.getIn(isReadOnlyPath(props.step, props.inputID), false);
    const readOnlyClass = isReadOnly ? "single-selection-input--readonly" : "";
    const errorContainerStyle = getErrorContainerStyle(isValidOrNotDirty(isValid, isDirty));
    const showErrorLabel = isValid || !isDirty ? "" : "GenderSelectionLabel--error";
    return (
        <div className={`single-selection-input--focused ${readOnlyClass} ${errorContainerStyle}`}>
            <div className='single-selection-input-title-container'>
                <div className={"single-selection-input-title " + showErrorLabel}>
                    {`Gender${isValid || !isDirty ? "" : ` ${errorMsg}`}`}
                </div>
                {isReadOnly &&
                    <img
                        className='read-only-single-selection-input--verified-icon'
                        src={`${FORMS_CNFG_GLBL.imgPath}/non-editable-icon4.svg`}
                        alt='verified-icon'
                    />}
            </div>

            <div className='single-selection-input-container'>
                <div
                    className={
                        gender === "M" ? "single-selection-input-button--selected" : "single-selection-input-button"
                    }
                    onClick={() => {
                        props.dispatchChooseGender("M", props.step, props.inputID, props.path);
                    }}
                >
                    <div className='single-selection-input-button-content'>
                        <img
                            src={`${FORMS_CNFG_GLBL.imgPath}/icon-male.svg`}
                            className='single-selection-input-button-icon'
                            alt='gender-male'
                        />
                        Male
                    </div>
                </div>
                <div
                    className={
                        gender === "F" ? "single-selection-input-button--selected" : "single-selection-input-button"
                    }
                    onClick={() => {
                        props.dispatchChooseGender("F", props.step, props.inputID, props.path);
                    }}
                >
                    <img
                        src={`${FORMS_CNFG_GLBL.imgPath}/icon-female.svg`}
                        className='single-selection-input-button-icon'
                        alt='gender-female'
                    />
                    Female
                </div>
            </div>
        </div>
    );
};

GenderSelection.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    path: PropTypes.array.isRequired,
    inputID: PropTypes.string.isRequired,
    dispatchChooseGender: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchChooseGender: bindActionCreators(chooseGender, dispatch)
    };
};

export default validatorHOC(connect(null, mapDispatchToProps)(GenderSelection));



