import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import EditableTextInput from "../EditablePasswordInput/EditablePasswordInput";
import ReadOnlyTextInput from "../ReadOnlyTextInput/ReadOnlyTextInput";
import PostalCodeInput from "../PostalCodeInput/PostalCodeInput";
import PrependTextInput from "../PrependTextInput/PrependTextInput";
import PhoneInput from "../PhoneInput/PhoneInput";
import {isReadOnlyPath} from "../../constants/path";

const PasswordInput = props => {
    const isReadOnly = props.state.getIn(isReadOnlyPath(props.step, props.inputID), false) || props.readOnly;
    const textInputs = {
        Phone: <PhoneInput {...props} />,
        Postal: <PostalCodeInput {...props} />,
        Prepend: <PrependTextInput {...props} />
    };
    const input = props.type ? textInputs[props.type] : <EditableTextInput {...props} />;
    return isReadOnly ? <ReadOnlyTextInput {...props} /> : input;
};

PasswordInput.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    inputID: PropTypes.string.isRequired,
    readOnly: PropTypes.bool,
    type: PropTypes.string
};

export default PasswordInput;



