import {createStore, applyMiddleware, compose} from "redux";
import {routerMiddleware, syncHistoryWithStore} from "react-router-redux";
import {browserHistory} from "react-router";
import apiMiddleware from "./middlewares/apiMiddleware";
import scrollViewMiddleware from "./middlewares/scrollViewMiddleware";
import errorMiddleware from "./middlewares/errorMiddleware";
import getOTPMiddleware from "./middlewares/getOTPMiddleware";
import customRouterMiddleware from "./middlewares/customRouterMiddleware";
import getPendingApplicationMiddleware from "./middlewares/getPendingApplicationMiddleware";
import postOTPMiddleware from "./middlewares/postOTPMiddleware";
import getEncryptionParamsMiddleware from "./middlewares/getEncryptionParamsMiddleware";
import getUserStatusMiddleware from "./middlewares/getUserStatusMiddleware";
import validateUsernameMiddleware from "./middlewares/validateUsernameMiddleware";
import formApi from "./api/formApi";
import rootReducer from "./reducers/rootReducer";

const defaultState = {};
// const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const composeEnhancers = process.env.NODE_ENV !== "production"
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose
    : "" || compose;
const store = createStore(
    rootReducer,
    defaultState,
    composeEnhancers(
        applyMiddleware(
            apiMiddleware(formApi),
            getOTPMiddleware,
            scrollViewMiddleware,
            errorMiddleware,
            customRouterMiddleware,
            getPendingApplicationMiddleware,
            postOTPMiddleware,
            getEncryptionParamsMiddleware,
            getUserStatusMiddleware,
            validateUsernameMiddleware,
            routerMiddleware(browserHistory)
        )
    )
);

export const history = syncHistoryWithStore(browserHistory, store);

if (module.hot) {
    module.hot.accept("./reducers/rootReducer", () => {
        const nextRootReducer = require("./reducers/rootReducer").default;
        store.replaceReducer(nextRootReducer);
    });
}

export default store;



