import React from "react";
import BasicInfo from "../components/steps/BasicInfo/BasicInfo";
import Otp from "../components/steps/Otp/Otp";
import PendingApplication from "../components/steps/PendingApplication/PendingApplication";
import AccountDetails from "../components/steps/AccountDetails/AccountDetails";
import PersonalInfo from "../components/steps/PersonalInfo/PersonalInfo";
import HomeMailingAddress from "../components/steps/HomeMailingAddress/HomeMailingAddress";
import WorkInfo from "../components/steps/WorkInfo/WorkInfo";
import UploadDocuments from "../components/steps/UploadDocuments/UploadDocuments";
import IncomeDocuments from "../components/steps/IncomeDocuments/IncomeDocuments";
import Declaration from "../components/steps/Declaration/Declaration";
import Summary from "../components/steps/Summary/Summary";
import DigitalServices from "../components/steps/DigitalServices/DigitalServices";

const BasicInfoWrapper = props => <BasicInfo {...props} />;
const OtpWrapper = props => <Otp {...props} />;
const PendingApplicationWrapper = props => <PendingApplication {...props} />;
const AccountDetailsWrapper = props => <AccountDetails {...props} />;
const PersonalInfoWrapper = props => <PersonalInfo {...props} />;
const HomeMailingAddressWrapper = props => <HomeMailingAddress {...props} />;
const WorkInfoWrapper = props => <WorkInfo {...props} />;
const UploadDocumentsWrapper = props => <UploadDocuments {...props} />;
const IncomeDocumentsWrapper = props => <IncomeDocuments {...props} />;
const DeclarationWrapper = props => <Declaration {...props} />;
const SummaryWrapper = props => <Summary {...props} />;
const DigitalServicesWrapper = props => <DigitalServices {...props} />;

export default {
    basicInfo: BasicInfoWrapper,
    otp: OtpWrapper,
    pendingApplication: PendingApplicationWrapper,
    accountDetails: AccountDetailsWrapper,
    personalInfo: PersonalInfoWrapper,
    homeMailingAddress: HomeMailingAddressWrapper,
    workInfo: WorkInfoWrapper,
    uploadDocuments: UploadDocumentsWrapper,
    incomeDocuments: IncomeDocumentsWrapper,
    digitalServices: DigitalServicesWrapper,
    declaration: DeclarationWrapper,
    summary: SummaryWrapper
};



