import {Map} from "immutable";
import {startChangeStep, endChangeStep} from "../actions/actionCreators";
import scrollIntoView from "scroll-into-view";
import {myInfoFlagPath, uiStepPath} from "../constants/path";
import {
    ACTION_POST_PARTIAL_APPLICATION_LOADING,
    ACTION_POST_PARTIAL_APPLICATION_SUCCESS,
    ACTION_SCROLL_SUMMARY_TO_STEP,
    ACTION_CHECK_PENDING_APPLICATION_SUCCESS,
    ACTION_GET_PENDING_APPLICATION_SUCCESS,
    ACTION_GET_ENCRYPTION_PARAMS_SUCCESS,
    ACTION_GET_USER_STATUS_SUCCESS,
    ACTION_GET_USER_STATUS_ERROR
} from "../constants/actionsConstant";

export const scrollToSection = (currentStep, nextStep, dispatch) => {
    if (nextStep) {
        dispatch(startChangeStep(currentStep, nextStep));
        // similar to nextTick in node. will push function to the next queue stack.
        Promise.resolve().then(() => {
            const element = document.querySelector(`#${nextStep}`);
            dispatch(endChangeStep(nextStep));
            scrollIntoView(element, {time: 500, align: {top: 0}});
        });
    }
};

export const scrollToSectionSummary = nextStep => {
    // similar to nextTick in node. will push function to the next queue stack.
    Promise.resolve().then(() => {
        const element = document.querySelector(`[id='${nextStep}']`);
        scrollIntoView(element, {time: 500, align: {top: 0}});
        // () => dispatch(removeSummaryStepFromShownSteps(nextStep))
    });
};

const scrollViewMiddleware = ({dispatch, getState}) => next => action => {
    const isMyInfoFlow = getState().state.getIn(myInfoFlagPath(), false);
    if (action.type === ACTION_GET_PENDING_APPLICATION_SUCCESS) {
        next(action);
        return scrollToSection("pendingApplication", "accountDetails", dispatch);
    }
    if (action.type === ACTION_GET_USER_STATUS_SUCCESS) {
        next(action);
        return scrollToSection(action.currentStep, "digitalServices", dispatch);
    }
    if (action.type === ACTION_GET_USER_STATUS_ERROR) {
        next(action);
        let nextStep = "uploadDocuments";
        let uiState = getState().state.getIn(uiStepPath("uploadDocuments"), Map());
        if (!uiState.size) {
            nextStep = "declaration";
            uiState = getState().state.getIn(uiStepPath("declaration"), Map());
            if (!uiState.size) {
                nextStep = "summary";
            }
        }
        return scrollToSection("workInfo", nextStep, dispatch);
    }
    if (action.type === ACTION_GET_ENCRYPTION_PARAMS_SUCCESS) {
        next(action);
        return scrollToSection("digitalServices", action.nextStep, dispatch);
    }
    if (action.type === ACTION_CHECK_PENDING_APPLICATION_SUCCESS) {
        next(action);
        return action.data.productId.length > 0
            ? scrollToSection(action.currentStep, "pendingApplication", dispatch)
            : setTimeout(() => scrollToSection(action.currentStep, "accountDetails", dispatch), 100);
    }
    if (
        (action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && !(["basicInfo", "digitalServices", "workInfo", "personalInfo"].indexOf(action.currentStep) > -1)) ||
        (action.type === ACTION_POST_PARTIAL_APPLICATION_SUCCESS && action.currentStep === "basicInfo" && !isMyInfoFlow)
    ) {
        next(action);
        return scrollToSection(action.currentStep, action.nextStep, dispatch);
    }
    if (ACTION_POST_PARTIAL_APPLICATION_LOADING && ["personalInfo"].indexOf(action.currentStep) > -1) {
        next(action);
        return scrollToSection(action.currentStep, action.nextStep, dispatch);
    }
    if (action.type === ACTION_SCROLL_SUMMARY_TO_STEP) {
        next(action);
        return scrollToSectionSummary(action.step, dispatch);
    }
    return next(action);
};
export default scrollViewMiddleware;



