import React, {PropTypes} from "react";
import ImmutablePropTypes from "react-immutable-proptypes";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import {openAccordian, closeAccordian} from "../../actions/actionCreators";

import {accordianPath} from "../../constants/path";
import "./Accordian.css";

const Accordian = props => {
    const {title, content} = props;
    const isAccordianOpen = props.state.getIn(accordianPath(props.step, props.inputID), false);
    const accordianContentState = isAccordianOpen ? "--open" : "--close";
    const readMoreLess = isAccordianOpen ? " Read Less" : " Read More";
    const hasContent = content ? "--with-content" : "";

    return (
        <div className='accordian--block'>
            <div
                className={`accordian--block-header-container${hasContent}`}
                onClick={() =>
                    isAccordianOpen
                        ? props.dispatchCloseAccordian(props.step, props.inputID)
                        : props.dispatchOpenAccordian(props.step, props.inputID)}
            >
                <div className='accordian--block-header-title'>
                    <span>
                        {title} {content && <span style={{textDecoration: "underline"}}>{readMoreLess}</span>}
                    </span>
                </div>
                {/* {content && <img className='accordian--block-header-img' src={arrowIcon} alt='header-img' />} */}
            </div>

            {/* CONTENT */}
            {content && (
                <div className={`accordian--block-content-container${accordianContentState}`}>
                    {isAccordianOpen && (
                        <div className='accordian--block-content' dangerouslySetInnerHTML={{__html: content}} />
                    )}
                </div>
            )}
        </div>
    );
};

Accordian.propTypes = {
    state: ImmutablePropTypes.map.isRequired,
    step: PropTypes.string.isRequired,
    title: PropTypes.any.isRequired,
    content: PropTypes.string,
    inputID: PropTypes.string.isRequired,
    dispatchOpenAccordian: PropTypes.func.isRequired,
    dispatchCloseAccordian: PropTypes.func.isRequired
};

const mapDispatchToProps = dispatch => {
    return {
        dispatchOpenAccordian: bindActionCreators(openAccordian, dispatch),
        dispatchCloseAccordian: bindActionCreators(closeAccordian, dispatch)
    };
};

export default connect(null, mapDispatchToProps)(Accordian);



